import UserBlocksHandler from "./user-blocks-common";
import { simpleStorageGet } from "../../../utils/storage.ts";
import { chrome } from "../../../utils/polyfill.ts";

export class Mv2UserBlocksHandler extends UserBlocksHandler {
    constructor() {
        super();
        this.migrateBlockedItems();
    }

    async migrateBlockedItems() {        
        // for mv2, an array of urls items is stored in extension storage with the key 'blockedItems'
        const blockedItems = await simpleStorageGet('blockedItems');
        if (!blockedItems) {
            return;
        }

        // we need to transform the blocked items into the new format {domain: {blockUrls: [], xpath: string}}
        // But we don't know the domain for each item, so we'll have to use a wildcard domain '*'
        
        const migratedBlockedItems = [{domain: '*', blockUrls: blockedItems, xpath: ''}];
        await this.importBlockedItems(migratedBlockedItems);

        await chrome.storage.local.remove('blockedItems');

        return migratedBlockedItems;
    }

    async importBlockedItems(items) {
        return super.importBlockedItems(items);
    }

    async blockItem(item) {
        console.debug('MV2 UserBlocksHandler: Blocking item', { item });
        if (!this.CACHE) {
            this.CACHE = new Map();
        }

        // cache is a map of domain -> { blockUrls: [], xpath: string }[]

        const domainEntries = this.CACHE.get(item.domain) || [];
        // if we already have an entry for this xpath, update it, otherwise add a new entry for this xpath/blockUrls
        const existingEntry = domainEntries.find((entry) => entry.xpath.toUpperCase() === item.xpath.toUpperCase());
        if (existingEntry) {
            existingEntry.blockUrls = Array.from(new Set([...existingEntry.blockUrls, ...item.blockUrls]));
        } else {
            domainEntries.push({ blockUrls: item.blockUrls, xpath: item.xpath });
        }
        this.CACHE.set(item.domain, domainEntries);
        await this.saveBlockedItems();        
    }

    async getBlockedItems() {        
        return super.getBlockedItems();
    }

    async loadSavedBlockedItems() {
        return super.loadSavedBlockedItems();
    }

    async saveBlockedItems() {
        return super.saveBlockedItems();
    }

    async deleteBlockedItem(item) {
        return super.deleteBlockedItem(item);
    }

    async deleteAllBlockedItems() {
        return super.deleteAllBlockedItems();
    }

    isBlocked(domain, url) {
        return super.isBlocked(domain, url);
    }
}
