import {chrome} from "../../../utils/polyfill.ts";
import {MV2ExclusionHandler} from './exclusions-mv2';
import {MV3ExclusionHandler} from './exclusions-mv3';
import {EXCLUSION_ADS, EXCLUSION_MALWARE, EXCLUSION_SCAMS} from '../app-consts';

const isMV3 = chrome.runtime.getManifest().manifest_version === 3;
const mv3ExclusionHandler = new MV3ExclusionHandler();
const mv2ExclusionHandler = new MV2ExclusionHandler();
export const exclusionHandler = isMV3 ? mv3ExclusionHandler : mv2ExclusionHandler;

export async function migrateExclusionsToMV3() {
    console.debug("MV3_Exclusion_Migrate: Migrating to MV3");
    if (!isMV3) {
        return;
    }

    // migrate Allow Lists
    const allowList = await mv2ExclusionHandler.getExclusions();
    console.debug("MV3_Exclusion_Migrate: Migrating Allow List", allowList);
    for (const [domain, exclusionInts] of Object.entries(allowList)) {
        try {
            console.debug("MV3_Exclusion_Migrate: Migrating Allow List for domain and ints", {domain, exclusionInts});
            const exclusionConsts = exclusionInts.map((e) => exclusionIntToConstant(e));
            console.debug("MV3_Exclusion_Migrate: Migrating Allow List for domain and consts",
                {domain, exclusionConsts});
            await mv3ExclusionHandler.exclude(domain, exclusionConsts);
            console.debug("MV3_Exclusion_Migrate: Migrated Allow List for domain", domain);
        } catch (error) {
            console.error("MV3_Exclusion_Migrate: Error migrating Allow List for domain",
                {domain, exclusionInts, error});
        }
    }

    // migrate block lists
}

const exclusionIntToConstant = (exclusionInt) => {
    switch (exclusionInt) {
        case 2:
        case 3:
            return EXCLUSION_ADS;

        case 4:
            return EXCLUSION_MALWARE;

        case 6:
            return EXCLUSION_SCAMS;
    }
    throw new Error(`${exclusionInt} is not recognised`);
};
