import {binaryFromBase64, binaryToBase64} from '../utils';

export class BloomFilter {
    add( str ) {
        var indices = this.indices( str );
        for ( var index of indices ) {
            this.data[~~(index / 32)] |= ( 1 << ( index % 32 ) );
        }
    }

    contains( str ) {
        var indices = this.indices( str );
        for ( var index of indices ) {
            if ( ( this.data[~~(index / 32)] & ( 1 << ( index % 32 ) ) ) === 0 ) {
                return false;
            }
        }
        return true;
    }

    indices( str ) {
        var hash1 = this.djb2( str );
        var hash2 = this.sdbm( str );
        var indices = [];
        for ( var i = 0; i < this.k; i++ ) {
            indices.push( ( ( hash1 + i * hash2 ) >>> 0 ) % this.m );
        }
        return indices;
    }

    serialize() {
        var object = {};
        object.k = this.k;
        object.m = this.m;
        object.data = binaryToBase64( this.data.buffer );
        return object;
    }

    static deserialize(object, isFromCache) {
        var bloomFilter = new BloomFilter();
        bloomFilter.k = object.k;
        bloomFilter.m = object.m;
        bloomFilter.data = isFromCache ? object.data : new Uint32Array(binaryFromBase64(object.data));
        return bloomFilter;
    }

    djb2( str ) {
        var hash = 5381;
        for ( var c of str ) {
            hash = ( 33 * hash + c.charCodeAt(0) ) >>> 0;
        }
        return hash;
    }

    sdbm( str ) {
        var hash = 0;
        for ( var c of str ) {
            hash = ( 65599 * hash + c.charCodeAt(0) ) >>> 0;
        }
        return hash;
    }

    getPlainObject() {
        return {
            k: this.k,
            m: this.m,
            data: this.data
        };
    }

    replace(obj) {
        this.k = obj.k;
        this.m = obj.m;
        this.data = obj.data;
    }
}
