export class StaticRuleLookupCache {
    constructor(name, rules) {
        this.name = name;
        this.idToRules = new Map();
        this.domainToIds = new Map();

        console.debug(`MV3LookupCache: Building lookup cache for ${name}`);

        for (const rule of rules) {
            this.idToRules.set(rule.id, rule);
            let {urlFilter} = rule.condition;
            // Remove leading || or ^ from urlFilter
            urlFilter = urlFilter.replace(/^\|\||^\^/g, '');
            // remove trailing ^ from urlFilter
            urlFilter = urlFilter.replace(/\^$/, '');
            this.domainToIds.set(urlFilter, rule.id);
        }

        console.debug(`StaticRuleLookupCache: Lookup cache for ${name} built`);
    }

    searchForDomain(domain) {
        const id = this.domainToIds.get(domain);
        if (!id) {
            return null;
        }
        
        const rule = this.idToRules.get(id);
        return {ruleId: id, rule};
    }
}
