import { chrome } from "../../../utils/polyfill.ts";
import { isMV3, urlHost, browserName } from '../../../utils/utils';

export const STATIC_RULE_BLOCKLIST_ADS_PRIORITY = 9;
export const STATIC_RULE_BLOCKLIST_MALWARE_PRIORITY = 9;
export const STATIC_RULE_WHITELIST_PRIORITY = 10;
export const STATIC_RULE_EASYLIST_ALLOW_PRIORITY = 4;
export const STATIC_RULE_EASYLIST_BLOCK_PRIORITY = 5;

// For dynamic whitelists.
export const DYNAMIC_EASYLIST_WHITELIST_PRIORITY = 7;
export const DYNAMIC_TRACKER_WHITELIST_PRIORITY = 12;

// for user blocked items
export const DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY = 27;

// For whitelisted ads
export const DYNAMIC_RULE_ADS_PRIORITY = 21;
// For whitelisted malware
export const DYNAMIC_RULE_MALWARE_PRIORITY = 22;
// For whitelisted scams
export const DYNAMIC_RULE_SCAMS_PRIORITY = 23;

// for content controls
export const DYNAMIC_RULE_CONTENT_CONTROL_PRIORITY = 24;
// for heuristic rules
export const DYNAMIC_RULE_HEURISTIC_PRIORITY = 25;
// for heuristic whitelist rules
export const DYNAMIC_RULE_HEURISTIC_WHITELIST_PRIORITY = 26;

// For temporary exclusions
export const DYNAMIC_RULE_TEMPORARY_PRIORITY = 29;
export const DYNAMIC_RULE_ALL_PRIORITY = 30;
export const DYNAMIC_RULE_INDIVIDUAL_GRANULAR_PRIORITY = 31;

export const RISKY_TLDS_PRIORITY = 15;

/**
 * Retrieves the existing dynamic rules.
 * @param {Number|string|Array<string>} [optionalPriority] - Optional priority or array of priorities to filter the rules.
 * @returns {Promise<Array<Object>>} - A promise that resolves to an array of dynamic rules.
 */
export const getExistingDynamicRules = async (optionalPriority) => {
    if (!isMV3()) {
        return [];
    }

    const rules = await chrome.declarativeNetRequest.getDynamicRules() || [];
    if (optionalPriority) {
        if (Array.isArray(optionalPriority)) {
            return rules.filter((rule) => optionalPriority.includes(rule.priority));
        }
        return rules.filter((rule) => rule.priority === optionalPriority);
    }
    return rules;
};

/**
 * Returns a list of existing dynamic rules filtered by the given action types
 *
 * @param {*} actionTypes An array of action types to filter the rules by
 * @param {*} optionalPriorities An optional list of priorities or a single priority to filter the rules by
 */
export const getExistingDynamicRulesByActionTypes = async (actionTypes, optionalPriorities) => {
    if (!isMV3()) {
        return [];
    }

    const dynamicRules = await getExistingDynamicRules(optionalPriorities);
    return dynamicRules.filter((rule) => actionTypes.includes(rule.action.type));
};

export const getLastDynamicRuleId = async () => {
    let lastRuleId = 1;
    const existingDynamicRules = await getExistingDynamicRules();
    // console.debug('EXISTING_DYNAMIC_RULES', existingDynamicRules);
    if (existingDynamicRules.length > 0) {
        const LastNth = existingDynamicRules.length - 1
        lastRuleId = existingDynamicRules[LastNth].id;
    }
    return lastRuleId;
};

export const getLastSessionRuleId = async () => {
    if (!isMV3()) {
        return 1;
    }

    let lastRuleId = 1;
    const existingSessionRules = await getExistingSessionRules();
    // console.debug('EXISTING_SESSION_RULES', existingSessionRules);
    if (existingSessionRules.length > 0) {
        lastRuleId = existingSessionRules.at(-1).id;
    }
    return lastRuleId;
};

let ruleIdLock = false;
let highestAllocatedRuleId = 0;

// Test utility function to reset the high-water mark
// ONLY FOR TESTING
export const resetHighestAllocatedRuleId = () => {
    highestAllocatedRuleId = 0;
};

// prevents race conditions with the rule id generation
export const getNextDynamicRuleId = async () => {
    // Wait for any ongoing ID allocation to complete
    while (ruleIdLock) {
        await new Promise(resolve => setTimeout(resolve, 1));
    }
    
    ruleIdLock = true;
    
    try {
        let currRuleId = await getLastDynamicRuleId();
        // Use the higher of the current last rule ID or our tracked highest allocated ID
        let nextRuleId = Math.max(currRuleId, highestAllocatedRuleId) + 1;
        
        // Update our high-water mark
        highestAllocatedRuleId = nextRuleId;
        
        return nextRuleId;
    } finally {
        ruleIdLock = false;
    }
}

export const getNextSessionRuleId = async () => {
    // Wait for any ongoing ID allocation to complete
    while (ruleIdLock) {
        await new Promise(resolve => setTimeout(resolve, 1));
    }
    
    ruleIdLock = true;
    
    try {
        let currRuleId = await getLastSessionRuleId();
        // Use the higher of the current last rule ID or our tracked highest allocated ID
        let nextRuleId = Math.max(currRuleId, highestAllocatedRuleId) + 1;
        
        // Update our high-water mark
        highestAllocatedRuleId = nextRuleId;
        
        return nextRuleId;
    } finally {
        ruleIdLock = false;
    }
}

export const clearAllDynamicRules = async (optionalPriorities) => {
    if (!isMV3()) {
        return;
    }

    const existingRules = await getExistingDynamicRules(optionalPriorities);
    const ruleIds = existingRules.map((rule) => rule.id);
    await chrome.declarativeNetRequest.updateDynamicRules({
        addRules: [],
        removeRuleIds: [...ruleIds],
    });
};

export const getExistingSessionRules = () => {
    if (!isMV3()) {
        return [];
    }

    return chrome.declarativeNetRequest.getSessionRules();
};

const addSessionRules = async (newRules) => {
    if (!isMV3()) {
        return;
    }
    await chrome.declarativeNetRequest.updateSessionRules({
        addRules: newRules,
        removeRuleIds: [],
    }, () => {
        let error = chrome.runtime.lastError;
        if (error) {
            console.error('Error adding session rules', error);
        }
    });
}

export const addSimpleSessionRules = async (hosts, actionType, optionalPriority = null) => {
    if (!isMV3()) {
        return;
    }
    let newRules = [];
    if (optionalPriority === undefined) {
        optionalPriority = DYNAMIC_RULE_ADS_PRIORITY;
    }
    const lastRuleId = await getLastSessionRuleId();
    let newRuleId = lastRuleId + 1;
    // remove existing rules for the same action type
    const existingSessionRules = await getExistingSessionRules();
    hosts = hosts.filter((host) => existingSessionRules.every((rule) => rule.condition.urlFilter !== host));

    let resourceTypes = allSupportedResourceTypes();
    for (const host of hosts) {
        const newRule = {
            id: newRuleId,
            priority: optionalPriority,
            action: {
                type: actionType,
            },
            condition: {
                urlFilter: `||${host}`,
                resourceTypes: resourceTypes,
            }
        };
        newRules.push(newRule);
        newRuleId += 1;
    }
    addSessionRules(newRules);
};

export const toggleGtldDomains = async (enable, domains) => {
    if (!isMV3()) {
        return;
    }

    if (enable) {
        let ruleId = (await getLastDynamicRuleId()) + 1;

        const rules = [];
        for (const domain of domains) {
            rules.push({
                id: ruleId,
                priority: RISKY_TLDS_PRIORITY,
                action: {
                    type: 'block',
                },
                condition: {
                    urlFilter: `||.${domain}`,
                    resourceTypes: allSupportedResourceTypes(),
                }
            });
            ruleId += 1;
        }

        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: rules,
            removeRuleIds: [],
        });
    } else {
        await clearAllDynamicRules(RISKY_TLDS_PRIORITY);
    }
};

export const updateDynamicTrackerWhitelistRules = async (trackerWhitelist) => {
    let unusedRules = [];
    // tracker whitelist is a list consisting of either easylist entries or tracker entries
    // Tracker whitelisting rules be like:  {"type":"tracker","host":"bayphoto.com","tracker":"www.googletagmanager.com"}
    // Easylist whitelisting rules be like: {"type":"tracker","host":"hulu.com","tracker":"easylist"}
    if (!isMV3()) {
        return unusedRules;
    }

    console.debug('UDTW: trackerWhitelist', { trackerWhitelist });
    const trackerRules = await getExistingDynamicRules(DYNAMIC_TRACKER_WHITELIST_PRIORITY);
    const easylistRules = await getExistingDynamicRules(DYNAMIC_EASYLIST_WHITELIST_PRIORITY);
    const existingRules = trackerRules.concat(easylistRules);
    const firstInstall = existingRules.length === 0;
    let currRuleId = await getLastDynamicRuleId();
    const rulesToAdd = [];
    // if the existing rules are not the same as the tracker whitelist, then we need to update the rules
    let isModified = existingRules.length !== trackerWhitelist.length;

    for (const trackerEntry of trackerWhitelist) {
        if (trackerEntry && trackerEntry.host && trackerEntry.tracker) {
            let { host, tracker } = trackerEntry;
            host = urlHost(host);
            const isEasylist = tracker.toLowerCase() === 'easylist';

            const existingRulesForHost = existingRules.filter(
                (rule) => (rule.condition.initiatorDomains &&
                    rule.condition.initiatorDomains.includes(host)) ||
                    (rule.condition.urlFilter && rule.condition.urlFilter === tracker)
            );
            // if the rule is not present, it means the list is modified
            // adding a new rule and removing another may result in the same number of rules
            // yet if the rules are different, we need to update the rules
            const isPresent = existingRulesForHost.length > 0;
            isModified = isModified || !isPresent;

            if (isEasylist) {
                const newRules = [
                    {
                        id: ++currRuleId,
                        priority: DYNAMIC_EASYLIST_WHITELIST_PRIORITY,
                        action: { type: 'allow' },
                        condition: {
                            urlFilter: "*",
                            initiatorDomains: [host],
                            resourceTypes: allSupportedResourceTypes(),
                        }
                    },
                    {
                        id: ++currRuleId,
                        priority: DYNAMIC_EASYLIST_WHITELIST_PRIORITY,
                        action: { type: 'allow' },
                        condition: {
                            urlFilter: host,
                            resourceTypes: allSupportedResourceTypes(),
                        }
                    }
                ];
                rulesToAdd.push(...newRules);
            } else {
                const newRule = {
                    id: ++currRuleId,
                    priority: DYNAMIC_TRACKER_WHITELIST_PRIORITY,
                    action: {
                        type: 'allow',
                    },
                    condition: {
                        initiatorDomains: [host],
                        urlFilter: tracker,
                        resourceTypes: allSupportedResourceTypes(),
                    }
                };
                rulesToAdd.push(newRule);
            }
        }
    }

    let ruleIdsToRemove = [];
    if (isModified) {
        // remove all existing rules so that unwhitelisted entries are removed
        ruleIdsToRemove = existingRules.map((rule) => rule.id);
    }

    if (isModified || firstInstall) {
        console.debug('UDTWR: Attempting to add new rules', {
            rulesToAdd,
            ruleIdsToRemove,
            addLen: rulesToAdd.length,
            removeLen: ruleIdsToRemove.length,
            existingLen: existingRules.length
        });
        try {
            await chrome.declarativeNetRequest.updateDynamicRules({
                addRules: rulesToAdd,
                removeRuleIds: ruleIdsToRemove,
            });
        } catch (error) {
            console.error('UDTWR: error updating dynamic rules', error);
            const msg = error.message || error;
            if (msg.toLowerCase().includes('dynamic rule count exceeded')) {
                console.error('UDTWR: maximum number of dynamic rules reached');
                await clearAllDynamicRules([DYNAMIC_TRACKER_WHITELIST_PRIORITY, DYNAMIC_EASYLIST_WHITELIST_PRIORITY]);
                // Do not retry
            }
        }
    }
}

export const removeDynamicTrackerWhitelistRules = async (trackerWhitelist) => {
    if (!isMV3()) {
        return;
    }

    const rulesToRemove = [];
    for (const trackerEntry of trackerWhitelist) {
        if (trackerEntry && trackerEntry.host && trackerEntry.tracker) {
            let { host, tracker, priority } = trackerEntry;

            const existingRules = await getExistingDynamicRules(priority);
            const existingRulesForHost = existingRules.filter(
                (rule) => {
                    if (rule.condition?.initiatorDomains?.includes(host) &&
                        rule.condition?.urlFilter === tracker
                    ) {
                        return true;
                    }
                }
            );
            for (const rule of existingRulesForHost) {
                rulesToRemove.push(rule.id);
            }
        }
    }

    await chrome.declarativeNetRequest.updateDynamicRules({
        addRules: [],
        removeRuleIds: rulesToRemove,
    });
}

export function allSupportedResourceTypes() {
    return browserName() === 'Firefox' ?
        allResourceTypes.filter((type) => !['webtransport', 'webbundle'].includes(type)) : allResourceTypes;
}

export function supportedSubFrameResourceTypes() {
    return browserName() === 'Firefox' ?
        subFrameResourceType.filter((type) => !['webtransport', 'webbundle'].includes(type)) : subFrameResourceType;
}

export const mainFrameResourceType = ['main_frame'];
export const subFrameResourceType = [
    'sub_frame',
    'stylesheet',
    'script',
    'image',
    'font',
    'object',
    'xmlhttprequest',
    'ping',
    'csp_report',
    'media',
    'websocket',
    'other',
    'webtransport',
    'webbundle'
];
export const allResourceTypes = [...mainFrameResourceType, ...supportedSubFrameResourceTypes()];
