import {chrome} from "../../../utils/polyfill.ts";
import {malwarebytes} from "../app";
import ExclusionHandler from "./exclusions-common";

export class MV2ExclusionHandler extends ExclusionHandler {
    constructor() {
        super();
    }

    exclude(host, exclusions) {
        console.debug('EXC: On MapExclusions', {host, exclusions});
        let namedNodeValue;
        return this.exclude_mapExclusionsToInts(exclusions).then((intArray) => {
            exclusions = intArray;
            //Then look at cache and datastore for existing data
            host = host.toLowerCase();
            return this.getExclusionsForHost(host);
        }).then((existingData) => {
            //Then merge with new data
            console.debug('EXC: On MergeExclusions for Exclude');
            return this.exclude_mergeExclusions(host, existingData, exclusions, true);
        }).then((mergedData) => {
            //Then set the named node in datastore
            console.debug('EXC: On SetNamedNode matching merged exclusions:', malwarebytes.EXCLUSIONS);
            return this.exclude_setNamedNode(host, mergedData);
        }).then((namedNode) => {
            //Then get the exclusion node from datastore
            //StackOverflow mentioned a limit of 8kb per key
            namedNodeValue = namedNode;
            console.debug('EXC: On GetExclusionsNode');
            return this.exclude_getExclusionsNode();
        }).then((existingExclusionsKeys) => {
            //Then merge name into the gotten array
            console.debug('EXC: On AddKeyToExclusionsIndex');
            return this.exclude_addKeyToExclusionsIndex(host, existingExclusionsKeys);
        }).then((mergedExclusionsIndex) => {
            //Then set the exclusion node back into datastore
            console.debug('EXC: On SetExclusionsNode');
            return this.exclude_setExclusionsNode(mergedExclusionsIndex, namedNodeValue);
        }).catch((err) => {
            console.error(err);
            return Promise.reject(err);
        });
    }

    async exportExclusions() {
        const allExclusions = await this.getExclusions();
        for (const domain in allExclusions) {
            if (Object.hasOwn(allExclusions, domain)) {
                allExclusions[domain] = await this.exclude_mapIntsToExclusions(allExclusions[domain]);
            }
        }
        console.debug('EXC: Exporting Exclusions', allExclusions);
        return allExclusions;
    }

    async importExclusions(exclusions) {
        const existingExclusions = await this.exportExclusions();

        console.debug('EXC: Importing Exclusions', exclusions);
        for (const domain in exclusions) {
            if (Object.hasOwn(exclusions, domain)) {
                if (Object.hasOwn(existingExclusions, domain) && 
                    existingExclusions[domain] === exclusions[domain]) {
                    continue;
                }
                await this.exclude(domain, exclusions[domain]);
            }
        }
    }

    getExclusionsSync() {
        return this.EXCLUSIONS;
    }

    async getExclusions() {
        try {
            this.getIndividualExclusions();

            const exclusionsIndex = await this.exclude_getExclusionsNode();
            const fullData = await this.getExclusions_getExclusionsByNames(exclusionsIndex);
    
            let allExclusions = {};
            for (let exclusion in fullData) {
                if (Object.hasOwn(fullData, exclusion)) {
                    if (fullData[exclusion].some((exlusionNumber) => typeof exlusionNumber !== 'number' ||
                        exlusionNumber <= 0)) {
                        /* eslint-disable max-len */
                        fullData[exclusion] = fullData[exclusion].filter((exlusionNumber)=> typeof exlusionNumber === 'number' && exlusionNumber >= 0);
                        /* eslint-enable max-len */
                        console.debug('EXC: Exclusion values cleaned succesfully', exclusion, fullData[exclusion] );
                    }
                    allExclusions = {...allExclusions, [exclusion]: fullData[exclusion]};
                }
            }
            //Cache it for later
            this.EXCLUSIONS = allExclusions;
            return Promise.resolve(allExclusions);    
        } catch (err) {
            console.error('GE: Exclusion Get Error: ', err);
            throw err;
        }
    }

    getExclusions_getExclusionsByNames(hostNames) {
        //Based on the 8kb limit
        //Each key should be around 40b = 200 keys should be possible (max websites blocked), 150 keys will be safe
        return new Promise((resolve, reject) => {
            chrome.storage.local.get(hostNames, function(result) {
                if (chrome.runtime.lastError) {
                    // eslint-disable-next-line prefer-promise-reject-errors
                    reject({error: chrome.runtime.lastError.message});
                } else {
                    resolve(result);
                }
            });
        });
    }

    async removeAllExclusions() {
        await this.removeAllExclusions_clearCache();
        const exclusionsIndex = await this.exclude_getExclusionsNode();
        await this.removeAllExclusions_removeExclusionsByNames(exclusionsIndex);
        await this.removeAllExclusions_removeIndexNode();
        return exclusionsIndex;
    }

    removeExclude(host, exclusions, addOverride) {
        let namedNodeValue;
        //Change the constant for the actual integers stored in db
        return this.exclude_mapExclusionsToInts(exclusions).then((intArray) => {
            exclusions = intArray;
            //Then look at cache and datastore for all existing allow-data for the domain
            host = host.toLowerCase();
            console.debug('RMX: On GetExistingData');
            return this.getExclusionsForHost(host);
        }).then((existingData) => {
            //Then remove the changed data
            return this.exclude_removeExclusions(host, existingData, exclusions);
        }).then((mergedData) => {
            if (mergedData == null) {
                return Promise.resolve(null);
            }
            //it used to be a NEW node could only happen from an add operation
            //  but now we also store hardcoded 'off' values (negatives) so any operation can result in a new node
            if (addOverride && addOverride === true) {
                console.log("RMX: OnMergeExclusions for Add");
                return this.exclude_mergeExclusions(host, mergedData, exclusions, false);
            }
            return Promise.resolve(mergedData);
        }).then((mergedData) => {
            //Then set the named node in datastore
            console.debug('RMX: On SetNamedNode matching merged exclusions:', this.EXCLUSIONS);
            return this.exclude_setNamedNode(host, mergedData);
        }).then((namedNode) => {
            //Then get the exclusion node from datastore
            //StackOverflow mentioned a limit of 8kb per key
            namedNodeValue = namedNode;
            console.debug('EXC: On GetExclusionsNode');
            return this.exclude_getExclusionsNode();
        }).then((existingExclusionsKeys) => {
            //Then merge name into the gotten array
            console.debug('EXC: On AddKeyToExclusionsIndex');
            return this.exclude_removeKeyFromExclusionsIndex(host, existingExclusionsKeys);
        }).then((mergedExclusionsIndex) => {
            //Then set the exclusion node back into datastore
            console.debug('EXC: On SetExclusionsNode');
            return this.exclude_setExclusionsNode(mergedExclusionsIndex, namedNodeValue);
        }).then((updatedExclusionsIndex) => {
            if (updatedExclusionsIndex === true) {
                delete this.EXCLUSIONS[host];
            }
            return Promise.resolve(updatedExclusionsIndex);
        }).catch((err) => {
            return Promise.reject(err);
        });
    }

    removeExclusions(host) {
        return this.removeExclusions_removeCacheNode(host).then((_ignore) => {
            return this.removeExclusions_removeHostNode(host);
        }).then((_ignored) => {
            return this.exclude_getExclusionsNode();
        }).then((exclusionsIndex) => {
            return this.removeExclusions_removeHostFromIndex(host, exclusionsIndex);
        }).then((updatedExclusionsIndex) => {
            return this.exclude_setExclusionsNode(updatedExclusionsIndex);
        }).catch((err) => {
            return Promise.reject(err);
        });
    }

    excludeTemporarily(_host, _tabId) {
        // MV2 implementation is in app.js
    }

    isTemporarilyExcluded(_host, _tabId) {
        // MV2 implementation is in app.js
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ///////////////////////////////////////////////////END PUBLIC INTERFACE ////////////////////////////////////////////
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    exclude_addKeyToExclusionsIndex(lowercaseHost, existingExclusionsKeys) {
        console.log("exclude_addKeyToExclusionsIndex: starting with: ", existingExclusionsKeys);
        //Update 'array' and push it back into storage
        if (!existingExclusionsKeys) {
            existingExclusionsKeys = [];
        } else if (!Array.isArray(existingExclusionsKeys)) {
            //Somehow we got an object, not an array. Converting.
            let tmp = existingExclusionsKeys;
            existingExclusionsKeys = [];
            for (let prop in tmp) {
                if (Object.hasOwn(tmp, prop)) {
                    existingExclusionsKeys.push(tmp);
                }
            }
        }
        console.debug("exclude_addKeyToExclusionsIndex: normalized to: ", existingExclusionsKeys);
        if (!existingExclusionsKeys.includes(lowercaseHost)) {
            existingExclusionsKeys.push(lowercaseHost);
        }
        console.debug("exclude_addKeyToExclusionsIndex: resolving as: ");
        console.debug(existingExclusionsKeys);
        return Promise.resolve(existingExclusionsKeys);
    }

    exclude_removeKeyFromExclusionsIndex(lowercaseHost, existingExclusionsKeys) {
        console.debug("exclude_removeKeyFromExclusionsIndex: starting with: ", existingExclusionsKeys);
        if (existingExclusionsKeys.includes(lowercaseHost)) {
            existingExclusionsKeys.splice(existingExclusionsKeys.indexOf(lowercaseHost), 1);
        }
        return Promise.resolve(existingExclusionsKeys);
    }

    exclude_deduplicateMergeExclusions(host) {
        //Now to deduplicate with the help of stack overflow (as js doesn't implicitly have a method for this)
        //https://stackoverflow.com/a/1584377/984830
        for (let i = 0; i < this.EXCLUSIONS[host].length; ++i) {
            for (let j = i + 1; j < this.EXCLUSIONS[host].length; ++j) {
                if (this.EXCLUSIONS[host][i] === this.EXCLUSIONS[host][j]) {
                    this.EXCLUSIONS[host].splice(j--, 1);
                }
            }
        }
    }

    exclude_getExclusionsNode() {
        return new Promise((y, n) => {
            chrome.storage.local.get(["exclusions"], function(result) {
                //results should be an array of keys of exclusions (one key per host)
                if (chrome.runtime.lastError) {
                    console.debug("Rejecting GetExclusionsNode after datastore");
                    // eslint-disable-next-line prefer-promise-reject-errors
                    n({error: chrome.runtime.lastError.message});
                } else {
                    if (result && result.exclusions) {
                        y(result.exclusions);
                    } else {
                        y(result);
                    }
                }
            });
        });
    }

    exclude_makeNegative(newData) {
        let newNewData = [];
        for (let j = 0; j < newData.length; j++) {
            if (newData[j] > 0) {
                newNewData[j] = -1 * newData[j];
            } else {
                newNewData[j] = newData[j];
            }
        }
        return newNewData;
    }

    exclude_mapExclusionsToInts(exclusionStr) {
        try {
            const mapped = exclusionStr.split(',').map((exclusionConst) =>
                ExclusionHandler.exclusionConstantToInt(exclusionConst)
            );
            return Promise.resolve(mapped);
        } catch (e) {
            console.log("Rejecting MapExclusions with: ", e);
            return Promise.reject(e);
        }
    }

    exclude_mapIntsToExclusions(exclusionIntArr) {
        try {
            const mapped = exclusionIntArr.map((exclusionInt) =>
                ExclusionHandler.exclusionIntToConstant(exclusionInt)
            );
            const consts = [...mapped];
            return Promise.resolve(consts.join(','));
        } catch (e) {
            console.log("Rejecting MapInts with: ", e);
            return Promise.reject(e);
        }
    }

    exclude_mergeExclusions(host, existingData, newData, isAddingExclusion) {
        let resolvedArr = this.exclude_normalizeExclusions(existingData);
        console.debug("EME: mergeExclusions normalized arr:", resolvedArr);
        if (resolvedArr.length > 0) {
            resolvedArr = this.exclude_unopposeMergeExclusions(resolvedArr, newData);
            console.debug("EME: mergeExclusions ran 'unoppose', new arr:", resolvedArr);
            if (!isAddingExclusion) {
                newData = this.exclude_makeNegative(newData);
            }
            this.EXCLUSIONS[host] = resolvedArr.concat(newData);
        } else {
            if (!isAddingExclusion) {
                newData = this.exclude_makeNegative(newData);
            }
            this.EXCLUSIONS[host] = newData;
        }
        console.debug("EME: mergeExclusions exclusions[" + host + "] set to:", this.EXCLUSIONS[host]);
        this.exclude_deduplicateMergeExclusions(host);
        console.debug("EME: mergeExclusions exclusions[" + host + "] deduplicated to:", this.EXCLUSIONS[host]);
        return Promise.resolve(this.EXCLUSIONS[host]);
    }

    exclude_normalizeExclusions(existingData) {
        //NOTE: Turn the existing data into an array, regardless of starting type
        let resolvedArr;
        if (existingData) {
            if (Array.isArray(existingData)) {
                resolvedArr = existingData;
            } else {
                //Map thenResolved keys to an array we can concat with, for some reason we received an object
                resolvedArr = [];
                if (existingData instanceof Object) {
                    for (let key in existingData) {
                        if (Object.hasOwn(existingData, key)) {
                            resolvedArr.push(key);
                        }
                    }
                } else if (existingData) { //could be undefined or string
                    resolvedArr.push(existingData);
                }
            }
        } else {
            resolvedArr = [];
        }
        return resolvedArr;
    }

    exclude_removeExclusions(host, existingData, removedData) {
        if (existingData && Array.isArray(existingData) && existingData.length > 0) {
            for (let i = 0; i < removedData.length; ++i) {
                for (let j = 0; j < existingData.length; ++j) {
                    if (existingData[j] === removedData[i]) {
                        existingData.splice(j--, 1);
                    }
                }
            }
        }
        console.debug("Delete node? " + (!existingData || existingData.length === 0), existingData);
        if (!existingData || existingData.length === 0) {
            //nothing excluded anymore - remove the key itself
            //WE NEED A SPLICE BECAUSE NULLING CAUSES NEVERENDING LOAD FOR ROW
            delete this.EXCLUSIONS[host];
            return Promise.resolve(null);
        } else {
            this.EXCLUSIONS[host] = existingData;
            return Promise.resolve(this.EXCLUSIONS[host]);
        }
    }

    exclude_setExclusionsNode(data, namedNodeValuePassThrough) {
        return new Promise((y, n) => {
            chrome.storage.local.set({'exclusions': data}, function() {
                if (chrome.runtime.lastError) {
                    console.debug("Rejecting SetExclusionsNode");
                    // eslint-disable-next-line prefer-promise-reject-errors
                    n({error: chrome.runtime.lastError.message});
                } else {
                    //true for delete, array for add
                    y({success: (namedNodeValuePassThrough ? namedNodeValuePassThrough : true)});
                }
            });
        });
    }

    exclude_setNamedNode(lowercaseHost, data) {
        return new Promise((y, n) => {
            if (data) {
                console.debug("Updating the named node");
                chrome.storage.local.set({[lowercaseHost]: data}, function() {
                    if (chrome.runtime.lastError) {
                        console.debug("Rejecting SetNamedNode in datastore");
                        // eslint-disable-next-line prefer-promise-reject-errors
                        n({error: chrome.runtime.lastError.message});
                    } else {
                        console.debug("Resolving SetNamedNode in datastore");
                        y(data);
                    }
                });
            } else if (lowercaseHost) {
                console.debug("Removing the named node");
                chrome.storage.local.remove(lowercaseHost, function() {
                    if (chrome.runtime.lastError) {
                        console.debug("Rejecting SetNamedNode (remove) in datastore");
                        // eslint-disable-next-line prefer-promise-reject-errors
                        n({error: chrome.runtime.lastError.message});
                    } else {
                        console.debug("Resolving SetNamedNode (remove) in datastore");
                        y({}); // return empty object!
                    }
                });
            }
        });
    }

    exclude_unopposeMergeExclusions(resolvedArr, newData) {
        //NOTE: We remove everything from existing data, which is in the new data
        //  (at this point, the new data has not been negated even if removing)
        for (let i = 0; i < newData.length; i++) {
            for (let j = 0; j < resolvedArr.length; j++) {
                //NOTE: We remove both the positive and negative versions
                if (newData[i] === resolvedArr[j] || newData[i] === (-1 * resolvedArr[j])) {
                    resolvedArr.splice(j--, 1);
                }
            }
        }
        return resolvedArr;
    }

    getExclusionsForHost(host) {
        let exclusions = this.EXCLUSIONS[host];
        if (exclusions) {
            console.debug("Resolving GetExistingData from cache:");
            console.debug(exclusions);
            return Promise.resolve(exclusions);
        } else {
            //we didnt have the row cached, load it from store
            return new Promise((y, n) => {
                chrome.storage.local.get([host], function(result) {
                    if (chrome.runtime.lastError) {
                        console.debug("Rejecting GetExistingData after datastore");
                        // eslint-disable-next-line prefer-promise-reject-errors
                        n({error: chrome.runtime.lastError.message});
                    } else {
                        y(result);
                    }
                });
            });
        }
    }

    getIndividualExclusions() {
        return new Promise((res) => {
            chrome.storage.local.get(["exclusionIndividual"], ({exclusionIndividual = {}}) => {
                this.EXCLUSION_INDIVIDUAL = exclusionIndividual;
                res();
            });
        });
    }

    removeAllExclusions_clearCache() {
        this.EXCLUSIONS = {};
        return Promise.resolve(true);
    }

    removeAllExclusions_removeExclusionsByNames(exclusionsIndex) {
        return new Promise((resolve, reject) => {
            chrome.storage.local.remove(exclusionsIndex, function() {
                if (chrome.runtime.lastError) {
                    console.debug("Rejecting RemoveNamedNodes");
                    // eslint-disable-next-line prefer-promise-reject-errors
                    reject({error: chrome.runtime.lastError.message});
                } else {
                    console.debug("Resolving RemoveNamedNodes");
                    //true for delete, array for add
                    resolve({success: true});
                }
            });
        });
    }

    removeAllExclusions_removeIndexNode() {
        return new Promise((y, n) => {
            chrome.storage.local.remove('exclusions', function() {
                if (chrome.runtime.lastError) {
                    console.debug("Rejecting RemoveExclusionsNode");
                    // eslint-disable-next-line prefer-promise-reject-errors
                    n({error: chrome.runtime.lastError.message});
                } else {
                    console.debug("Resolving RemoveExclusionsNode");
                    //true for delete, array for add
                    y({success: true});
                }
            });
        });
    }

    removeExclusions_removeCacheNode(host) {
        return new Promise((resolve) => {
            delete this.EXCLUSIONS[host];
            resolve({success: true});
        });
    }

    removeExclusions_removeHostFromIndex(host, exclusionsIndex) {
        for (let i = 0; i < exclusionsIndex.length; ++i) {
            if (exclusionsIndex[i] === host) {
                exclusionsIndex.splice(i--, 1);
            }
        }
        return Promise.resolve(exclusionsIndex);
    }

    removeExclusions_removeHostNode(host) {
        return new Promise((y, n) => {
            chrome.storage.local.remove([host], function() {
                if (chrome.runtime.lastError) {
                    // eslint-disable-next-line prefer-promise-reject-errors
                    n({error: chrome.runtime.lastError.message});
                } else {
                    y({success: true});
                }
            });
        });
    }
}
