import { chrome } from './polyfill.ts';
import {
    isIncognito,
    uuidGet,
    fetchJSON,
    browserName,
    urlHost,
    getBrowserInformation,
} from './utils';
import { simpleStorageGet, simpleStorageSet } from './storage.ts';
import * as telemetry from './telemetry';
import { TARGET_BROWSER } from '../app/scripts/browser-const';

export const telemetryRequest = async () => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.log("Incognito Mode: No Telemetry was sent.");
            return;
        }
        const userAgent = await getBrowserInformation();

        // If we have access to the user's machine ID we want to use that,
        // so we can correlate those datapoints in DSE.
        const machineId = await simpleStorageGet("machineId");
        const uuid = await uuidGet();

        const _bName = browserName();
        console.debug(`RTR: BNAME IS: ${JSON.stringify(_bName)}`);
        const bName = _bName.toLowerCase();
        const request = {
            client: {
                program: "mbgc-c",
                build: "consumer",
                version: chrome.runtime.getManifest().version,
                caller: { name: "BrowserExtension" },
                components: await telemetryComponents(),
                architecture: userAgent.cpu.architecture || "",
                os_version: userAgent.os.name ?
                    userAgent.os.name + " " + userAgent.os.version :
                    "",
                browser_version: userAgent.name ?
                    userAgent.name + " " + userAgent.version :
                    "",
                device_type: userAgent.device.type || "",
                language: chrome.i18n.getUILanguage(),
                email_opt_in: false,
            },
            header: {
                time: new Date().toISOString(),
                installation_token: bName + "-" + uuid,
                uuid,
            },
        };        
        return request;
    }
};

export const getTimestamp = () => {
    return new Date().toISOString();
};

export const telemetryUserAction = async (action, _isProd = true) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.log("Incognito Mode: No Telemetry was sent.");
            return;
        }
        if ((await simpleStorageGet("sendTelemetry")) === false) {
            console.log("Telemetry disabled: No Telemetry was sent.");
            return;
        }
        let request = await telemetry.telemetryRequest();
        request.client.caller["trigger"] = "action";
        request["ui_events"] = [
            {
                action,
                event_type: "EmailInputScreen",
                obj_id: request.header.uuid, //TODO: WTF is "<session_id>" in this context, if not a UUID
                time_stamp: getTimestamp(), //NOTE: NOT a time stamp, use an iso string. We dont have control over this
            },
        ];
        let body = JSON.stringify(request);
        const telemetryHost = buildTelemetryHost();
        try {

            let resp = await fetchJSON(
                `${telemetryHost}/api/v2/streams/user_actions/record/`,
                {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body,
                }
            );
            console.debug("Sent userAction[" + action + "] telemetry. ", resp);
        } catch (err) {
            console.error("Error sending userAction[" + action + "] telemetry. ", err);
        }
    }
};

export const telemetryComponents = async () => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Telemetry was sent.");
            return;
        }

        const databases = (await simpleStorageGet("databases")) || {};

        let components = {};

        for (let database in databases) {
            if (Object.hasOwn(databases, database)) {
                components[database] = databases[database].version;
            }
        }

        return components;
    }
};

export const telemetryClient = async (_isProd = true) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Telemetry was sent.");
            return;
        }
        // throttle client telemetry to once per day
        if (await telemetry.telemetryThrottle("client", 24 * 60 * 60 * 1000)) {
            console.debug("Telemetry skipped for throttling");
            return;
        }
        let request = await telemetry.telemetryRequest();
        request.client.caller.trigger = "scheduled";
        let body = JSON.stringify(request);
        const telemetryHost = buildTelemetryHost();
        try {
            await fetchJSON(
                `${telemetryHost}/api/v2/streams/client/record`,
                {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: body,
                }
            );
            console.debug("TC: Sent daily client telemetry");
        } catch (err) {
            console.error("Error sending client telemetry. ", err);
        }
    }
};

export const telemetryMalware = async (type, subtype, url, tabURL, tabHistory, threatName, _isProd = true) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Telemetry was sent.");
            return;
        }

        let request = await telemetry.telemetryRequest();

        request.client.caller.trigger = "detection";

        request.mwac = {
            detections: [
                {
                    direction: "outbound",
                    disposition: "block",
                    category: type,
                    sub_category: subtype,
                    network: {
                        url,
                        domain: urlHost(url),
                        parent_url: tabURL,
                        url_history: tabHistory.join(" "),
                    },
                    ...(threatName && { process: { threatName } })
                },
            ],
        };

        const telemetryHost = buildTelemetryHost();
        try {
            await fetchJSON(
                `${telemetryHost}/api/v2/streams/mwac/record`,
                {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify(request),
                }
            );
            console.debug(`TLE: Sent telemetry malware event for type ${type}, url ${url} threatname ${threatName}`);
        } catch (err) {
            console.error("Error sending telemetry malware event. ", err);
        }
    }
};

/* eslint-disable camelcase */ // Needed for DSE request structure
export const telemetryBlockCount = async (ad_count, malware_count, scams_count, _isProd = true) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Ad/Tracker Telemetry was sent.");
            return;
        }

        // we can include machine ID here because it's just aggregate data
        let request = await telemetry.telemetryRequest();

        request.client.caller.trigger = "timed";

        request.mwac = {
            detections: [
                {
                    direction: "outbound",
                    disposition: "block",
                    category: "ad_tracker",
                    ad_count: ad_count || 0,
                },
                {
                    direction: "outbound",
                    disposition: "block",
                    category: "malware",
                    malware_count: malware_count || 0,
                },
                {
                    direction: "outbound",
                    disposition: "block",
                    category: "scam",
                    scams_count: scams_count || 0,
                },
            ],
        };

        const telemetryHost = buildTelemetryHost();
        try {
            await fetchJSON(
                `${telemetryHost}/api/v2/streams/mwac/record`,
                {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify(request),
                }
            );
            console.debug('TA: Sent ad_tracker daily telem');
        } catch (err) {
            console.error("Error sending ad_tracker daily telemetry. ", err);
        }
    }
};
/* eslint-enable camelcase */

export const telemetryExclusion = async (category, type, domain, _isProd = true) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Telemetry was sent.");
            return;
        }

        const request = await telemetry.telemetryRequest();
        request.client.caller.trigger = 'exclusion';
        request.mwac = {
            detections: [
                {
                    direction: 'outbound',
                    disposition: 'exclude ' + category + ' ' + type,
                    category: category,
                    sub_category: type,
                    network: {
                        domain: domain
                    }
                }
            ]
        };
        const telemetryHost = buildTelemetryHost();
        try {
            await fetchJSON(
                `${telemetryHost}/api/v2/streams/mwac/record`,
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(request),
                }
            );
            console.debug(`TLE: Sent exclusion telemetry for category ${category}, type ${type} and domain ${domain}`);
        } catch (err) {
            console.error("Error sending exclusion telemetry. ", err);
        }
    }
};

export const telemetryThrottle = async (type, interval) => {
    if (TARGET_BROWSER !== 'Firefox') {
        if (isIncognito()) {
            console.debug("Incognito Mode: No Telemetry was sent.");
            return true;
        }

        console.debug('CALLING... SIMPLE STORAGE GET');
        let telemetry = await simpleStorageGet("telemetry");
        console.debug('CALLED SIMPLE STORAGE GET', telemetry);
        if (
            telemetry &&
            telemetry[type] &&
            telemetry[type].lastSendTime &&
            telemetry[type].lastSendTime < Date.now() &&
            Date.now() < telemetry[type].lastSendTime + interval
        ) {
            return true;
        }

        telemetry = telemetry || {};
        telemetry[type] = telemetry[type] || {};
        telemetry[type].lastSendTime = Date.now();

        await simpleStorageSet({ telemetry });

        return false;
    } else {
        return true;
    }
};

export function buildTelemetryHost(prod = true) {
    const productCode = 'mbgc-c';
    let version = chrome.runtime.getManifest().version;
    // replace all periods with dashes
    version = version.replace(/\./g, '-');
    // format of the host is https://productCode-product_version.telemetry.malwarebytes.com
    return prod ? `https://${productCode}-${version}.telemetry.malwarebytes.com` : `https://${productCode}-${version}.telemetry.mwbsys-stage.com`;
}
