import { chrome } from "../../utils/polyfill.ts";
import { RecordType, malwarebytes } from './app';
import {
    createBlockUrl,
    domain,
    urlDomains,
    urlHost,
    getBlockedHostURL,
    isBlockPage,
    getTabById,
    getCurrentTab
} from "../../utils/utils";
import { simpleStorageGet, simpleStorageSet } from "../../utils/storage.ts";
import {
    MSG_MV3_LAST_TAB_URL,
    MSG_ADD_ALLOW_TEMPORARY,
    MSG_MV3_INIT_WITH_IDS,
    MSG_MV3_POST_NATIVE_EXCLUSION,
    MSG_INFO_BLOCKED_CONTENT
} from './app-consts';
import { exclusionHandler } from './exclusions/exclusions';
import { telemetryMalware } from "../../utils/telemetry";
import { TARGET_BROWSER } from "./browser-const";
import { tryInitMV3RulesetsWithSelectedIds } from "./mv3/ruleset-utils.js";
import MALWARE_HASH from "@/db/hashmaps/mbgc.mv3.malware_1.js";
import { sendTabMessage } from "../../utils/messaging/messaging.ts";

Object.defineProperty(malwarebytes, "MV3_TAB_HISTORY", { value: {}, writable: true, configurable: true });
Object.defineProperty(malwarebytes, "NATIVE_EXCLUSION_CACHE", { value: {}, writable: true, configurable: true });
// keep track of the blocked requests that have been redirected to the block page and tracked
Object.defineProperty(malwarebytes, "TELEMETRY_CACHE", { value: {}, writable: true, configurable: true });

const setLastStatsFetchTimestamp = (timestamp) => {
    return simpleStorageSet({ lastStatsFetchTimestamp: timestamp });
};

const getLastStatsFetchTimestamp = () => {
    return simpleStorageGet('lastStatsFetchTimestamp');
};

const getCurrentRuleBlockCount = () => {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get(['ruleBlockCount'], (result) =>
            chrome.runtime.lastError ? reject(chrome.runtime.lastError) : resolve(result.ruleBlockCount)
        );
    });
};

const setCurrentRuleBlockCount = (blockCount) => {
    return new Promise((resolve, reject) => {
        chrome.storage.sync.set({ ruleBlockCount: blockCount }, () =>
            chrome.runtime.lastError ? reject(chrome.runtime.lastError) : resolve(true)
        );
    });
};

const incrementBlockCount = async ({ incrBy = 0 }) => {
    const currBlockCount = (await getCurrentRuleBlockCount()) || 0;
    await setCurrentRuleBlockCount(currBlockCount + incrBy);
};

const debugUrls = [
    'https://www.malwarebytes.com/browserguard/download-debug-logs?yes=true',
    'https://www.malwarebytes.com/browserguard/debugger?action=user-group-a',
    'https://www.malwarebytes.com/browserguard/debugger?action=my-data',
    'https://www.malwarebytes.com/browserguard/debugger?action=full-stats',
    'https://www.malwarebytes.com/browserguard/debugger?action=factory-reset',
    'https://www.malwarebytes.com/browserguard/debugger?action=toggle-reputation',
    'https://www.malwarebytes.com/browserguard/debugger?action=toggle-local-ip-allow',
    'https://www.malwarebytes.com/browserguard/debugger?action=test-channel-update',
    'https://www.malwarebytes.com/browserguard/debugger?action=populate-detection-history',
];

malwarebytes.addDetailsToTabHistory = ({ tabId, url }) => {
    if (malwarebytes.isOwnExtensionPage(url)) {
        return;
    }

    if (!malwarebytes.MV3_TAB_HISTORY[tabId] || malwarebytes.MV3_TAB_HISTORY[tabId].url !== url) {
        malwarebytes.MV3_TAB_HISTORY[tabId] = { tabId, url };
    }
};

const addToNativeExclusionCache = (tabId, url) => {
    if (!tabId || !url) {
        return;
    }

    if (malwarebytes.isOwnExtensionPage(url)) {
        return;
    }

    if (!malwarebytes.NATIVE_EXCLUSION_CACHE[tabId]) {
        malwarebytes.NATIVE_EXCLUSION_CACHE[tabId] = [];
    }

    malwarebytes.NATIVE_EXCLUSION_CACHE[tabId].push(url);
};

const removeFromNativeExclusionCache = ({ tabId, url }) => {
    if (!tabId || !url) {
        return;
    }

    if (malwarebytes.isOwnExtensionPage(url)) {
        return;
    }

    if (malwarebytes.NATIVE_EXCLUSION_CACHE[tabId]) {
        malwarebytes.NATIVE_EXCLUSION_CACHE[tabId] =
            malwarebytes.NATIVE_EXCLUSION_CACHE[tabId].filter((item) => item !== url);
    }

    if (malwarebytes.NATIVE_EXCLUSION_CACHE[tabId].length === 0) {
        delete malwarebytes.NATIVE_EXCLUSION_CACHE[tabId];
    }
};

const isInNativeExclusionCache = (tabId, url) => {
    if (!tabId || !url) {
        return false;
    }

    if (malwarebytes.isOwnExtensionPage(url)) {
        return false;
    }

    if (!malwarebytes.NATIVE_EXCLUSION_CACHE[tabId]) {
        return false;
    }

    return malwarebytes.NATIVE_EXCLUSION_CACHE[tabId].includes(url);
};

const cleanUrl = (host) => {
    host = urlHost(host);
    if (host.startsWith('http://')) {
        host.replace('http://', '');
    }

    if (host.startsWith('https://')) {
        host.replace('https://', '');
    }

    if (host.startsWith('www.')) {
        host = host.replace('www.', '');
    }
    return host;
};

const excludeTemporarily = async (host, tabId) => {
    // remove www from host
    host = cleanUrl(host);

    await exclusionHandler.excludeTemporarily(host, tabId);
    malwarebytes.EXCLUSION_TEMPORARY = { host: host, tab: tabId };
};

const isTemporarilyExcluded = (url, tabId) => {
    if (!url || !tabId) {
        return false;
    }

    url = cleanUrl(url);

    if (exclusionHandler.isTemporarilyExcluded(url, tabId)) {
        return true;
    }

    if (malwarebytes.EXCLUSION_TEMPORARY &&
        malwarebytes.EXCLUSION_TEMPORARY.host === url && malwarebytes.EXCLUSION_TEMPORARY.tab === tabId) {
        return true;
    }

    return false;
};

function trackTelemetry(details) {
    const url = details.url;
    const tabId = details.tabId;
    const requestId = details.requestId;
    const initiator = details.initiator;
    const telemType = details.type;
    if (!malwarebytes.TELEMETRY_CACHE[requestId]) {
        console.debug('MV3: onErrorOccurred: NO TELEMETRY CACHE record FOR REQUEST ID: ', requestId);

        const host = urlHost(initiator);
        if (telemType === RecordType.MALWARE) {
            telemetryMalware(telemType, telemType, url, initiator, []);
            sendTabMessage(
                tabId,
                {
                    type: MSG_INFO_BLOCKED_CONTENT,
                    src: host,
                    malware: urlHost(url),
                    tabId
                }
            );
        }
        malwarebytes.recordAll(telemType, tabId, urlHost(host), urlHost(url), true);
        malwarebytes.updateBadgeCount(tabId);
    }
}

const setTypeByRulesetURL = (blockedURL) => {
    if (MALWARE_HASH[blockedURL]) { // complixity O(1)
        return RecordType.MALWARE;
    } return RecordType.AD;
}

// Replaces the default block page if the request is for the main frame.
// Please note that this only redirects the main frame if the request is for ads
const replaceDefaultBlockPageIfMainframe = (details) => {
    if (details.type === 'main_frame') {
        // type should be looked at in the future
        const blockUrl = createBlockUrl(null, details.url, 'ads', '', details.tabId, null, details.url);
        chrome.tabs.update(details.tabId, { url: blockUrl })
    }
};

const onRuleMatchBlock = (details) => {
    const record = {};
    record.url = details.url;
    record.tabId = details.tabId;
    record.requestId = details.requestId;
    record.initiator = details.initiator;
    record.type = setTypeByRulesetURL(cleanUrl(details.url));
    trackTelemetry(record);
    console.debug('DNR: Rule matched', JSON.stringify(details));
    replaceDefaultBlockPageIfMainframe(details);
}

chrome.webRequest.onErrorOccurred.addListener((details) => {
    // to prevent double counting, don't track telemetry for redirect-hack blocks
    console.debug('MV3: onErrorOccurred', { details });
    if (details.error === 'net::ERR_BLOCKED_BY_CLIENT') {
        onRuleMatchBlock(details);
    }
}, { urls: ["<all_urls>"] });

// eslint-disable-next-line max-len
chrome.declarativeNetRequest.onRuleMatchedDebug && chrome.declarativeNetRequest.onRuleMatchedDebug.addListener((details) => {
    console.debug('DNR: Rule matched', JSON.stringify(details));
});

const redirectedTabs = new Map(); // Cache to track redirected tabs and URLs

const lookForPreFetchBlock = async (details) => {
    // Check for prefetch headers
    const isPrefetch = details.requestHeaders.some(
        h => h.name.toLowerCase() === 'purpose' && h.value === 'prefetch'
    );
    if (!isPrefetch) { return; } // only handle prefetch requests

    const CurrentTab = await getTabById(details?.tabId);
    if (!isBlockPage(CurrentTab?.pendingUrl)) { return; } // only handle block page requests

    const blockedHost = getBlockedHostURL(CurrentTab?.pendingUrl);
    const blockedHostUrlDomain = domain(blockedHost);
    const blockedRequestUrlDomain = domain(details?.url);
    const cacheKey = `${CurrentTab.id}:${blockedHostUrlDomain}`;

    if (blockedHostUrlDomain === blockedRequestUrlDomain && !isPrefetchBlockedBefore(cacheKey)) { // make sure the blocked url is the same as the prefetch url
        redirectedTabs.set(cacheKey, true); // Mark this tab and URL as redirected
        await chrome.tabs.update(CurrentTab.id, { url: CurrentTab.url });
    }
}

const isPrefetchBlockedBefore = (cacheKey) => {
    return redirectedTabs.has(cacheKey);
}

chrome.webRequest.onBeforeSendHeaders.addListener(
    async (details) => {
        await lookForPreFetchBlock(details);
    }, { urls: ["<all_urls>"] }, ["requestHeaders"]
);


chrome.webRequest.onBeforeRequest.addListener((details) => {
    if (details.tabId === -1) {
        return;
    }

    details["sld"] = domain(details.url);
    details["domains"] = urlDomains(details.url);
    details["isMainFrame"] = details.type === "main_frame";
    if (details.isMainFrame) {
        malwarebytes.tryNewTab(details);
        malwarebytes.addDetailsToTabHistory(details);
    }

    if (malwarebytes.isOwnExtensionPage(details.url)) {
        return;
    }

    if (isTemporarilyExcluded(details.url, details.tabId)) {
        return;
    }

    if (malwarebytes.isExcludedIndividuallyMV3(details)) {
        console.debug("BTW: Excluded Individually: " + details.url);
        malwarebytes.recordMV3ExcludedIndividual(details);
        return { cancel: false }
    }

    addToNativeExclusionCache(details.tabId, details.url);

    if (!malwarebytes.READY_TO_ACCEPT_REQUESTS) {
        console.log(`REQ: Request deferred - extension is being initialized (Req: ${details.url})`);
        return;
    }

    if (TARGET_BROWSER !== 'Firefox' && !details.isMainFrame) {
        // not handled by mv2-style web request blocking
        return;
    }

    // Check if the request is for a prefetch is already blocked to avoid loops
    const cacheKey = `${details.tabId}:${domain(details.url)}`;
    if (isPrefetchBlockedBefore(cacheKey)) {
        redirectedTabs.delete(cacheKey); // Remove the cache entry after handling
        return; // Prevent redirect loop
    }

    const cancelResult = malwarebytes.onBeforeTabWebRequest(details);
    if (TARGET_BROWSER === 'Firefox') {
        // Use MV2-style web request blocking for Firefox
        return cancelResult;
    }

    if (debugUrls.includes(details.url)) {
        if (cancelResult.redirectUrl) {
            chrome.tabs.update(details.tabId, { url: cancelResult.redirectUrl });
            return;
        }
    }

    if (cancelResult.cancel && cancelResult.cancel === true) {
        malwarebytes.TELEMETRY_CACHE[details.requestId] = details;
        removeFromNativeExclusionCache(details.tabId, details.url);

        if (cancelResult.type && cancelResult.type === 'ad') {
            // Don't redirect ad blocks, allow MV3 rulesets to block these
            return;
        }

        const blockUrl = createBlockUrl(null, details.url, 'malware', '', details.tabId, null, details.url);
        incrementBlockCount({ incrBy: 1 });

        console.debug('MV3_OBTW: BLOCKING: ', { url: details.url, isMainFrame: details.isMainFrame });
        if (details.isMainFrame === true && details.frameId !== null && details.frameId === 0) {
            chrome.tabs.update(details.tabId, { url: blockUrl })
                .then((_tab) => {
                    // TODO: Do redirects for only the main page and skip the assets
                });
        }
    } else if (cancelResult.redirectUrl) {
        console.debug('MV3: OBTW: REDIRECTING: ', { url: details.url, redirectUrl: cancelResult.redirectUrl });
        removeFromNativeExclusionCache(details.tabId, details.url);

        chrome.tabs.update(details.tabId, { url: cancelResult.redirectUrl })
            .then((_tab) => {
            });
    }
}, { urls: ["<all_urls>"] }, TARGET_BROWSER === 'Firefox' ? ['blocking'] : []);

chrome.runtime.onMessage.addListener((request, sender, callback) => {
    let isFromExtension = !sender.tab ||
        (chrome && sender.tab.url.startsWith("chrome-extension://" + chrome.runtime.id)) || //top-level event pages
        (chrome && sender.url.startsWith("chrome-extension://" + chrome.runtime.id)) || //embedded event pages
        (typeof browser !== "undefined" && sender.tab.url.startsWith("moz-extension://" + chrome.i18n.getMessage("@@extension_id"))) ||
        (typeof browser !== "undefined" && sender.url.startsWith("moz-extension://" + chrome.i18n.getMessage("@@extension_id")));

    if (request.type === 'requestPermissions') {
        chrome.permissions.request({
            origins: ['<all_urls>'],
        }).catch((err) => {
            console.error('MV3: Error requesting permissions: ', err);
        });
        return false;
    }

    if (request.type === MSG_MV3_LAST_TAB_URL) {
        // Should only be called from an extension page
        if (!isFromExtension) {
            console.debug('MSG_LAST_TAB_URL should only be called from an extension page');
            callback({ url: null });
        }
        const tabId = request.payload.tabId;
        if (malwarebytes.MV3_TAB_HISTORY[tabId]) {
            callback({ url: malwarebytes.MV3_TAB_HISTORY[tabId].url });
        } else {
            callback({ url: null });
        }
        return true;
    } else if (request.type === MSG_ADD_ALLOW_TEMPORARY) {
        console.debug('MV3 ADD ALLOW TEMP', request);
        if (request.host) {
            excludeTemporarily(request.host, request.initiator)
                .then(() => {
                    malwarebytes.EXCLUSION_TEMPORARY = { host: urlHost(request.host), tab: request.initiator };
                    callback({ success: true });
                }).catch((err) => {
                    console.error('MV3: Error excluding temporarily: ', { err, request });
                    callback({ success: false });
                });
        }

        return true;
    } else if (request.type === MSG_MV3_INIT_WITH_IDS) {
        tryInitMV3RulesetsWithSelectedIds(request.rulesetIds).then(({ failedRulesets, enabledRulesets }) => {
            callback({ success: true, failedRulesets, enabledRulesets });
        }).catch((err) => {
            console.error('MV3: Error initializing with rulesets: ', { err, request });
            callback({ success: false, error: err });
        });
        return true;
    } else if (request.type === MSG_MV3_POST_NATIVE_EXCLUSION) {
        if (isInNativeExclusionCache(request.tabId, request.url)) {
            console.debug('MV3: POST_NATIVE_EXCLUSION:', request);
            malwarebytes.postNativeExclusion(request.url);
            removeFromNativeExclusionCache(request.tabId, request.url);
        }
        return true;
    } else if (request.type === 'is-dark-mode-response') {
        if (isDarkModeResolver) {
            isDarkModeResolver(request.data);
            closeOffscreenDocument();
            isDarkModeResolver = null;
        }
        return true;
    }
});

(async () => {
    const ruleMatchCheckInterval = chrome.declarativeNetRequest.GETMATCHEDRULES_QUOTA_INTERVAL;
    await chrome.alarms.create(
        'updateMV3StatsAlarm',
        { delayInMinutes: 1, periodInMinutes: ruleMatchCheckInterval || 10 }
    );
    console.log('MV3: updateMV3StatsAlarm created with', ruleMatchCheckInterval, 'minute period');
})();

chrome.alarms.onAlarm.addListener(async (alarm) => {
    if (alarm.name === 'updateMV3StatsAlarm') {
        console.log('MV3: updateMV3StatsAlarm triggered');
        const currentTab = await getCurrentTab();
        const oneMinuteAgo = new Date().getTime() - (1 * 60 * 1000);

        let lastFetchTimestamp = (await getLastStatsFetchTimestamp()) || null;
        if (!lastFetchTimestamp) {
            lastFetchTimestamp = oneMinuteAgo;
        }

        let matchedRulesQuery = { minTimeStamp: lastFetchTimestamp };
        if (currentTab) {
            matchedRulesQuery = { minTimeStamp: lastFetchTimestamp, tabId: currentTab.id };
        }

        const rulesMatchedDetails = await chrome.declarativeNetRequest.getMatchedRules(matchedRulesQuery);
        await setLastStatsFetchTimestamp(new Date().getTime());
        if (chrome.runtime.lastError) {
            const isQuotaBreachError =
                chrome.runtime.lastError.includes('MAX_GETMATCHEDRULES_CALLS_PER_INTERVAL quota');
            console.warn(
                `MV3: Error getting matched rules: ${isQuotaBreachError ? 'getMatchedRules quota breached' : ''}`
            );
            return;
        }
        const rulesMatched = rulesMatchedDetails.rulesMatchedInfo;
        incrementBlockCount({ incrBy: rulesMatched.length });
    }
});

let isDarkModeResolver;

malwarebytes.isInDarkMode = async () => {
    sendMessageToOffscreenDocument('is-dark-mode', null);
    return new Promise((resolve, _reject) => {
        isDarkModeResolver = resolve;
    });
};


const OFFSCREEN_DOCUMENT_PATH = '/offscreen/offscreen.html';

let isCreatingOffscreenDocument;
async function sendMessageToOffscreenDocument(type, data) {
    // Create an offscreen document if one doesn't exist yet
    try {
        if (!(await hasOffscreenDocument())) {
            if (isCreatingOffscreenDocument) {
                await isCreatingOffscreenDocument;
            } else {
                isCreatingOffscreenDocument = chrome.offscreen.createDocument({
                    url: OFFSCREEN_DOCUMENT_PATH,
                    reasons: ['MATCH_MEDIA'],
                    justification: 'Call match media API to determine if the browser is in light or dark mode'
                });
            }
            await isCreatingOffscreenDocument;
            isCreatingOffscreenDocument = null;
        }
    } catch (err) {
        console.warn('OFFSCREEN: ', err)
    }

    // Now that we have an offscreen document, we can dispatch the
    // message.
    chrome.runtime.sendMessage({
        type,
        target: 'offscreen',
        data
    });
}

async function closeOffscreenDocument() {
    if (!(await hasOffscreenDocument())) {
        return;
    }

    await chrome.offscreen.closeDocument();
}

async function hasOffscreenDocument() {
    // eslint-disable-next-line no-undef
    const matchedClients = await clients.matchAll();
    for (const client of matchedClients) {
        if (client.url.endsWith('offscreen.html')) {
            return true;
        }

    }
    return false;
}
