export const susOnFF = [".bat.txt", ".ps1.txt", ".sh.txt", ".py.txt"];

// Block downloads based on URL patterns
export const urlPatternsDownloads = new Map();

urlPatternsDownloads.set([...allSus, ...susOnFF], "/wp-.*/");
