import {ALLOWED_EXCLUSION_OPTIONS} from "../app-consts";

export default class ExclusionHandler {
    constructor() {
        this.EXCLUSIONS = {};
        this.EXCLUSION_INDIVIDUAL = {};
    }

    static exclusionConstantToInt(exclusionConstant) {
        const intValue = ALLOWED_EXCLUSION_OPTIONS.find(((option)=>option.constantKey === exclusionConstant));
        if (intValue !== undefined) {
            return intValue.value;
        }

        throw new Error(`${exclusionConstant} is not recognised`);
    }

    static exclusionIntToConstant(exclusionInt) {
        const constant = ALLOWED_EXCLUSION_OPTIONS.find(((option)=>option.value === exclusionInt));
        if (constant !== undefined) {
            return constant.constantKey;
        }
        throw new Error(`${exclusionInt} is not recognised`);
    }

    exclude(_host, _exclusions) {
        throw new Error('Cannot call this method on base class');
    }

    getExclusions() {
        throw new Error('Cannot call this method on base class');
    }

    exportExclusions() {
        throw new Error('Cannot call this method on base class');
    }

    importExclusions(_exclusions) {
        throw new Error('Cannot call this method on base class');
    }

    /* This can return a single or many named nodes */
    getExclusions_getExclusionsByNames(_hostNames) {
        throw new Error('Cannot call this method on base class');
    }

    removeAllExclusions() {
        throw new Error('Cannot call this method on base class');
    }

    removeExclude(_host, _exclusions, _addOverride) {
        throw new Error('Cannot call this method on base class');
    }

    removeExclusions(_host) {
        throw new Error('Cannot call this method on base class');
    }

    excludeTemporarily(_host, _tabId) {
        throw new Error('Cannot call this method on base class');
    }

    isTemporarilyExcluded(_host, _tabId) {
        throw new Error('Cannot call this method on base class');
    }
}
