import { chrome } from "../../../utils/polyfill.ts";
import {
    getExistingDynamicRulesByActionTypes,    
    getNextDynamicRuleId,
    mainFrameResourceType, supportedSubFrameResourceTypes,
    DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY,
} from '../mv3/dynamic-rule-utils';
import { simpleStorageGet, simpleStorageSet } from "../../../utils/storage.ts";

export class Mv3UserBlocksHandler {
    constructor() {
        this.CACHE = new Map();
    }

    async migrateBlockedItems() {
        console.debug('Mv3UserBlocksHandler.migrateBlockedItems - Migrating blocked items');
        // migrate dynamic rules        
        // In v1, user block rules had a priority of DYNAMIC_RULE_ALL_PRIORITY -> 30
        const oldUserBlockRules = await getExistingDynamicRulesByActionTypes(['block', 'redirect'], [30]);
        const ruleIdsToDelete = oldUserBlockRules.map((rule) => rule.id);
        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: [],
            removeRuleIds: [...ruleIdsToDelete]
        });

        // in v1, an array of urls items was stored in extension storage with the key 'blockedItems'
        const blockedItems = await simpleStorageGet('blockedItems');
        if (!blockedItems) {
            console.debug('Mv3UserBlocksHandler.migrateBlockedItems - No blocked items found');
            return;
        }

        // we need to transform the blocked items into the new format {domain: {blockUrls: [], xpath: string}}
        // But we don't know the domain for each item, so we'll have to use a wildcard domain '*'        
        const migratedBlockedItems = [{ domain: '*', blockUrls: blockedItems, xpath: '' }];
        await this.importBlockedItems({ '*': migratedBlockedItems });

        await chrome.storage.local.remove('blockedItems');

        // get updated dynamic rules
        const newRules = await this._getExistingUserBlockDynamicRules();
        console.debug('Mv3UserBlocksHandler.migrateBlockedItems - newRules: ', { newRules });
        // get new saved blocked items
        const newSavedBlockedItems = await this.getBlockedItems();
        console.debug('Mv3UserBlocksHandler.migrateBlockedItems - newSavedBlockedItems: ', { newSavedBlockedItems });

        return migratedBlockedItems;
    }

    async importBlockedItems(items) {
        console.debug('UserBlocksHandler.importBlockedItems - Importing blocked items', { items });
        for (const [domain, domainEntries] of Object.entries(items)) {
            for (const entry of domainEntries) {
                await this.blockItem({ domain, ...entry });
            }
        }
        console.debug('UserBlocksHandler.importBlockedItems - Imported blocked items', { items });
    }

    /**
    * @typedef {Object} BlockItem
 * @property {string} domain - The domain of the tab where the block was created
 * @property {string=} blockUrl - The URL to block
     * @property {string=} xpath - The XPath selector to block
     * 
     * @param {BlockItem} item - The item to block
     * @returns {Promise<void>}
     */
    async blockItem(item) {
        console.debug('MV3 UserBlocksHandler: Blocking item', { item });
        if (!this.CACHE) {
            this.CACHE = new Map();
        }

        // cache is a map of domain -> { blockUrls: [], xpath: string }[]

        const domainEntries = this.CACHE.get(item.domain) || [];
        // if we already have an entry for this xpath, update it, otherwise add a new entry for this xpath/blockUrls
        const existingEntry = domainEntries.find((entry) => entry.xpath.toUpperCase() === item.xpath.toUpperCase());
        if (existingEntry) {
            existingEntry.blockUrls = Array.from(new Set([...existingEntry.blockUrls, ...item.blockUrls]));
        } else {
            domainEntries.push({ blockUrls: item.blockUrls, xpath: item.xpath });
        }
        this.CACHE.set(item.domain, domainEntries);
        await this.saveBlockedItems();
    }

    async getBlockedItems() {
        await this.loadSavedBlockedItems();
        const cacheObj = {};
        for (const [domain, domainEntries] of this.CACHE) {
            cacheObj[domain] = domainEntries;
        }
        return cacheObj;
    }

    async loadSavedBlockedItems() {
        const userBlockedItems = await simpleStorageGet('userBlockedItems');
        if (!userBlockedItems) {
            return;
        }

        this.CACHE = new Map();

        // userBlockedItems is an object mapping domains to an entry of { blockUrls, xpath }

        for (const [domain, domainEntries] of Object.entries(userBlockedItems)) {
            this.CACHE.set(domain, domainEntries);
        }

        console.debug('Mv3UserBlocksHandler.loadSavedBlockedItems - cache: ', this.CACHE);
    }

    async saveBlockedItems() {

        if (!this.CACHE) {
            return false;
        }

        // // 1. get the items in the cache
        // // 2. save items to extension storage
        // // 3. Add dynamic rules for the url items

        const cacheObj = {};
        for (const [domain, domainEntries] of this.CACHE) {
            const entryArr = domainEntries.map((entry) => ({ blockUrls: entry.blockUrls, xpath: entry.xpath }));
            cacheObj[domain] = entryArr;
        }

        await simpleStorageSet({
            userBlockedItems: cacheObj
        });
        console.debug('Mv3UserBlocksHandler.saveBlockedItems - Saved blocked items to extension storage');

        // create dynamic rules to block the blockUrls
        await this._updateDynamicRules();
        console.debug('Mv3UserBlocksHandler.saveBlockedItems - Updated dynamic rules');

        return true;
    }

    /**
     * @typedef {Object} BlockItem
     * @property {string} domain - The domain of the tab where the block was created
     * @property {string=} blockUrl - The URL to block (optional)
     * @property {string=} xpath - The XPath selector to block (optional)
     * 
     * @param {BlockItem} item - The item to delete
     * @returns {Promise<boolean>}
     */
    async deleteBlockedItem(item) {
        if (!this.CACHE) {
            return false;
        }

        const { domain, _blockUrls, xpath } = item;

        let domainEntries = this.CACHE.get(domain) || [];
        // remove the entry with this xpath from the cache
        domainEntries = domainEntries.filter((entry) => entry.xpath.toUpperCase() !== xpath.toUpperCase());
        this.CACHE.set(domain, domainEntries);
        console.debug('Mv3UserBlocksHandler.deleteBlockedItem - Cache after deletion: ', { cache: this.CACHE });

        return await this.saveBlockedItems();
    }

    async deleteAllBlockedItems() {
        if (!this.CACHE) {
            return false;
        }

        console.debug('Mv3UserBlocksHandler.deleteAllBlockedItems - cache: ', { cache: this.CACHE });
        this.CACHE.clear();

        await this.saveBlockedItems();
        console.debug('Mv3UserBlocksHandler.deleteAllBlockedItems - Saved blocked items to extension storage');

        return true;
    }

    isBlocked(domain, url) {
        if (!this.CACHE) {
            return false;
        }

        const domainEntries = this.CACHE.get(domain) || [];
        if (domainEntries.length === 0) {
            return false;
        }

        for (const entry of domainEntries) {
            const { blockUrls, _xpath } = entry;
            if (blockUrls.some((blockUrl) => blockUrl === url)) {
                return true;
            }
        }

        return false;
    }

    /**
     * Updates the dynamic rules for user blocked items by removing existing user-block rules and adding new ones
     */
    async _updateDynamicRules() {
        const domainToBlockedUrls = new Map();
        for (const [domain, domainEntries] of this.CACHE) {
            const blockedUrlsForDomain = domainEntries.flatMap((entry) => entry.blockUrls);
            domainToBlockedUrls.set(domain, Array.from(new Set(blockedUrlsForDomain)));
        }


        console.debug(
            'Mv3UserBlocksHandler._updateDynamicRules - domainToBlockedUrls: ', JSON.stringify(domainToBlockedUrls)
        );

        const existingDynamicRules = await this._getExistingUserBlockDynamicRules();
        console.debug(
            'Mv3UserBlocksHandler._saveItemsToDynamicRuleStorage - existingDynamicRules: ',
            JSON.stringify(existingDynamicRules)
        );
        const ruleIdsToDelete = existingDynamicRules.map((rule) => rule.id);
        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: [],
            removeRuleIds: [...ruleIdsToDelete]
        });
        console.debug('Mv3UserBlocksHandler._updateDynamicRules - Deleted existing user-block rules');

        const newRules = await this._buildMv3BlockRules(domainToBlockedUrls);
        console.debug('Mv3UserBlocksHandler._updateDynamicRules - newRules: ', newRules);
        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: [...newRules],
            removeRuleIds: [],
        });
    }

    async _buildMv3BlockRules(domainToBlockedUrls) {        
        const rules = [];

        for (const [_domain, blockedUrls] of domainToBlockedUrls) {
            for (const blockUrl of blockedUrls) {
                // remove query params from the blockUrl
                const urlWithoutQueryParams = blockUrl.split('?')[0];
                rules.push({
                    id: await getNextDynamicRuleId(), // This avoids race conditions with the rule id generation
                    priority: DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY,
                    action: {
                        type: 'block',
                    },
                    condition: {
                        urlFilter: urlWithoutQueryParams,
                        resourceTypes: supportedSubFrameResourceTypes(),
                    }
                });
                rules.push({
                    id: await getNextDynamicRuleId(), // This avoids race conditions with the rule id generation
                    priority: DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY,
                    action: {
                        type: 'redirect',
                        redirect: {
                            extensionPath: '/app/eventpages/block-mv3.html?type=user-block&url=' + blockUrl,
                        },
                    },
                    condition: {
                        urlFilter: urlWithoutQueryParams,
                        resourceTypes: mainFrameResourceType,
                    }
                });
            }
        }

        return rules;
    }

    async _getExistingUserBlockDynamicRules() {
        let rules = await getExistingDynamicRulesByActionTypes(
            ['block', 'redirect'],
            [DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY]);
        const filteredRules = rules.filter((rule) => {
            return rule.priority === DYNAMIC_RULE_USER_BLOCKED_ITEMS_PRIORITY;
        })
        return filteredRules;
    }
}
