import {
    DB_NAMES_BLOOM,
    DB_NAMES_RAW,
    REGEX_DBS,
    DB_DIR,
    DB_NAME_FEATURE_FLAGS,
    DB_NAMES_JSON,
    DB_NAME_MV3_DYNAMIC_WHITELIST_JSON, DB_HEURISTICS,
} from "./db-consts";
import {IndexedDatabase} from "./indexed-database";
import {IndexedDatabaseFile} from "./indexed-database-file";
import {isIndexedDbAvailable, cleanDbName, objToBinary} from "../utils";
import {simpleStorageGet, simpleStorageSet} from "../storage.ts";
import {getObjs as getObjsIndexedDb, saveObjs as saveObjsIndexedDb} from "../indexed-db.ts";
import {fileWrite, fileDelete, queuedFileRead} from "../file";

import bundledDbVersions from '../../db/version.json';
// const bundledDbVersions = require('../../db/version.json');

/**
 * Result for database result.
 * @typedef {Object} DbResult
 * @property {string} name full name
 * @property {string} version
 * @property {string} cleanName
 * @property {Object} data Object ready to be queried
 * @property {Object} [rawData] Raw object parsed from the file, use for cache
 * @property {Boolean} isBloom
 * @property {Boolean} isRaw
 * @property {Boolean} isRegex
 */

export const parseDbsParameter = (databases) => {
    const aux = new Set(databases);
    const bloomDbs = DB_NAMES_BLOOM.filter((x) => aux.has(x));
    const rawDbs = DB_NAMES_RAW.filter((x) => aux.has(x));
    const jsonDbs = DB_NAMES_JSON.filter((x) => aux.has(x));

    const result = bloomDbs.map((x) => {
        return {
            name: x,
            cleanName: cleanDbName(x),
            isBloom: true,
            isFeatureFlags: false
        };
    }).concat(rawDbs.map((x) => {
        return {
            name: x,
            cleanName: cleanDbName(x),
            isBloom: false,
            isFeatureFlags: false
        };
    })).concat(jsonDbs.map((x) => {
        return {
            name: x,
            cleanName: cleanDbName(x),
            isBloom: false,
            isFeatureFlags: false,
            isJson: true
        };
    }));
    if (aux.has(DB_NAME_FEATURE_FLAGS)) {
        result.push({
            name: DB_NAME_FEATURE_FLAGS,
            cleanName: cleanDbName(DB_NAME_FEATURE_FLAGS),
            isBloom: false,
            isFeatureFlags: true
        });
    }

    if (aux.has(DB_NAME_MV3_DYNAMIC_WHITELIST_JSON)) {
        result.push({
            name: DB_NAME_MV3_DYNAMIC_WHITELIST_JSON,
            cleanName: cleanDbName(DB_NAME_MV3_DYNAMIC_WHITELIST_JSON),
            isBloom: false,
            isFeatureFlags: false
        });
    }

    return result;
};
/**
 * Load the given databases from the db directory
 * @param {Array<String>} databases - List of databases to be loaded, use the full name
 * @return {Promise<Array<DbResult>>}
 * @example
 * await loadBundledDatabases(["mbgc.db.spyware.2", "mbgc.db.top1m.2"])
 */
export const loadBundledDatabases = async (databases) => {
    console.debug("LBD: Forcing reading bundled databases at " + new Date().toLocaleString());
    console.debug("LBD: Databases to be loaded:", databases);

    const allDbs = parseDbsParameter(databases);
    let dbResults = [];

    for (let i = 0; i < allDbs.length; i++) {
        try {
            let db = await loadBundledDb(allDbs[i].name, allDbs[i].isBloom, allDbs[i].isFeatureFlags);
            dbResults.push(db);
        } catch (error) {
            console.debug(`LBD: Error loading ${allDbs[i].name} from the bundle`, error);
        }
    }
    
    const result = [];
    
    dbResults.forEach((dbResult) => {
        if (!bundledDbVersions[dbResult.name]) {
            console.warn(`LBD: No DB found for ${dbResult.name}`);
            return;
        }
        let version = bundledDbVersions[dbResult.name].version || '0';
        result.push(buildDbResult(dbResult, version));
    });
    console.debug("LBD: Bundled unpack complete at " + new Date().toLocaleString());
    console.log(`LBD: ${result.length} bundled databases loaded`);
    return result;
};

/**
 * Load the given databases from the cache,
 * NOTE 1: Its not guaranteed that all database will be loaded, when that is the case, the unloaded databases will not be present in the result array
 * NOTE 2: In case of an error, this method will return an empty array
 * @param {Array<String>} databases - List of databases to be loaded, use the full name
 * @return {Promise<Array<DbResult>>}
 * @example
 * await loadCachedDatabases(["mbgc.db.spyware.2", "mbgc.db.top1m.2"])
 */
export const loadCachedDatabases = async (databases) => {
    if (!isIndexedDbAvailable()) {
        console.warn('LCD: IndexedDB not available in this browser');
        return [];
    }

    const allDbs = parseDbsParameter(databases);
    const cacheResult = await getObjsIndexedDb({
        dbName: 'cachedDb',
        storeName: 'cachedDbStore',
        storeOptions: {keyPath: 'dbName'},
        waitStrategy: 'allSettled',
        keys: allDbs.map((x) => x.cleanName)
    });

    const result = [];
    cacheResult.forEach((promiseResult, i) => {
        if (!promiseResult) {
            console.warn(`LCD: No cache db for ${allDbs[i].name}`);
            return;
        }
        const dbInfo = allDbs[i];
        if (promiseResult.status === 'rejected') {
            console.warn(`LCD: Error loading ${dbInfo.name} from the cache`, promiseResult.reason);
            return;
        }
        if (promiseResult.status !== 'fulfilled') {
            console.log(`LCD: ${dbInfo.name} not loaded from cache`);
            return;
        }
        const cacheObj = promiseResult.value;
        if (!cacheObj) {
            console.log(`LCD: ${dbInfo.name} not loaded from cache`);
            return;
        }

        if (cacheObj.dbName && cacheObj.data) {
            result.push(buildDbResult({
                name: dbInfo.name,
                isBloom: dbInfo.isBloom,
                isFeatureFlags: dbInfo.isFeatureFlags,
                isRaw: !dbInfo.isBloom,
                result: dbInfo.isBloom ? new IndexedDatabase(dbInfo.name, cacheObj.data.bloomFilter) : cacheObj.data
            }, cacheObj.version));
        } else {
            console.debug(`LCD: No cache db for ${dbInfo.name}.`, dbInfo.name);
        }
    });
    /**************************************************************************************************** */
    /* This block must be deprecated in the future, it migrates version unware dbs,
    6 months of support?, means this block must be delete on December 2022
    */
    const noVersions = result.filter((x) => !x.version);
    if (noVersions.length > 0) {
        console.warn(`LCD: Found ${noVersions.length} dbs with no version in the cache` +
            `, querying simpleStorage to get the version`);
        console.debug("LCD: Cache databases with no version", noVersions.map((x) => x.name));
        const dbVersions = (await simpleStorageGet("databases")) || {};
        noVersions.forEach((dbResult) => {
            dbResult.version = dbVersions[dbResult.name] && dbVersions[dbResult.name].version;
            console.debug(`LCD: Version for ${dbResult.name}:`, dbResult.version);
        });
        const withVersion = noVersions.filter((x) => x.version);
        if (withVersion.length > 0) {
            console.debug(`LCD: Re-writing ${withVersion.length} databases in the cache, version was set`);
            await saveDbsToCache(withVersion);
        }
    }
    /**************************************************************************************************** */
    console.log(`LCD: ${result.length} databases loaded from the cache`);
    return result;
};

/**
 * Save the databases to cache
 * @param {Array<DbResult>} databases - List of databases to save
 * @return {Promise<Boolean>}
 */
export const saveDbsToCache = async (databases) => {
    if (!isIndexedDbAvailable()) {
        console.warn('SDTC: IndexedDB not available in this browser');
        return false;
    }

    var cacheObjs = databases.map((db) => {
        let data;
        if (db.rawData) {
            data = db.rawData;
        } else if (db.data instanceof Set) {
            data = Array.from(db.data);
        } else {
            data = db.data;
        }
        return {dbName: db.cleanName, data, version: db.version};
    });

    await saveObjsIndexedDb({
        dbName: 'cachedDb',
        storeName: 'cachedDbStore',
        storeOptions: {keyPath: 'dbName'},
        values: cacheObjs
    });
    return true;
};

/**
 * Load the given databases from IdbFileStorage, the most recent databases
 * NOTE 1: Its not guaranteed that all database will be loaded, when that is the case, the unloaded databases will not be present in the result array
 * NOTE 2: In case of an error, this method will return an empty array
 * @param {Array<String>} databases - List of databases to be loaded, use the full name
 * @return {Promise<Array<DbResult>>}
 * @example
 * await loadIdbStorageDatabases(["mbgc.db.spyware.2", "mbgc.db.top1m.2"])
 */
export const loadIdbStorageDatabases = async (databases) => {
    if (!isIndexedDbAvailable()) {
        console.warn('LISD: IndexedDB not available in this browser');
        return [];
    }
    const allDbs = parseDbsParameter(databases);
    const promises = [];

    allDbs.forEach((dbInfo) => {
        promises.push(loadIdbStorageDb(dbInfo.name, dbInfo.isBloom, dbInfo.isFeatureFlags));
    });
    const result = [];
    const loadResults = await Promise.allSettled(promises);
    loadResults.forEach((promiseResult, i) => {
        const dbInfo = allDbs[i];
        if (promiseResult.status == 'rejected') {
            console.warn(`LISD: Error loading ${dbInfo.name} from the IdbStorage`, promiseResult.reason);
            return;
        }
        const dbResult = promiseResult.value;
        result.push(buildDbResult(dbResult, dbResult.version));
    });
    /**************************************************************************************************** */
    /* This block must be deprecated in the future, it migrates version unware dbs,
    6 months of support?, means this block must be delete on December 2022
    */
    const noVersions = result.filter((x) => !x.version);
    if (noVersions.length > 0) {
        console.warn(`LISD: Found ${noVersions.length} dbs with no version file` +
            `, querying simpleStorage to get the version`);
        console.debug("LISD: Databases with no version", noVersions.map((x) => x.name));
        const dbVersions = (await simpleStorageGet("databases")) || {};
        const promiseSavedVersions = [];
        noVersions.forEach((dbResult) => {
            dbResult.version = dbVersions[dbResult.name] && dbVersions[dbResult.name].version;
            console.debug(`LISD: Version for ${dbResult.name}:`, dbResult.version);
            if (dbResult.version) {
                promiseSavedVersions.push(saveVersionFile(dbResult.name, dbResult.version));
            }
        });
        if (promiseSavedVersions.length > 0) {
            try {
                await Promise.all(promiseSavedVersions);
                console.debug(`LISD: ${promiseSavedVersions.length} version files created`);
            } catch (error) {
                console.error(`LISD: Error saving version files`, error);
            }
        }
    }
    /**************************************************************************************************** */
    console.log(`LBD: ${result.length} databases loaded from IdbStorage`);
    return result;
};

/**
 * Save the given database pulled from Sirius
 * @param {String} name - Full name of the database
 * @param {String} version - Version
 * @param {any} fileContents - Raw bytes
 * @return {Promise<Void>}
 */
export const saveDownloadedDb = (name, version, fileContents) => {
    if (!version) {
        throw new Error(`Version is required`);
    }
    return Promise.all([
        fileDelete(DB_DIR, name),
        fileWrite(DB_DIR, name, fileContents),
        saveVersionFile(name, version),
        simpleStorageSet({idbStorageDatabases: true})
    ]);
};

const saveVersionFile = (name, version) => {
    const versionObj = {
        name,
        version
    };
    const versionBinary = objToBinary(versionObj);
    return fileWrite(DB_DIR, `${name}.version`, versionBinary);
};

const getVersionFromVersionFile = (name, fileContents) => {
    if (!fileContents) {
        console.warn(`GVFVF: Version file for ${name} is falsy`, fileContents);
        return undefined;
    }
    try {
        const obj = JSON.parse(new TextDecoder().decode(fileContents));
        return obj.version;
    } catch (error) {
        console.warn(`GVFVF: Error parsing version file for ${name}`, error);
        return undefined;
    }
};

//Helper methods
export const buildDbResult = (loadedDb, version) => {
    const row = {};
    row.name = loadedDb.name;
    row.version = version;
    row.cleanName = cleanDbName(row.name);
    row.isBloom = loadedDb.isBloom;
    row.isRaw = loadedDb.isRaw;
    row.data = loadedDb.result;
    row.isFeatureFlags = loadedDb.isFeatureFlags;
    if (row.cleanName == 'malware_urls') {
        row.rawData = row.data; //For incrementals
        row.data = new Set(row.data);
    }
    if (REGEX_DBS.includes(row.cleanName)) {
        row.rawData = row.data; //For cache
        row.data = row.data.map((pattern) => new RegExp(pattern, "i"));
        row.isRegex = true;
    }
    if (DB_NAMES_JSON.filter((db=>db !== DB_HEURISTICS)).includes(row.name)) {
        row.isJson = true;
        row.rawData = row.data;
        for (let i = 0; i < row.data.length; i++) {
            let r = row.data[i]?.r;
            if (!r && typeof row.data[i] == 'string') {
                let strRegex = row.data[i];
                let objRegex = {s: false, r: new RegExp(strRegex)};
                row.data[i] = objRegex;
            }
            if (r && typeof r == 'string') {
                let strRegex = r;
                // replace quadruple backslashes with double backslashes
                if (strRegex.startsWith('/') && strRegex.endsWith('/')) {
                    strRegex = strRegex.slice(1, -1);
                    strRegex = strRegex.replace(/\\\\/g, '\\');
                }
                try {
                    row.data[i].r = new RegExp(strRegex);
                } catch (error) {
                    console.debug(`Error parsing regex ${strRegex} from ${row.name}`, error);
                    delete row.data[i];
                }
            }
        }
        row.data = row.data.filter((x) => x);
    }
    return row;
};

export const loadBundledDb = (name, isBloom, isFeatureFlags) => {
    const bloomId = isBloom && new IndexedDatabase(name);
    const rawLoader = !isBloom && new IndexedDatabaseFile(name);
    const promise = isBloom ? bloomId.parseBundled() : rawLoader.readBundled();
    return promise.then((result) => {
        return {
            name,
            isBloom,
            isFeatureFlags,
            isRaw: !isBloom,
            result: isBloom ? bloomId : result
        };
    });
};

export const loadIdbStorageDb = (name, isBloom, isFeatureFlags) => {
    const bloomId = isBloom && new IndexedDatabase(name);
    const rawLoader = !isBloom && new IndexedDatabaseFile(name);
    const promise = isBloom ? bloomId.parse() : rawLoader.read();

    return Promise.allSettled([promise, queuedFileRead(`${name}.version`)]).then((promiseResults) => {
        const [fileResult, versionResult] = promiseResults;
        if (fileResult.status == 'rejected') {
            if (typeof fileResult.reason == 'string') {
                throw new Error(`Error loading ${name} from IdbStorage: ${fileResult.reason}`);
            }
            //reason should be an Error object
            throw fileResult.reason;
        }
        //We dont want to reject the main promise if the version promise failed
        const version = getVersionFromVersionFile(name, versionResult.value);
        return {
            name,
            isBloom,
            isFeatureFlags,
            isRaw: !isBloom,
            result: isBloom ? bloomId : fileResult.value,
            version
        };
    });
};
