
import { sha256 } from 'js-sha256';
import { chrome } from './polyfill.ts';
import {
    browserName,
    fetchJSON,
    cleanDbName,
    binaryToString,
    binaryToBase64,
    objToBinary,
    getBrowserInformation
} from './utils';
import { queuedFetchBinary } from './file';
import { getUUID } from './storage.ts';
import { SemVer } from './semver';
import { saveDownloadedDb, loadIdbStorageDatabases } from "./databases/db-persistence";
import { DB_NAMES_ALL, TOP1M_KEYWORD } from "./databases/db-consts";
import * as fzstd from 'fzstd';
import * as Sentry from "@sentry/browser";
import {BAD_PACKAGE_ERROR, MISSING_DATABASE_ERROR} from "@/app/scripts/app-consts";

// SIRIUS PROD
const SIRIUS_PROD_URL = "https://sirius.mwbsys.com/api/v1/updates/manifest";
const SIRIUS_PROD_TOKEN = "XbXzxs1H5c852pToE3xA";
// SIRIUS STAGING
const SIRIUS_STAGING_URL = "https://sirius-staging.mwbsys.com/api/v1/updates/manifest";
const SIRIUS_STAGING_TOKEN = "pznxfw65x9vuPAs_1N1y";

let SIRIUS_URL = SIRIUS_PROD_URL;
let SIRIUS_TOKEN = SIRIUS_PROD_TOKEN;

const USE_STAGING = false;
if (USE_STAGING) {
    console.warn("UPD: Using staging environment");
    SIRIUS_URL = SIRIUS_STAGING_URL;
    SIRIUS_TOKEN = SIRIUS_STAGING_TOKEN;
}

export class Updater {
    constructor(hooks, channel = "release", prod = true, isChannelSwitch = false) {
        this.hooks = hooks;
        this.channel = channel;
        this.prod = prod;
        this.isChannelSwitch = isChannelSwitch;
        if (!this.prod) {
            SIRIUS_URL = SIRIUS_STAGING_URL;
            SIRIUS_TOKEN = SIRIUS_STAGING_TOKEN;
        }
    }

    static hashSHA256(arrayBuffer) {
        return sha256(arrayBuffer);
    }

    cleanPackages(installedPackages) {
        let cleanedPackages = installedPackages.filter((p) => p !== null);
        cleanedPackages = cleanedPackages.filter((p) => Object.keys(p).length > 0);
        console.debug("UPD: Packages cleaned successfully.");
        return cleanedPackages;
    }

    /**
    * Update the given databases packages
    *
    * @param {Array<{name: string, version: string}>} databases - Databases to update
    * @return {Promise<Array<SiriusPackage>>}
    */

    async update(databases) {
        console.debug("UPD: Update called with channel: " + this.channel);
        console.debug("UPD: Will check updates for: ", databases);
        console.debug("UPD: Is production: ", this.prod);
        this.feedbackFunc && this.feedbackFunc("UPD: Will check updates");
        const validDbs = DB_NAMES_ALL.map(cleanDbName);
        let installedPackages = databases
            .filter((db) => validDbs.includes(cleanDbName(db.name)))
            .map(({ version = '', name }) => {
                version = version.trim();

                if (!version || version === '') {
                    version = '2.0.2';
                }

                if (!name) {
                    return null;
                }

                return {
                    name,
                    semver: version,
                    channel: this.channel,
                };
            });
        installedPackages = this.cleanPackages(installedPackages);
        console.debug("UPD: Version mapping part finished");
        const uuid = await getUUID();
        console.debug("UPD: GetUUID part finished");
        const browserInfo = await getBrowserInformation();
        const payload = {
            product: "mbgc-c",
            build: "consumer",
            semver: chrome.runtime.getManifest().version,
            os_version: browserInfo.name + " " + browserInfo.version,
            installation_token: browserName().toLowerCase() + "-" + uuid,
            installed_packages: installedPackages
        };
        let response;
        try {
            response = await fetchJSON(
                SIRIUS_URL,
                {
                    method: "POST",
                    headers: {
                        "Authorization": `Token token="${SIRIUS_TOKEN}"`,
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify(payload)
                }
            );

            console.debug("UPD: FetchSirius finished");
            if (response.status !== "ok") {
                console.error("Negative response from Sirius: ", JSON.stringify(response));
                throw new Error(response.status);
            }

        } catch (error) {
            console.error("Error fetching Sirius: ", error);
            this.notifyHook('onUpdateError');
            return []
        }

        // parse response from Sirius
        const siriusPackages = parsePackages(response.manifest.packages);
        const packagesToUpdate = siriusPackages.filter((pkg) => {
            if (!pkg.version) {
                return false;
            }
            const siriusVersion = new SemVer(pkg.version);
            const localVersion = new SemVer(databases.find((x) => x.name == pkg.name).version);
            return siriusVersion.greater(localVersion);
        });
        console.debug(`UPD: ${packagesToUpdate.length}/${siriusPackages.length} fetched packages need to be updated ` +
            `(${siriusPackages.length - packagesToUpdate.length} up-to-date)`);
        if (packagesToUpdate.length > 0) {
            this.notifyHook('onUpdateStart');
        }
        await Promise.all(packagesToUpdate.map((pkg) => Updater.download(pkg)));
        this.notifyHook('onDownloadComplete');
        return packagesToUpdate;
    }

    async updateIncrementals(databases) {
        let clonedDbs, clonedDbVers;
        try {
            clonedDbs = JSON.parse(JSON.stringify(databases));
            clonedDbVers = Object.keys(clonedDbs).map((name) => {
                return { name, version: clonedDbs[name].version };
            });
        } catch (e) {
            // ignore
        }

        console.debug("UPD_INC: Update called", { databases });

        // avoid asking for updates for the top1m database
        let validDbs = DB_NAMES_ALL.map(cleanDbName).filter((db) => db !== TOP1M_KEYWORD);

        console.debug("UPD_INC: Valid databases", validDbs);

        let installedPackages = Object.keys(databases)
            .filter((k) => validDbs.includes(k) && k !== TOP1M_KEYWORD) // avoid asking for updates for the top1m database
            .map((k) => {
                let version = databases[k] && databases[k].version && databases[k].version.trim();

                if (!version){
                    console.error(`UPD_INC: Database ${k} has no version.`);
                    // Something went wrong here!. The malwarebytes.DATABASES object is empty. We need to report this to Sentry
                    this.notifyHook('onUpdateError');
                    // eslint-disable-next-line max-len
                    throw new Error(`UPD_INC: ${MISSING_DATABASE_ERROR} - Database ${k} ${databases[k].name} has no version`);
                }

                if (this.isChannelSwitch) {
                    version = '0.0.0';
                }

                if (!databases[k].name) {
                    return null;
                }

                return {
                    name: databases[k].name,
                    semver: version,
                    channel: this.channel
                };
            });
        if (installedPackages.length === 0) {
            // Something went wrong here!. The malwarebytes.DATABASES object is empty. We need to report this to Sentry
            console.error('UPD_INC: The malwarebytes.DATABASES object is empty');
            this.notifyHook('onUpdateError');
            throw new Error(`UPD_INC: ${MISSING_DATABASE_ERROR}. The malwarebytes.DATABASES object is empty`);
        }

        console.debug("UPD_INC: Installed packages Initial", installedPackages);
        installedPackages = this.cleanPackages(installedPackages);
        console.debug("UPD_INC: Version mapping part finished");
        console.debug("UPD_INC: Will check updates for", installedPackages.map((x) => x.name));
        const uuid = await getUUID();
        console.debug("UPD_INC: GetUUID part finished");
        const browserInfo = await getBrowserInformation();
        const payload = {
            product: "mbgc-c",
            build: "consumer",
            semver: chrome.runtime.getManifest().version,
            os_version: browserInfo.name + " " + browserInfo.version,
            installation_token: browserName().toLowerCase() + "-" + uuid,
            installed_packages: installedPackages
        };
        let response;
        try {
            response = await fetchJSON(
                SIRIUS_URL,
                {
                    method: "POST",
                    headers: {
                        "Authorization": `Token token="${SIRIUS_TOKEN}"`,
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify(payload)
                }
            );
            console.debug("UPD_INC: FetchSirius finished");
            if (response.status !== "ok") {
                console.error("Negative response from Sirius: ", JSON.stringify(response));
                throw new Error(response.status);
            }
        } catch (error) {
            console.error("Error fetching Sirius: ", error);
            this.notifyHook('onUpdateError');
            return []
        }
        // parse response from Sirius
        const siriusPackages = parsePackages(response.manifest.packages);
        const packagesToUpdate = siriusPackages.filter((pkg) => {
            if (!pkg.version) {
                return false;
            }
            const siriusVersion = new SemVer(pkg.version);
            const localVersion = new SemVer(installedPackages.find((x) => x.name == pkg.name).semver);
            return siriusVersion.greater(localVersion);
        }).map((pk) => ({ ...pk, localVersion: installedPackages.find((x) => x.name == pk.name).semver }));

        console.debug(`UPD_INC:
          ${packagesToUpdate.length}/${siriusPackages.length} fetched packages need to be updated ` +
        `(${siriusPackages.length - packagesToUpdate.length} up-to-date)`);
        console.debug(`UPD_INC: Packages to update `, packagesToUpdate);
        if (packagesToUpdate.length > 0) {
            this.notifyHook('onUpdateStart');
        }
        try {
            if (
                clonedDbVers.some((db) =>
                    packagesToUpdate.some(pkg =>
                        pkg.name === db.name &&
                    Number(pkg.version.replaceAll('.', '')) <= Number(db.version.replaceAll('.', ''))
                    )
                )
            ) {
                console.log('UPD_INC: caught downloading wrong package')
                Sentry.captureEvent({
                    message: `Caught downloading wrong package`,
                    logentry: {
                        message: `Caught downloading wrong package`,
                        params: [clonedDbVers, packagesToUpdate]
                    },
                    level: 'log',
                    tags: {
                        'custom': true,
                        'bg-source': 'updater.caught_wrong_package',
                        version: chrome.runtime.getManifest().version,
                    },
                });
            }
        } catch (e) {
            console.log('UPD_INC: failed to check version info')
        }

        let [applyIncremetals, fullPackages] = determineDownloadType(packagesToUpdate);
        const promiseResults = await Promise.allSettled(
            applyIncremetals.map((pk) => this.downloadAndApplyIncremental(pk, databases))
        );
        let badPackages = [];
        promiseResults.forEach((promiseResult, i) => {
            const pkg = applyIncremetals[i];
            if (promiseResult.status == 'rejected') {
                console.warn(`UPD_INC: Error downloading or applying incremental for ${pkg.name}`,
                    promiseResult.reason, 'falling back to the full package');
                fullPackages.push(pkg);
            }
        });
        console.debug(`UPD_INC: ${fullPackages.length} full packages will be downloaded`);

        for (const pk of fullPackages) {
            try {
                await Updater.download(pk);
            } catch (err) {
                console.error(`UPD_INC: Error downloading full package for ${pk.name}`, err);
                // Log the error to Sentry
                Sentry.captureException(err);
                badPackages.push(pk);
                continue;
            }
        }


        if (badPackages.length > 0) {
            throw new Error(BAD_PACKAGE_ERROR);
        }

        console.debug(`UPD_INC: ${fullPackages.length} full packages downloaded`, { fullPackages });
        if (fullPackages.length > 0) {
            try {
                await reloadFullPackages(fullPackages, databases);
            } catch (err) {
                console.error(`UPD_INC: Error reloading full packages`, err);
                throw err;
            }
        }
        this.notifyHook('onDownloadComplete');
        return packagesToUpdate.map((pk) => databases[cleanDbName(pk.name)]);
    }

    async downloadAndApplyIncremental(packageInfo, databases) {
        const cleanName = cleanDbName(packageInfo.name);
        const currentDatabase = databases[cleanName];
        //Donwload
        const downloadResult = await Promise.all(
            packageInfo.incrementals.map((inc) => queuedFetchBinary(inc.url))
        );
        //Integrity verification
        downloadResult.forEach((bin, i) => {
            const hash = Updater.hashSHA256(bin);
            if (hash !== packageInfo.incrementals[i].sha256) {
                throw new Error("Integrity verification");
            }
        });

        const incrementals = downloadResult.map((bin) => unpack(bin));
        //First incremental should be based on the current version
        //When changing env it's expected to throw this error b/c the bundled packages are always pulled from prod
        const firstIncremental = incrementals[0];
        if (firstIncremental.from != currentDatabase.version) {
            throw new Error(`First incremental version does not match current version, ` +
                `expected: ${currentDatabase.version}, found: ${firstIncremental.from}`);
        }
        const finalIncremental = mergeIncrementals(incrementals);
        const newDb = applyIncremental(finalIncremental, currentDatabase);
        //Update version
        const fromVersion = currentDatabase.version;
        databases[currentDatabase.cleanName].version = packageInfo.version;
        //Store the generated package, use the version in packageInfo
        const dbBinary = objToBinary(newDb);
        console.log(`UPD: Database ${currentDatabase.name} successfully migrated ` +
            `from ${fromVersion} to ${packageInfo.version}`);
        return saveDownloadedDb(packageInfo.name, packageInfo.version, dbBinary);
    }

    notifyHook(name) {
        if (this.hooks && Object.hasOwn(this.hooks, name)) {
            this.hooks[name]();
        }
    }

    static download(siriusPackage) {
        console.log("UPD: Downloading full package for", {siriusPackage});
        return queuedFetchBinary(siriusPackage.url).then((contents) => {
            // verify database integrity
            let hash = Updater.hashSHA256(contents);
            if (hash !== siriusPackage.sha256) {
                throw new Error("integrity verification");
            }
            // write database to disk
            return saveDownloadedDb(siriusPackage.name, siriusPackage.version, contents);
        });
    }
}

const reloadFullPackages = async (packages, databases) => {
    const dbNames = packages.map((pk) => pk.name);
    const newDatabases = await loadIdbStorageDatabases(dbNames);
    newDatabases.forEach((db) => {
        databases[db.cleanName] = db;
    });
};

export const unpack = (binaryData) => {
    return JSON.parse(binaryToString(fzstd.decompress(new Uint8Array(binaryData))));
};

export const applyIncremental = (incremental, currentDb) => {
    let newDb;
    if (currentDb.isBloom) {
        const currentBloomFilter = currentDb.data.getBloomFilterPlainObject();
        const sourceArray = currentBloomFilter.data;
        const targetArray = new Uint32Array(incremental.length);
        for (let i = 0; i < targetArray.length; i++) {
            if (i.toString() in incremental.changes) {
                targetArray[i] = parseInt(incremental.changes[i.toString()]);
            } else {
                targetArray[i] = sourceArray[i];
            }
        }
        newDb = {
            k: incremental.k,
            m: incremental.m,
            data: targetArray
        };
        currentDb.data.replaceBloomFilter(newDb);
        newDb.data = binaryToBase64(newDb.data.buffer); //Need to convert to base64 for packaging
    } else if (currentDb.isRegex) {
        const sourceArray = currentDb.rawData;
        const targetArray = new Array(incremental.length);
        for (let i = 0; i < targetArray.length; i++) {
            if (i.toString() in incremental.changes) {
                targetArray[i] = incremental.changes[i.toString()];
            } else {
                targetArray[i] = sourceArray[i];
            }
        }
        //Replace current db
        currentDb.rawData = targetArray;
        currentDb.data = targetArray.map((pattern) => new RegExp(pattern, "i"));
        newDb = targetArray;
    } else if (currentDb.isJson) {
        const sourceArray = currentDb.rawData;
        const targetArray = new Array(incremental.length);
        for (let i = 0; i < targetArray.length; i++) {
            if (i.toString() in incremental.changes) {
                targetArray[i] = incremental.changes[i.toString()];
            } else {
                targetArray[i] = sourceArray[i];
            }

            let r = targetArray[i]?.r;
            if (!r && typeof targetArray[i] == 'string') {
                let strRegex = targetArray[i];
                let objRegex = { s: false, r: new RegExp(strRegex) };
                targetArray[i] = objRegex;
            }
            if (r && typeof r == 'string') {
                let strRegex = r;
                // replace quadruple backslashes with double backslashes
                if (strRegex.startsWith('/') && strRegex.endsWith('/')) {
                    strRegex = strRegex.slice(1, -1);
                    strRegex = strRegex.replace(/\\\\/g, '\\');
                }
                targetArray[i].r = new RegExp(strRegex);
            }
        }
        currentDb.rawData = targetArray;
        newDb = targetArray;
    } else {
        const sourceArray = currentDb.rawData || currentDb.data;
        const targetArray = new Array(incremental.length);
        for (let i = 0; i < targetArray.length; i++) {
            if (i.toString() in incremental.changes) {
                targetArray[i] = incremental.changes[i.toString()];
            } else {
                targetArray[i] = sourceArray[i];
            }
        }
        if (currentDb.data instanceof Set) {
            currentDb.data = new Set(targetArray);
            currentDb.rawData = targetArray;
        } else {
            currentDb.data = targetArray;
        }
        newDb = targetArray;
    }
    return newDb;
};

export const mergeIncrementals = (incrementals) => {
    let result = {};
    let changes = {};
    for (let i = 0; i < incrementals.length; i++) {
        if (!incrementals[i].isIncremental) {
            throw new Error('Object is not incremental');
        }
        result = { ...result, ...incrementals[i] };
        changes = { ...changes, ...incrementals[i].changes };
    }
    result.changes = changes;
    return result;
};

export const determineDownloadType = (packagesToUpdate) => {
    const applyIncremetals = [];
    const fullPackages = [];
    packagesToUpdate.forEach((pkg) => {

        if (!pkg.incrementals || pkg.incrementals.length == 0) {
            //No incrementals
            console.debug(`UPD: No incrementals found for ${pkg.name}. The full package will be downloaded`);
            fullPackages.push(pkg);
            return;
        }

        const lastIncremental = pkg.incrementals[pkg.incrementals.length - 1];
        if (pkg.version != lastIncremental.version) {
            //Last incremental version does not match latest package
            console.debug(`UPD: Last incremental version does not match latest full version ${pkg.name}, ` +
                `expected version: ${pkg.version}, found:
                ${lastIncremental.version}. The full package will be downloaded`);
            fullPackages.push(pkg);
            return;
        }

        const totalSizeIncrementals = pkg.incrementals.reduce((p, n) => p + n.size, 0);
        if (totalSizeIncrementals >= pkg.size) {
            //Download all the incrementals will consume more bandwidth than downloading the ful package
            console.debug(`UPD: Incremental sizes exceed the full packag size ${pkg.name}, ` +
                `full package size: ${pkg.size}, incremental sizes: ${totalSizeIncrementals}. ` +
                `The full package will be downloaded`);
            fullPackages.push(pkg);
            return;
        }
        applyIncremetals.push(pkg);
    });
    return [applyIncremetals, fullPackages];
};

/**
 * @typedef {Object} SiriusPackage
 * @property {string} name
 * @property {string} version
 * @property {string} md5
 * @property {string} sha256
 * @property {string} size
 * @property {string} url
 */

export const parsePackages = (packages = []) =>
    /* eslint-disable camelcase */
    packages.map(({ name, available_packages = [], available_incrementals = [] }) => ({
        name,
        version: available_packages[0] && available_packages[0].semver,
        md5: available_packages[0] && available_packages[0].md5,
        sha256: available_packages[0] && available_packages[0].sha256,
        size: available_packages[0] && available_packages[0].file_hash && available_packages[0].file_hash.file_size,
        url: (available_packages[0] && available_packages[0].file_hash && "https://" + available_packages[0].file_hash.url),
        incrementals: available_incrementals.map((inc) => ({
            version: inc.semver,
            md5: inc.md5,
            sha256: inc.sha256,
            size: inc.file_hash && inc.file_hash.file_size,
            url: (inc.file_hash && "https://" + inc.file_hash.url)
        }))
    }));
/* eslint-enable camelcase */
