
export class SemVer {
    constructor(str) {
        this.major = 0;
        this.minor = 0;
        this.patch = 0;
        var elements = str.split(".");
        if (elements.length === 3) {
            var major = parseInt(elements[0]);
            var minor = parseInt(elements[1]);
            var patch = parseInt(elements[2]);
            if (!isNaN(major) && !isNaN(minor) && !isNaN(patch)) {
                this.major = major;
                this.minor = minor;
                this.patch = patch;
            }
        }
    }

    greater(that) {
        if (this.major > that.major) {
            return true;
        }
        if (this.major < that.major) {
            return false;
        }
        if (this.minor > that.minor) {
            return true;
        }
        if (this.minor < that.minor) {
            return false;
        }
        if (this.patch > that.patch) {
            return true;
        }
        if (this.patch < that.patch) {
            return false;
        }
        return false;
    }
}

