import DetectionHistory from "./detection-history-common";
import {chrome} from "../../../utils/polyfill.ts";

export class Mv2DetectionHistory extends DetectionHistory {
    constructor() {
        super();
    }

    async addItem(entry) {
        console.debug('DH: Adding item to storage', entry);
        try {
            await this._saveItemToStorage(entry);
            console.debug('DH: Added item to storage', {...entry});
        } catch (err) {
            console.error('DH: Error adding item to storage', err);
            throw err;
        }
    }

    async deleteItem(site, rowId) {
        const currentHistory = await this.loadSavedItems();
        const postState = currentHistory.filter((item)=>item.site !== site && item.date !== rowId);
        return new Promise((resolve, reject) => {
            chrome.storage.local.set({detectionHistory: postState}, () => {
                if (chrome.runtime.lastError) {
                    console.error('Error deleting the history to storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve(true);
                }
            });
        });
    }

    async deleteAllItems() {
        return new Promise((resolve, reject) => {
            chrome.storage.local.set({detectionHistory: []}, () => {
                if (chrome.runtime.lastError) {
                    console.error('Error deleting the history to storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve(true);
                }
            });
        });
    }

    async getAll() {
        return await this.loadSavedItems();
    }

    async loadSavedItems() {
        const entries = await this._itemsFromStorage();
        if (!entries) {
            return [];
        }
        return entries;
    }

    async _itemsFromStorage() {
        return new Promise((resolve, reject) => {
            chrome.storage.local.get('detectionHistory', (result) => {
                if (chrome.runtime.lastError) {
                    console.error('Error getting detection history from storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve(result.detectionHistory);
                }
            });
        });
    }

    async _saveItemToStorage(item) {
        const prevState = await this._itemsFromStorage() || [];
        return new Promise((resolve, reject) => {
            chrome.storage.local.set({detectionHistory: [item, ...prevState]}, () => {
                if (chrome.runtime.lastError) {
                    console.error('Error saving detection history item to storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve();
                }
            });
        });
    }
}
