import { simpleStorageGet, simpleStorageSet } from "@/utils/storage.ts";
import { chrome } from "../../../utils/polyfill.ts";
import {
    MV3_PROTECTION_ADS,
    MV3_PROTECTION_MALWARE,
    MV3_PROTECTION_SCAMS
} from "../app-consts";
import { TARGET_BROWSER } from "../browser-const.js";
import * as Sentry from "@sentry/browser";

export const defaultRulesetPatterns = [
    'mbgc.mv3.whitelist',
    'mbgc.mv3.ads',
    'mbgc.mv3.malware',
    'mbgc.mv3.easylist',
    'mbgc.mv3.easyprivacy'
];

export const tryInitMV3Rulesets = async () => {
    const allRulesets = await getAllAvailableStaticRulesets();
    const enabledRulesets = await chrome.declarativeNetRequest.getEnabledRulesets();
    let notEnabledRulesets = allRulesets.filter((rs) => !enabledRulesets.includes(rs));
    console.debug('TIRM: Will try to enable rulesets: ', { notEnabledRulesets });
    if (notEnabledRulesets.length > 0) {
        const { failedRulesets, enabledRulesets } = await tryUpdateEnabledRulesets(notEnabledRulesets);
        if (failedRulesets.length > 0) {
            const failedRulesetIDs = failedRulesets.map((r) => r.rulesetID);
            const isBrowserError  = failedRulesets.some((r) => r.isBrowserError === true);
            console.error('TIRM: Failed to enable all rulesets: ', { failedRulesetIDs, enabledRulesets });
            showRulesetErrorPage(failedRulesetIDs, isBrowserError);
            return;
        } else {
            // all enabled
            await simpleStorageSet({ mv3RulesetSelection: allRulesets });
        }
    }
}

export const tryInitMV3RulesetsWithSelectedIds = async (rulesetIds) => {
    const {failedRulesets, enabledRulesets} = await tryUpdateEnabledRulesets(rulesetIds);
    if (failedRulesets.length > 0) {
        console.error('TIMRWSI: Failed init selected rulesets: ', { failedRulesets, enabledRulesets });
    }

    await simpleStorageSet({ mv3RulesetSelection: enabledRulesets });
    return { failedRulesets, enabledRulesets };
}

export const checkForAdditionalRuleSpace = async () => {
    console.debug('CR: Checking for additional rule space');
    const enabledRulesets = await chrome.declarativeNetRequest.getEnabledRulesets() || [];
    
    const allRulesets = await getAllAvailableStaticRulesets();
    console.log('CR: Enabled rulesets: ', JSON.stringify(enabledRulesets));
    let disabledRulesets = allRulesets.filter((rs) => !enabledRulesets.includes(rs)) || [];
    console.log('CR: Disabled rulesets: ', JSON.stringify(disabledRulesets));
    if (disabledRulesets.length > 0) {
        Sentry.captureEvent({
            message: 'Not all rulesets are enabled.',
            logentry: {
                message: `Not all rulesets are enabled. Disabled rulesets: ${JSON.stringify(disabledRulesets)}`,
                params: [disabledRulesets]
            },
            level: 'error',            
            tags: {
                'custom': true,
                'bg-source': 'ruleset-utils.checkForAdditionalRuleSpace',
                version: chrome.runtime.getManifest().version,
            }
        });
    }

    console.debug('CR: User Ruleset selection: ', { enabledRulesets, allRulesets, disabledRulesets });

    // try to enable the disabled rulesets
    await tryUpdateEnabledRulesets(disabledRulesets);
};

const tryUpdateEnabledRulesets = async (rulesetIDs) => {
    if (rulesetIDs.length === 0) {
        console.debug('TUER: No rulesets to enable');
        return;
    }

    let availableRuleCount = await chrome.declarativeNetRequest.getAvailableStaticRuleCount();
    console.debug('TUER: Available rule count: ', availableRuleCount);
    const enabledRulesets = [];
    let failedRulesets = [];

    for (const rulesetID of rulesetIDs) {
        try {
            await chrome.declarativeNetRequest.updateEnabledRulesets({
                enableRulesetIds: [rulesetID]
            });
            enabledRulesets.push(rulesetID);
            console.debug('TUER: Enabled ruleset ', rulesetID);
            
        } catch (err) {
            console.error(
                'TUER: Error enabling ruleset ',
                rulesetID, chrome.runtime.lastError, err, err.message
            );
            failedRulesets.push({
                rulesetID,
                isBrowserError:
                err.message.toLowerCase().includes('internal error')
            });
            Sentry.captureEvent({
                message: `Error enabling ruleset ${rulesetID}: ${err.message}`,
                logentry: {
                    message: `Error enabling ruleset ${rulesetID}: ${err.message}`,
                    params: [err.message, `Ruleset ID: ${rulesetID}`, `Available rule count: ${availableRuleCount}`]
                },
                level: 'error',
                tags: {
                    'custom': true,
                    'bg-source': 'ruleset-utils.tryUpdateEnabledRulesets',
                    version: chrome.runtime.getManifest().version,
                }
            });
        }
    }

    return { enabledRulesets, failedRulesets };
};

export const getRuleStats = async () => {
    const dynamicRuleCount = (await chrome.declarativeNetRequest.getDynamicRules() || []).length;
    const availableStaticRuleCount = (await chrome.declarativeNetRequest.getAvailableStaticRuleCount() || 0);
    const staticRuleCount = 330000 - availableStaticRuleCount;
    const enabledRulesets = await chrome.declarativeNetRequest.getEnabledRulesets();
    const staticRulesetCount = enabledRulesets.length;
    return { enabledRulesets, dynamicRuleCount, staticRulesetCount, staticRuleCount, availableStaticRuleCount };
};

export const toggleEnabledRuleset = async (protection, enable) => {
    const affected = rulesetsForProtectionConst(protection);
    if (enable === false) {
        await chrome.declarativeNetRequest.updateEnabledRulesets({
            disableRulesetIds: enable ? [] : affected,
        });
        return;
    }

    await tryUpdateEnabledRulesets(affected);
};

export const toggleAllEnabledRulesets = async (enable) => {
    const enabledRulesets = await chrome.declarativeNetRequest.getEnabledRulesets() || [];
    console.debug('TAER: Enabled rulesets: ', enabledRulesets);

    const allRulesets = await getAllAvailableStaticRulesets() || [];
    const notCurrentlyEnabled = allRulesets.filter((rs) => !enabledRulesets.includes(rs));
    console.debug('TAER: Not currently enabled: ', notCurrentlyEnabled);
    if (enable === false) {
        await chrome.declarativeNetRequest.updateEnabledRulesets({
            disableRulesetIds: enable ? [] : enabledRulesets,
        });
        return;
    }

    await tryUpdateEnabledRulesets(notCurrentlyEnabled);
};

function setDifference(setA, setB) {
    const difference = new Set();
    for (const item of setA) {
        if (!setB.has(item)) {
            difference.add(item);
        }
    }
    return difference;
}

// rulesToRemove is an array of objects with the following structure:
// { ruleset: string, ruleId: number }
export const disableStaticRules = async (rulesToRemove) => {
    const existingDisabledRules = await simpleStorageGet('mv3DisabledRules') || [];

    const existingRulesetGroups = {};
    for (const rule of existingDisabledRules) {
        if (!existingRulesetGroups[rule.ruleset]) {
            existingRulesetGroups[rule.ruleset] = [];
        }
        existingRulesetGroups[rule.ruleset].push(rule.ruleId);
    }

    // group by ruleset
    const rulesetGroups = {};
    for (const rule of rulesToRemove) {
        if (!rulesetGroups[rule.ruleset]) {
            rulesetGroups[rule.ruleset] = [];
        }
        rulesetGroups[rule.ruleset].push(rule.ruleId);
    }

    for (const ruleset in rulesetGroups) {
        const existingRuleIdSet = new Set(existingRulesetGroups[ruleset] || []);
        const ruleIdSet = new Set(rulesetGroups[ruleset]);
        const existingDisabledIdsToEnable = Array.from(setDifference(existingRuleIdSet, ruleIdSet));

        const ruleIds = Array.from(ruleIdSet);
        try {
            await chrome.declarativeNetRequest.updateStaticRules({
                disableRuleIds: ruleIds,
                enableRuleIds: existingDisabledIdsToEnable,
                rulesetId: ruleset
            });
            console.debug('DSR: Disabled rules: ', { ruleset, ruleIds, existingDisabledIdsToEnable });
        } catch (err) {
            const runtimeError = chrome.runtime.lastError;
            console.error('DSR: Error disabling rules: ', { ruleset, ruleIds, err, runtimeError });
        }
    }

    await simpleStorageSet({ 'mv3DisabledRules': rulesToRemove });
    
}

const showRulesetErrorPage = async (disabledRulesets, isBrowserError = false) => {
    let protocol = TARGET_BROWSER === 'Firefox' ? 'moz-extension://' : 'chrome-extension://';
    // url encode the disabled rulesets
    const disabledRulesetsParamString = disabledRulesets.map((rs) => rs.replace('mbgc.mv3.', '')).join(',');
    const url = protocol + chrome.i18n.getMessage('@@extension_id') +
        `/app/eventpages/ruleset-error.html?disabledRulesets=${disabledRulesetsParamString}
        &isBrowserError=${isBrowserError}`;
    console.debug('RULESET: Ruleset error page url: ', url);
    if (!await isRulesetErrorPageOpen()) {
        chrome.tabs.create({ url, active: true }, (tab) => {
            console.debug('RULESET: Ruleset error page created', tab);
        });
    }
};

const getAllAvailableStaticRulesets = () => {
    const manifest = chrome.runtime.getManifest();
    const allRulesets = manifest.declarative_net_request.rule_resources.map((rs) => rs.id);
    console.debug('GASR: All rulesets: ', allRulesets);
    return allRulesets;
};
const rulesetsForProtectionConst = (protectionConst) => {
    let rulesetPatterns = null;
    switch (protectionConst) {
        case MV3_PROTECTION_ADS:
            rulesetPatterns = ['mbgc.mv3.ads', 'mbgc.mv3.easylist', 'mbgc.mv3.easyprivacy', 'mbgc.arw'];
            break;
        case MV3_PROTECTION_MALWARE:
            rulesetPatterns = ['mbgc.mv3.malware'];
            break;
        case MV3_PROTECTION_SCAMS:
            rulesetPatterns = [];
            break;
        default:
            rulesetPatterns = [];
            break;
    }

    const allRulesets = getAllAvailableStaticRulesets();
    const affected = [];
    for (const ruleset of allRulesets) {
        rulesetPatterns.find((rp) => ruleset.startsWith(rp)) && affected.push(ruleset);
    }

    console.debug('RFPC: Affected rulesets: ', { rulesetPatterns, protectionConst, affected });
    return affected;
}

const isRulesetErrorPageOpen = async () => {
    const tabs = await chrome.tabs.query({ currentWindow: true });
    for (const tab of tabs) {
        if (tab.url.includes('/ruleset-error.html')) {
            return true;
        }
    }
    return false;
};
