import ContentControls from "./content-control-common";
import {chrome} from "../../../utils/polyfill.ts";

export class Mv2ContentControls extends ContentControls {
    constructor() {
        super();
    }

    async addItem(url, category) {
        console.debug('CCTRL: Adding item to cache', {url, category});
        if (!this.CACHE) {
            this.CACHE = new Map();
        }

        try {
            const categories = this.CACHE.has(url) ? new Set(this.CACHE.get(url)) : new Set();
            categories.add(category);
            this.CACHE.set(url, categories);
            console.debug('CCTRL: Added item to cache', {url, category, cache: this.CACHE});
            await this.saveItems();
        } catch (err) {
            console.error('CCTRL: Error adding item to cache', err);
            throw err;
        }
    }

    async deleteItem(url) {
        if (this.CACHE) {
            this.CACHE.delete(url);
            console.debug('CCTRL: Deleted item from cache', {url, cache: this.CACHE});
            await this.saveItems();
            return true;
        }
        return false;
    }

    async deleteAllItems() {
        if (this.CACHE) {
            this.CACHE.clear();
            await this.saveItems();
            return true;
        }
        return false;
    }

    existsForUrl(url) {
        const exists = this.CACHE.has(url);
        return exists;
    }

    // Map<string, Set<string>>
    async getAll() {
        await this.loadSavedItems(); // is this necessary?
        return this.CACHE;
    }

    async export() {
        try {
            console.debug('CCTRL: Loading items to export');
            const entries = await this._itemsFromStorage();
            console.debug('CCTRL: Loaded items to export', {entries});
            if (!entries) {
                return {};
            }

            return entries;
        } catch (err) {
            console.error('CCTRL: Failed to load saved items', {cache: this.CACHE, err});
            throw err;
        }
    }

    async import(data) {
        if (!this.CACHE) {
            this.CACHE = new Map();
        }

        try {
            console.debug('CCTRL: Importing items', {data});
            for (const url in data) {
                if (Object.hasOwn(data, url)) {
                    const categories = data[url];
                    this.CACHE.set(url, new Set(categories));
                }
            }
            console.debug('CCTRL: Imported items', {cache: this.CACHE});
            await this.saveItems();
        } catch (err) {
            console.error('CCTRL: Failed to import items', {cache: this.CACHE, err});
            throw err;
        }
    }

    getForUrl(url) {
        return this.CACHE.get(url);
    }

    async saveItems() {
        if (!this.CACHE) {
            console.debug('CCTRL: User blocks cache does not yet exist');
            return;
        }

        const entries = {};
        for (const [key, value] of this.CACHE.entries()) {
            entries[key] = Array.from(value);
        }
        console.debug('CCTRL: Saving items to storage', {cache: this.CACHE, entries});
        await this._saveItemsToStorage(entries);
    }

    async loadSavedItems() {
        if (!this.CACHE) {
            this.CACHE = new Map();
        }

        try {
            const entries = await this._itemsFromStorage();
            if (!entries) {
                return;
            }

            for (const url in entries) {
                if (Object.hasOwn(entries, url)) {
                    const categories = entries[url];
                    this.CACHE.set(url, new Set(categories));
                }
            }
        } catch (err) {
            console.error('CCTRL: Failed to load saved items', {cache: this.CACHE, err});
            throw err;
        }
    }

    async _itemsFromStorage() {
        return new Promise((resolve, reject) => {
            chrome.storage.local.get('contentControls', (result) => {
                if (chrome.runtime.lastError) {
                    console.error('Error getting content controls from storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve(result.contentControls);
                }
            });
        });
    }

    async _saveItemsToStorage(items) {
        return new Promise((resolve, reject) => {
            chrome.storage.local.set({contentControls: items}, () => {
                if (chrome.runtime.lastError) {
                    console.error('Error saving content controls to storage', chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                } else {
                    resolve();
                }
            });
        });
    }
}
