import {
    urlTLD,
    isValidIP,
    subdomain,
    sld,
    domainList,
    hasSubstringMatch,
    urlHost,
} from './utils';

import {ipLiteralRegex} from './patterns';

import {
    safePageExtRegex,
    semVerRegex,
    validRedirectUrls,
    cloudfrontUrls,
} from './patterns';

import {riskyTlds} from "../utils/downloads/downloadConsts";

const safeCloudfrontFilenames = [
    ["audio", "editor"],
    ["boost", "speed"],
    ["clean", "pc"],
    ["disk", "boost"],
    ["disk", "optimizer"],
    ["disk", "clean"],
    ["disk", "restore"],
    ["driver", "boost"],
    ["fast", "pc"],
    ["fast", "recorder"],
    ["fast", "screen"],
    ["pc", "boost"],
    ["pc", "clean"],
    ["pc", "optimizer"],
    ["pc", "restore"],
    ["pc", "wizard"],
    ["regcleaner"],
    ["registry", "boost"],
    ["registry", "wizard"],
    ["system", "care"],
    ["system", "mechanic"],
    ["system", "optimizer"],
    ["system", "restore"],
    ["system", "wizard"],
    ["tuneup"],
    ["winoptimizer"],
];

const isSafeFilename = (filename = "", pageExt, url) =>
    // BG-877 & BG-927 #1 + #3
    ["install", "installer", "setup", "portable", "converter", "webcam", "recovery"].some(
        (safeIndicator) => filename.includes(safeIndicator)
    ) || // BG-1035 #1
    (pageExt === "exe" &&
        ["pdftojpg", "pdftoword", "pdftoexcel"].some((f) => f === filename)) || // BG-1035 #2
    (cloudfrontUrls.test(url) &&
        safeCloudfrontFilenames.some((filenames) =>
            filenames.every((f) => filename.includes(f))
        )) || //BG-1505 #3
    (semVerRegex.test(filename));

const isFileDomainMatch = (host, filename, pathParts, urlSld) =>
    domainList(host).some((domain) =>
        hasSubstringMatch(domain ? domain.toLowerCase() : "", filename, 4)
    ) ||
    // BG-904 #1
    pathParts.some((part) => {
        if (!part) {
            return false;
        }
        part = part.toLowerCase();
        return (
            hasSubstringMatch(part, filename, 4) ||
            hasSubstringMatch(part, urlSld, 4)
        ); // BG-927 #2 & BG-980 #2
    });

const safeDomainCheck = (urlSld) =>
    [
        "backup",
        "converter",
        "disk",
        "driver",
        "editor",
        "software",
        "speed",
        "system",
        "tools",
        "vpn",
        "webcam",
    ].some((w) => urlSld.includes(w)); // BG-904 #4 & BG-980 #1 & #7

const winArchs = ["win32", "win64", "msi64", "msi86"]; // BG-904 #6
const archs = ["64bit", "32bit"]; //BG-1505 # 4
const safeFilenamePath = (filename, pathParts) => {
    return (winArchs.some((arch) => filename.includes(arch)) &&
        pathParts.some((part) => winArchs.some((arch) => part.includes(arch)))) ||
    pathParts.some((part) => archs.some((arch) => arch === part));
};

// BG-904 #7
const isSldDlMatch = (host, urlSld, referrerSLD) => {
    if (urlSld === referrerSLD) {
        const hostSubdomain = subdomain(host);
        return ["download", "cdn"].some((keyword) => keyword === hostSubdomain);
    }
    return false;
};

const safeReferralCheck = (urlObj, pathParts, filename, referrer, urlSld) => {
    if (!referrer) {
        return false;
    }
    let refUrlObj;
    try {
        refUrlObj = new URL(referrer);
    } catch (e) {
        console.debug(`SRC: Failed to parse url ${referrer}: `, e);
        return false;
    }

    const referrerSLD = sld(refUrlObj.hostname);
    if (isSldDlMatch(urlObj.hostname, urlSld, referrerSLD)) {
        return true;
    }

    // BG-904 #5
    if (
        isValidRedirect(urlObj.href) ||
        (urlObj.hostname && urlObj.hostname.toLowerCase() === "github.com")
    ) {
        return (
            pathParts.some((part) =>
                hasSubstringMatch(
                    part ? part.toLowerCase() : "",
                    referrerSLD,
                    5
                )
            ) || hasSubstringMatch(filename, referrerSLD)
        );
    }
};

const paintPatterns = [
    "paintnet",
    "paint.net",
    "paint_net",
    "paint-net",
    "paintdotnet",
    "paint net",
    "paint dot net",
]; // BG-1405 paint.net FP prevention
const paintNet = (urlObj, pageExt) => {
    const lowerHref = urlObj.href ? urlObj.href.toLowerCase() : '';
    const lowerSearch = urlObj.search ? urlObj.search.toLowerCase() : '';
    return (
        ["exe", "zip", "7z"].some((ext) => ext === pageExt) &&
        urlObj.protocol === 'https:' &&
        paintPatterns.some((paint) => lowerHref.includes(paint) || lowerSearch.includes(paint))
    );
};

export const isValidRedirect = (url) =>
    url && validRedirectUrls.some((aws) => url.match(aws));

export const isWhitelistedScamsByIpLiteral = (domain, localIpWhitelisting) => {
    // whitelist internal and non-public IPv4 literals
    let match = domain.match(ipLiteralRegex);
    if (match && localIpWhitelisting) {
        if (
            match[1] == 0 ||
            match[1] == 10 ||
            match[1] == 127 ||
            (match[1] == 169 && match[2] == 254) ||
            (match[1] == 172 && match[2] >= 16 && match[2] <= 31) ||
            (match[1] == 192 && match[2] == 168)
        ) {
            console.debug(`IWL: Scams wl ip literal matched. ${domain} is allowed >>> Matches: `, match);
            return true;
        }
    }
    return false;
};


// BG-780 & BG-782 & BG-904
export const safePatternFilename = (url, pageExt, referrer) => {
    const tld = urlTLD(url);


    let urlObj;
    try {
        urlObj = new URL(
            url.toLowerCase().startsWith("blob:") ? url.substring(5) : url
        );
    } catch (e) {
        console.debug("SPF: Failed to parse url: ", e);
        return false;
    }

    // PSL/tldts interprets some TLDs by Service for example fdsjl.cloudfront.net -> cloudfront.net -- Thus, the second condition
    if (riskyTlds.some((riskyTld) => riskyTld === tld || tld.endsWith(`.${riskyTld}`))) {
        //BG-1505 #1
        console.debug("SPF: No check for risky tld", tld);
        return null;
    }

    if (isValidIP(urlObj.hostname)) {
        return null;
    }

    // BG-904 #9, BG-1505 #2
    const sub = subdomain(urlObj.hostname);
    if (sub === "download" || sub === 'dl') {
        return true;
    }

    if (
        !["dmg", "exe", "msi", "zip", "7z"].some((ext) => ext === pageExt)
    ) {
        return null;
    }

    let splitPath = urlObj.pathname.split("/");
    let filename = splitPath.slice(-1)[0];

    if (filename) {
        const matchy = filename.match(safePageExtRegex);
        if (matchy) {
            // should always be true, but safety first
            filename = filename.slice(0, matchy.index); // chop off file ext
        }
        filename = filename.toLowerCase();

        const pathParts = splitPath
            .slice(0, -1)
            .map((p) => p.toLowerCase());
        const urlSld = sld(urlObj.hostname).toLowerCase();

        return (
            isSafeFilename(filename, pageExt, url) ||
            isFileDomainMatch(
                urlObj.hostname,
                filename,
                pathParts,
                urlSld
            ) ||
            ["x32", "x64", "x86", "i386"].some((arch) =>
                filename.includes(arch)
            ) || // BG-904 #3 & BG-927 #9
            safeDomainCheck(urlSld) ||
            safeFilenamePath(filename, pathParts) ||
            filename.match(semVerRegex) || // BG-1480
            pathParts.some((part) => semVerRegex.test(part)) || // BG-904 #8
            safeReferralCheck(urlObj, pathParts, filename, referrer, urlSld) ||
            paintNet(urlObj, pageExt)
        );
    }

    return false;
};

export const isSafeReferral = (url, referrer, initiatorIsWhitelisted) => {
    if (!initiatorIsWhitelisted) {
        return false;
    }

    let urlSLD;
    let referrerSLD;
    try {
        [urlSLD, referrerSLD] = [url, referrer].map((u) => sld(urlHost(u)));
    } catch (e) {
        console.debug("ISR: Failed to parse url: ", e);
        return false;
    }

    return urlSLD === referrerSLD || referrerSLD === "softpedia";
};
