import {chrome} from "../../../utils/polyfill.ts";
import {Mv3UserBlocksHandler} from "./user-blocks-mv3";
import {Mv2UserBlocksHandler} from "./user-blocks-mv2.js";

const isMV3 = chrome.runtime.getManifest().manifest_version === 3;
export const userBlocksHandler = new Mv3UserBlocksHandler();
const mv2UserBlocksHandler = new Mv2UserBlocksHandler();

export async function migrateUserBlocksToMV3() {
    console.debug("MV3_UserBlocks_Migrate: Migrating to MV3");
    if (!isMV3) {
        return;
    }

    try {
        const userBlocks = await mv2UserBlocksHandler.getBlockedItems();
        console.debug("MV3_UserBlocks_Migrate: Migrating user blocks", userBlocks);
        await userBlocksHandler.blockItems(userBlocks);
        console.debug("MV3_UserBlocks_Migrate: Migrated user blocks", userBlocks);
    } catch (error) {
        console.error("MV3_UserBlocks_Migrate: Error migrating user blocks", error);
    }
}
