settings.addSetToStorageHook(async (added) => {
    if (added && Object.hasOwn(added, SETTING_NATIVE_MESSAGING)) {
        if (!added[SETTING_NATIVE_MESSAGING]) {
            malwarebytes.disconnectNative();
        }
    }

    if (typeof added.enableProtection !== "undefined") {
        console.debug("SS: Cached the enableProtection value which was added to settings");
        malwarebytes.postNativeTogglesState();
    }

    malwarebytes.reloadApp();
});


