/**
 * Maps data from all tabs when the background scripts start,
 * otherwise we only have data from tabs created after we started
 */
malwarebytes.init = async () => {
    try {
        await initLocalIpWhitelisting();
        await initDetectReputation();

        chrome.runtime.connectNative && malwarebytes.connectNative();
        // load user defined blocked items, no need to wait for this. Could slow down startup
        await userBlocksHandler.loadSavedBlockedItems();
        await contentControls.loadSavedItems();
    } catch (err) {
        console.error('INI: Failed to init due to error. ', {err});
    }
    //NOTE: When the script loads, we getExclusions into cache before trying to do any blocking
    exclusionHandler.getExclusions().then((_ignored) => {
        malwarebytes.postNativeTogglesState();
        return Promise.resolve();
    }).then(() => {
        return malwarebytes.initDatabases();
    }).then(() => {
        malwarebytes.READY_TO_ACCEPT_REQUESTS = true;
        return malwarebytes.getOrCreateUUID(); // force creation of UUID if necessary
    }).then((uuid) => {
        postBrowserToNative(uuid);

        if (TARGET_BROWSER === 'Firefox') {
            settings.getFromStorage("sendTelemetry").then((_value) => {
                settings.setToStorage({sendTelemetry: false});
            });
        }
        // Get IDB Log storage setup
        malwarebytes.flushLogs();

        return malwarebytes.hasFinishedOnboarding().then((_isFinished) => {
            // Default Protection on
            return new Promise((resolve) => {
                if (TARGET_BROWSER === 'Firefox') {
                    return resolve(true);
                }
                chrome.storage.local.get("enableProtection", (retrievedSettings) => {
                    if (chrome.runtime.lastError) {
                        console.error("HFO: Error checking true protection value: ", chrome.runtime.lastError);
                        // DEFAULT ON
                        return resolve(true);
                    } else {
                        if (typeof (retrievedSettings.enableProtection) === "undefined") {
                            console.debug("HFO: Switching Protection On");
                            // DEFAULT ON & SAVE
                            return settings.setToStorage({ enableProtection: true }).then(() => resolve(true));
                        } else {
                            console.debug("HFO: Restoring Protection value");
                            // USE TRUE VALUE
                            return resolve(retrievedSettings.enableProtection);
                        }
                    }
                });
            }).then((valueToUse) => {
                console.log(`HFO: Protection layers are ${valueToUse ? '' : 'not'} active`);
                settings.set('enableProtection', valueToUse);
                return Promise.resolve(valueToUse);
            });
        });
    }).then((_ignored) => {
        malwarebytes.sendClientTelemetry();
        return Promise.resolve();
    }).then((_ignored) => {
        //NOTE: Find tabs that were active before the extension was & block if needed
        chrome.tabs.query({}, function (tabs) {
            for (const element of tabs) {
                malwarebytes.map(element.id, element.url || element.pendingUrl);
            }
            malwarebytes.iconRefreshAll();
        });
        return Promise.resolve(true);
    }).then((_ignored) => {
        //FINALLY SHOW THE WELCOME SCREEN - we need to know that we only show it once -> ie this is first install,
        //so we need the install listener to finish before us. We have no GUARANTEE, BUT it SHOULD finish a long long
        //time before this. We also  need to know which group -> ie what screen to show
        if (malwarebytes.SHOULD_SHOW_WELCOME) {
            if (TARGET_BROWSER === "Firefox") {
                //Firefox forces us to check again, because even though this is flagged as a new install,
                //Firefox caches some stuff from old installs, so we may not need to enter email again...
                return malwarebytes.hasFinishedOnboarding().then((isFinished) => {
                    malwarebytes.showWelcome("moz-extension://", isFinished);
                });
            } else {
                malwarebytes.showWelcome("chrome-extension://", false);
            }
            return Promise.resolve();
        } else {
            return Promise.resolve();
        }
    }).catch((err) => {
        console.error("INI: Errored:", err);
    });

    // wait 60 seconds then do an update (after we've been interactive)
    chrome.alarms.create('initUpdateDBAlarm', { delayInMinutes: 1 });
    chrome.alarms.create('showMonthlyNotificationAlarm', { delayInMinutes: 1 });
    malwarebytes.initPermissionRequestAlarm();
    malwarebytes.createContextMenu();
};

export async function sendUpdateMessage(text, id = null) {
    const tabId = await getTabId();
    let msg;
    switch (id) {
        case "download-completed":
            console.debug("SUM: Download completed");
            return;
        case "update-completed":
            console.debug("SUM: SUM: Update completed");
            return;
        case "update-started":
            console.debug("SUM: Update started");
            return;
        case "end-test-channel-update":
            msg = MSG_DEBUG_STAGING_CHANNEL_RESPONSE;
            break;
        case "end-staging-channel-update":
            msg = MSG_DEBUG_STAGING_CHANNEL_RESPONSE;
            break;
        case "error-in-channel-update":
            console.error("SUM: Error in channel update: ", text);
            msg = MSG_DEBUG_UPDATE_ERROR;
            break;
        default:
            console.debug("SUM: Unknown message id: ", id);
            return;
    }
    sendTabMessage(tabId, {
        type: msg,
        text: text,
        id: id
    });
}

