/**
 * Checks the given URL against various whitelists and heuristics to determine its status.
 * This function is used for debugging purposes and can be invoked via debugger.html/check-url?q=<URL>.
 *
 * @param {string} url - The URL to check.
 * @returns {Object} An object containing the success status and the result or error.
 * @returns {boolean} return.success - Indicates if the check was successful.
 * @returns {Object} return.result - The result of the URL check.
 * @returns {boolean} return.result.isInTop1m - Indicates if the URL is in the top 1 million sites.
 * @returns {boolean} return.result.isWhitelistedAd - Indicates if the URL is whitelisted as an ad.
 * @returns {boolean} return.result.isWhitelistedScam - Indicates if the URL is whitelisted as a scam.
 * @returns {boolean} return.result.isWhiteListedTracker - Indicates if the URL is whitelisted as a tracker.
 * @returns {boolean} return.result.isHeuristicsScam - Indicates if the URL is flagged by heuristic scam rules.
 * @returns {boolean} return.result.isHeuristicsPhishing - Indicates if the URL is flagged by heuristic phishing rules.
 * @returns {Object} return.error - The error object if the check failed.
 */
malwarebytes.checkUrl = async (url) => {
    let result = {
        isInTop1m: false,
        isWhitelistedAd: false,
        isWhitelistedScam: false,
        isWhiteListedTracker: false,
        isHeuristicsScam: false,
        isHeuristicsPhishing: false
    };
    try {
        let allDatabases = await DatabaseManager.getInstance().getDatabases(settings);
        let domain = urlHost(url);
        result.isInTop1m = malwarebytes.isWhitelisted(domain, allDatabases['top1m'], "Scams");
        result.isWhitelistedScam = malwarebytes.isWhitelistedScams(url);
        result.isWhitelistedAd = malwarebytes.isWhitelisted(domain, allDatabases['whitelist_ads'], "Ads");
        result.isWhiteListedTracker = allDatabases['whitelist_tracker'].data.some((entry) => {
            if (entry && entry.host && entry.tracker &&
                entry.host === domain && entry.tracker === "heuristic") {
                // eslint-disable-next-line max-len
                console.log(`BTW: Site is whitelisted by a 'whitelist_tracker' entry for ${url}`);
                return true;
            }
        });
        let domains = urlDomains(url);
        if (allDatabases['heuristics']) {
            // Check if the URL is in the heuristics database

            for (let domain of domains) {
                let scamRules = allDatabases['heuristics'].scam;
                for (let rule of scamRules) {
                    const [domainRegex] = rule.split(ruleSeparatorRegexInstance);
                    if (new RegExp(domainRegex).test(domain)) {
                        result.isHeuristicsScam = true;
                        break;
                    }
                }
                let phishingRules = allDatabases['heuristics'].phishing;
                for (let rule of phishingRules) {
                    if (new RegExp(rule.r).test(domain)) {
                        result.isHeuristicsPhishing = true;
                        break;
                    }
                }
            }
        }

        return { success: true, result: result };
    }
    catch (error) {
        console.error("Error checking URL: ", error);
        return { success: false, error: error };
    }
}

malwarebytes.addHeuristicRule = async (rule) => {
    if (!rule) {
        console.error("Error adding heuristic rule: rule is empty");
        return { result: false, error: "Rule is empty" };
    }
    try {
        const dbManager = DatabaseManager.getInstance();
        dbManager.updateDatabase('heuristics_json', {appendData: JSON.parse(rule)});
        const dbHeuristicsJson = await dbManager.getDatabaseByName('heuristics_json', settings);
        const data = dbHeuristicsJson.data;

        processHeuristics(data).then((processedData) => {
            dbManager.setDatabase('heuristics', processedData);
            console.debug("Heuristic rules updated successfully");
        }).catch((error) => {
            console.error("Error processing heuristic rules: ", error);
        });
        console.debug("Added heuristic rule: ", rule);
        return { result: true };
    } catch (error) {
        console.error("Error adding heuristic rule: ", error);
        return { result: false, error: error };
    }
};


