malwarebytes.showMonthlyNotification = () => {
    console.debug('SMN: Checking if we should show monthly notification');
    chrome.storage.local.get("lastMonthlyNotification", (retrievedSettings) => {
        console.debug('SMN: Last monthly notification date:', retrievedSettings.lastMonthlyNotification);
        if (chrome.runtime.lastError) {
            console.error("SMN: Error checking last monthly notification value: ", chrome.runtime.lastError);
            return;
        }
        if (typeof (retrievedSettings.lastMonthlyNotification) === "undefined") {
            const newMonthlyNotificationStartDate = new Date().toISOString();
            console.debug("SMN: New user detected, setting monthly notification start date:",
                newMonthlyNotificationStartDate);
            settings.setToStorage({ lastMonthlyNotification: newMonthlyNotificationStartDate }).then(() => {
                console.debug('SMN: Updated last monthly notification date');
            });
        } else {
            const lastDate = new Date(retrievedSettings.lastMonthlyNotification);
            const now = new Date();
            if (lastDate.getMonth() === now.getMonth() && lastDate.getFullYear() === now.getFullYear()) {
                console.debug('SMN: Already shown this month');
                return;
            } else {
                console.debug("SMN: Showing monthly notification");
                malwarebytes.showNotification().then(() => {
                    // first show notification and if it is successful then update the last shown date
                    settings.setToStorage({ lastMonthlyNotification: new Date().toISOString() }).then(() => {
                        console.debug('SMN: Updated last monthly notification date');
                    });
                }).catch((err) => {
                    console.error('SMN: Failed to show notification due to error. ', err);
                });
            }
        }
    });
};

