malwarebytes.showNotification = () => {
    return new Promise((resolve, reject) => {
        // only show notification if we are not in Safari
        if (browserName() === 'Safari') {
            console.debug('SN: Not showing notification in Safari');
            resolve();
            return;
        }
        chrome.tabs.query({ active: true, currentWindow: true }, ([tabs]) => {
            if (chrome.runtime.lastError) {
                console.error('SN: Failed to get current tab due to error. ', chrome.runtime.lastError);
                return reject(chrome.runtime.lastError);
            }
            if (typeof tabs === "undefined") {
                console.error('SN: Failed to get current tab due to no tabs');
                return reject(new Error('No tabs'));
            }
            const tabId = tabs.id;
            console.debug('SN: Sending message to tab ', tabId);
            sendTabMessage(tabId, { type: MSG_INFO_MONTHLY_NOTIFICATION });
            return resolve();
        });
    });
};

