malwarebytes.connectNative = async () => {
    console.debug("CN: Connecting to native...");
    try {
        // Loading optional permissions requires a reload
        !chrome.runtime.connectNative && chrome.runtime.reload();
        // use "ping_pong" for local python app testing
        nativeApp = chrome.runtime.connectNative('mbambgnativemsg.exe');
        // nativeApp.postMessage("ping");
        nativeApp.onDisconnect.addListener(async (_msg) => {
            nativeApp = null;
            if (chrome.runtime.lastError) {
                console.log(
                    'CN: Native App connection error ', chrome.runtime.lastError.message
                );
            }
            chrome.storage.local.set({ nativeHostStatus: false });
            await simpleStorageSet({ licenseStatePremium: 0 });
            malwarebytes.LICENSE = 0;
            console.log("CN: Native App Disconnected.");
        });

        chrome.storage.local.set({ nativeHostStatus: true });
        nativeApp.onMessage.addListener((resp) => {
            console.debug('CN: Native App Message: ', resp);
            if (resp && resp.eBGAction && resp.eBGAction.forEach) {
                resp.eBGAction.forEach(async (key) => {
                    if (key.eMachId) {
                        return await simpleStorageSet({ machineId: key.eMachId });
                    }
                    if (key.ePing) {
                        return nativeApp.postMessage({ "eBGAction": [{ "ePong": "Unused" }] });
                    }
                    if (key.eExpirationTime) {
                        console.debug('CN: Received expirationTime', key);
                        const newTimeoutInSecs = key.eExpirationTime;
                        const newTimeoutInMins = newTimeoutInSecs / 60;
                        return malwarebytes.overrideAdTelemAlarm(newTimeoutInMins);
                    }
                    if (key.eLicenseStatePremium !== undefined) {//We don't want to skip 0
                        console.debug("CN: Setting licenseStatePremium to", key.eLicenseStatePremium);
                        malwarebytes.LICENSE = key.eLicenseStatePremium;
                        console.log("CN: Setting the new license status to", key.eLicenseStatePremium);
                        await simpleStorageSet({ licenseStatePremium: key.eLicenseStatePremium });
                        return;
                    }
                    if (key.eLicenseTermPremium) {
                        console.debug("CN: Setting licenseTermPremium to", key.eLicenseTermPremium);
                        return await simpleStorageSet({ licenseTermPremium: key.eLicenseTermPremium });
                    }
                    if (key.eLicenseStatePrivacy !== undefined) { //We don't want to skip 0
                        console.debug("CN: Setting licenseStatePrivacy to", key.eLicenseStatePrivacy);
                        return await simpleStorageSet({ licenseStatePrivacy: key.eLicenseStatePrivacy });
                    }
                    if (key.eLicenseTermPrivacy) {
                        console.debug("CN: Setting licenseTermPrivacy to", key.eLicenseTermPrivacy);
                        return await simpleStorageSet({ licenseTermPrivacy: key.eLicenseTermPrivacy });
                    }
                    if (key.eMbamVersion) {
                        console.debug("CN: Mbam versions", key.eMbamVersion);
                        return await simpleStorageSet({ mbamVersion: key.eMbamVersion });
                    }
                });
            }
        });
    } catch (err) {
        chrome.storage.local.set({ nativeHostStatus: false });
        console.log("CN: Failed to connect to local app: ", err);
        console.log("CN: Will retry on browser restart");
        nativeApp = null;
    }
};

