// a function to check if the url permission is removed
const togglePermissionRequiredFlagIfRemoved = async () => {

    if (browserName() !== BROWSERS.FIREFOX) { return; }

    const STORAGE_OBJ = {};
    STORAGE_OBJ[IS_URL_PERM_REMOVED_FLAG] = false; // default value
    const currPermissions = await chrome.permissions.getAll();
    for (const defaultOrigin of DEFAULT_PERMISSIONS.origins) {
        if (!currPermissions.origins.includes(defaultOrigin)) {

            STORAGE_OBJ[IS_URL_PERM_REMOVED_FLAG] = true;

            try {
                await malwarebytes.updateAdsProtectionSettingsOnPermChange(false);
                await malwarebytes.openPermissionReqPage();
                break;
            } catch (err) {
                console.error('TPR: Error setting storage object', err);
            }
        }
    }
    simpleStorageSet(STORAGE_OBJ);
    return STORAGE_OBJ[IS_URL_PERM_REMOVED_FLAG];
}

