malwarebytes.msgIsNeedingNativeMsg = async (callback, { parameters } = {}) => {
    const source = parameters && parameters.source;

    if (await osName() !== 'win') {
        return callback({ needsIt: false, why: 'NOT_WINDOWS' });
    }

    if (chrome.runtime.connectNative) {
        return callback({ needsIt: false, why: 'ALREADY_DONE' });
    }

    const tabMsgSentTo = await getTabMsgSentTo();

    if ((tabMsgSentTo && source !== 'blockPage')) {
        return callback({ needsIt: false, why: 'MSG_SENT_ONCE' });
    }

    if (
        await settings.getFromStorage(SETTING_NATIVE_MESSAGING) === false
    ) {
        return callback({ needsIt: false, why: 'USER_DENIED' });
    }


    if (source !== 'blockPage') {
        await setTabMsgSentTo(true);
    }

    return callback({ needsIt: true, why: 'WE_SAID_SO' });
};

