export const pushBlockTelem = () => {
    if (TARGET_BROWSER !== 'Firefox') {
        malwarebytes.loadAllStatsData().then(async (allTimeStats)=>{
            if (allTimeStats) {
                let totalAds = 0;
                let totalMalware = 0;
                let totalScams = 0;

                // we add silent + loud ads to the total
                totalAds += allTimeStats.ads || 0;
                totalAds += allTimeStats.silentAds || 0;

                totalMalware += allTimeStats.malwares || 0;
                totalMalware += allTimeStats.silentMalwares || 0;

                totalScams += allTimeStats.scams || 0;
                totalScams += allTimeStats.silentScams || 0;

                const adsLastCount = await simpleStorageGet('adsLastCount') || 0;
                const malwareLastCount = await simpleStorageGet('malwareLastCount') || 0;
                const scamsLastCount = await simpleStorageGet('scamsLastCount') || 0;
                const telemetryPayload = {};

                if (adsLastCount !== totalAds) {
                    const countDiferenceToUpdate = totalAds - adsLastCount;
                    const eBlockTelemetryData = [{
                        adTrackers: countDiferenceToUpdate
                    }];
                    console.debug('CN: eBlockTelemetryData', eBlockTelemetryData);
                    nativeApp && nativeApp.postMessage({"eBlockTelemetry": eBlockTelemetryData});
                    telemetryPayload.ads = countDiferenceToUpdate;
                    await simpleStorageSet({adsLastCount: totalAds});
                }

                if (malwareLastCount !== totalMalware) {
                    const countDiferenceToUpdate = totalMalware - malwareLastCount;
                    telemetryPayload.malware = countDiferenceToUpdate;
                    await simpleStorageSet({malwareLastCount: totalMalware});
                }

                if (scamsLastCount !== totalScams) {
                    const countDiferenceToUpdate = totalScams - scamsLastCount;
                    telemetryPayload.scams = countDiferenceToUpdate;
                    await simpleStorageSet({scamsLastCount: totalScams});
                }

                // if payload has any data, send it to telemetry
                if (Object.keys(telemetryPayload).length > 0) {
                    await telemetryBlockCount(
                        telemetryPayload.ads || 0, telemetryPayload.malware || 0, telemetryPayload.scams || 0
                    );
                    console.log('CN: Ads, Malware & Scams stats sent to telemetry', JSON.stringify(telemetryPayload));
                    await dudeService.trackDailyStats(
                        telemetryPayload.ads || 0, telemetryPayload.malware || 0, telemetryPayload.scams || 0
                    );
                    console.log('CN: Daily stats sent to Data Dude');
                } else {
                    console.log('CN: No stats to send to telemetry');
                }
            }
        }).catch((err)=>{
            console.error('CN: Error getting stats', err);
        });
    }
};

