malwarebytes.processHeuristicsDatabase = async () => {
    const featureflags = malwarebytes.FEATURE_FLAGS;
    const dbManager = DatabaseManager.getInstance();

    if (!featureflags[FLAG_ENABLE_HEURISTIC_BLOCKING]) {return;}
    try {
        const heuristicsJsonDb = await dbManager.getDatabaseByName('heuristics_json', settings);
        const content = heuristicsJsonDb.data;
        dbManager.setDatabase('heuristics', await processHeuristics(content));
    } catch (error) {
        console.error("ID: Error processing heuristics database", error);
    }
};

malwarebytes.updateDatabases = async (
    updaterHooks,
    attempt = 1,
    channel = "release",
    prod = true,
    isChannelSwitch = false
) => {
    const databaseManager = DatabaseManager.getInstance();
    console.debug('UPD: Update channel:', channel.toUpperCase());
    console.debug('UPD: Update attempt:', attempt);
    const updater = new Updater(updaterHooks, channel, prod, isChannelSwitch);
    try {
        const availableDBNames = await databaseManager.getAllAvailableDatabaseNames(settings);
        if (availableDBNames.length === 0) {
            // BG-2734 - somehow the malwarebytes.DATABASES object is empty, we need to reload it.
            // Update: 15-05-25 - This should no longer happen after migrating to the DatabaseManager but well leave it here for monitoring purposes
            // console.error('UPD: Database object is empty, restoring databases');
            // await malwarebytes.restoreCachedDatabases();
            return;
        }

        const databasesRecords = await databaseManager.getDatabases(settings);

        let newDbs = [];
        try {
            newDbs = await updater.updateIncrementals(databasesRecords);
        } catch (error) {
            if (error.message.includes(MISSING_DATABASE_ERROR)) {
                console.error(`UPD: Database update error. Restoring databases. ${error.message}`);

                Sentry.captureEvent({
                    message: 'Missing Database error',
                    level: 'error',
                    tags: {
                        'custom': true,
                        'bg-source': 'updater.empty_database_object',
                        version: chrome.runtime.getManifest().version,
                    },
                    exception: { values: [{ type: error.name, value: error.message }] }
                });

                return;
            }
            return;
        }

        console.debug("UPD: Updater part finished", { newDbs });
        let printableUpdates = {};
        for (let db of newDbs) {
            printableUpdates[db.name] = db.version;
        }
        console.log(`UPD: ${newDbs.length}/${DB_NAMES_ALL.length} databases updated`,
            JSON.stringify(printableUpdates));
        settings.setToStorage({ last_successful_fetch: new Date().toLocaleString() });
        if (newDbs.length > 0) {
            await malwarebytes.updateDatabasesCache(newDbs);
            const featureFlagDbs = newDbs.filter((x) => x.isFeatureFlags);
            if (featureFlagDbs && featureFlagDbs.length > 0) {
                malwarebytes.setFeatureFlags(featureFlagDbs[0].data);
            }

            if (isMV3() && TARGET_BROWSER !== 'Firefox') {
                const mv3WhitelistDb = newDbs.find((x) => x.name === 'mbgc.mv3.dynamicwhitelist.json');
                const wlTrackerDb = newDbs.find((x) => x.name === 'mbgc.db.whitelist.tracker.2');
                const trackerData = (wlTrackerDb && wlTrackerDb.data) || [];
                await applyDynamicWhitelists((mv3WhitelistDb && mv3WhitelistDb.data) || [], trackerData);
            }
        }
        // let's check if newDbs has "heuristics_json" in it?
        const heuristicsDb = newDbs.find((x) => x.name === "mbgc.db.heuristics.json.2");
        if (heuristicsDb) {
            try {
                databaseManager.setDatabase('heuristics', await processHeuristics(heuristicsDb.data));
            } catch (error) {
                console.error('UPD: Error processing heuristics', error);
            }
        }
        if (updaterHooks && Object.hasOwn(updaterHooks, 'onUpdateComplete')) {
            await updaterHooks['onUpdateComplete']();
        }

        await malwarebytes.processHeuristicsDatabase();

    } catch (error) {

        if(error.message === BAD_PACKAGE_ERROR){
            console.log(`UPD: Error bad packages, stoping retry`);
            return;
        }

        if (attempt < UPDATE_DB_MAX_ATTEMPS) {
            console.log(`UPD: Error when updating the database on attempt:`, attempt);
            // fullJitterBackoffLogic requires number of attempts, timeout and base time
            const delayTime = fullJitterBackoffLogic(attempt, 2000, 250);
            console.log(`UPD: Delay time for next attempt:`, delayTime);
            setTimeout(() => {
                malwarebytes.updateDatabases(updaterHooks, attempt + 1, channel, prod, isChannelSwitch);
            }, delayTime);
        } else {
            console.log('UPD: Updates canceled due to max request limit reached', UPDATE_DB_MAX_ATTEMPS);
            console.error("UPD: error handing database read: ", error);
            if (updaterHooks && Object.hasOwn(updaterHooks,'onUpdateError')) {
                await updaterHooks['onUpdateError'](error);
            }
            settings.setToStorage({ last_fetch_failure: new Date().toLocaleString() });
        }
    }
};

malwarebytes.getOrCreateUUID = () => {
    return getUUID().then((uuid) => {
        if (uuid) {
            return Promise.resolve(uuid);
        } else {
            //https://stackoverflow.com/questions/105034/create-guid-uuid-in-javascript
            // noinspection SpellCheckingInspection
            uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
                // noinspection EqualityComparisonWithCoercionJS
                let r = Math.random() * 16 | 0;
                let v = c == 'x' ? r : (r & 0x3 | 0x8);
                return v.toString(16);
            });
            return settings.setToStorage({ uuid: uuid }).then((_ignored) => {
                return Promise.resolve(uuid);
            });
        }
    });
};


