malwarebytes.setFeatureFlags = (featureFlags = {}) => {
    for (const key in featureFlags) {
        if (Object.hasOwn(featureFlags, key)) {
            malwarebytes.FEATURE_FLAGS[key] = featureFlags[key];
        }
    }
    console.debug('FEATURE_FLAGS: Feature flags set to: ', malwarebytes.FEATURE_FLAGS);
};

malwarebytes.initDatabases = async (databasesToLoad = DB_NAMES_ALL, isFreshInstall = true) => {
    if (isMV3() && databasesToLoad.length && isFreshInstall) {
        // Don't do this if we are not a fresh install, The additionalRuleSpaceCheckAlarm handles
        // checking for disabled rulesets subseqently
        await tryInitMV3Rulesets();
    }

    const databaseManager = DatabaseManager.getInstance();
    await databaseManager.initDatabases(settings);

    const allDbRecords = await databaseManager.getDatabases(settings);
    const allDbs = Object.values(allDbRecords);

    const featureFlagDbs = allDbs.filter((x) => x && x.isFeatureFlags);
    if (featureFlagDbs.length > 0) {
        console.debug('ID: FEATURE_FLAGS: Setting from:', featureFlagDbs);
        malwarebytes.setFeatureFlags(featureFlagDbs[0].data);
    }

    if (isMV3() && TARGET_BROWSER !== 'Firefox') {
        const mv3WhitelistDb = allDbs.find((x) => x.name === 'mbgc.mv3.dynamicwhitelist.json');
        if (!mv3WhitelistDb) {
            console.error('ID: Dynamic Whitelist: No dynamic whitelist found');
        }

        // store tracker domains from whitelist.tracker in the dynamic whitelist
        const wlTrackerDb = allDbs.find((x) => x.name === 'mbgc.db.whitelist.tracker.2');
        if (!wlTrackerDb) {
            console.warn('ID: Dynamic Whitelist: No tracker whitelist found');
        }

        const trackerData = (wlTrackerDb && wlTrackerDb.data) || [];
        // Build a lookup cache for from the dynamic whitelists mbgc.mv3.dynamicwhitelist.json and from the tracker whitelist
        await buildLookupCaches();
        await applyDynamicWhitelists((mv3WhitelistDb && mv3WhitelistDb.data) || [], trackerData);

        const ruleStats = await getRuleStats();
        console.debug('ID: Rule Stats: ', {ruleStats});
    }

    //Fire and forget ;D (do not await)
    databaseManager.updateDatabasesCache(settings, allDbs);

    await loadEasylist();
    await malwarebytes.processHeuristicsDatabase();
};

malwarebytes.resetDynamicWhitelist = async () => {
    console.debug("RDW: Resetting dynamic whitelist");
    // if we have more than 5k rules, then we need to reset the dynamic whitelist
    const existingRules = await getExistingDynamicRules(DYNAMIC_EASYLIST_WHITELIST_PRIORITY);
    existingRules.push(...await getExistingDynamicRules(DYNAMIC_TRACKER_WHITELIST_PRIORITY));
    if (existingRules.length > 5000) {
        console.debug("RDW: Resetting dynamic whitelist");
        await malwarebytes.initDatabases([DB_NAME_MV3_DYNAMIC_WHITELIST_JSON, 'mbgc.db.whitelist.tracker.2'], false);
    }
    console.debug("RDW: Dynamic whitelist reset finished");
}

async function applyDynamicWhitelists(dynamicWhitelistData, whitelistTrackerData) {
// use the lookup cache to disable static rules that match domains in the dynamic whitelist
    const staticRulesToDisable = [];
    console.debug('ADW: Dynamic Whitelist: Applying:', dynamicWhitelistData);
    for (const item of dynamicWhitelistData) {
        switch (item.type) {
            case 'ad': {
                let res = ads1LookupCache.searchForDomain(item.domain);
                if (res) {
                    const {ruleId} = res;
                    staticRulesToDisable.push({ruleset: 'mbgc.mv3.ads_1', ruleId});
                }

                // Also check ads2
                res = ads2LookupCache.searchForDomain(item.domain);
                if (res) {
                    const {ruleId} = res;
                    staticRulesToDisable.push({ruleset: 'mbgc.mv3.ads_2', ruleId});
                }

                break;
            }
            case 'malware': {
                let res = malwareLookupCache.searchForDomain(item.domain);
                if (res) {
                    const {ruleId} = res;
                    staticRulesToDisable.push({ruleset: 'mbgc.mv3.malware_1', ruleId});
                }
                break;
            }
        }
    }

    if (staticRulesToDisable.length > 0) {
        await disableStaticRules(staticRulesToDisable);
    }
    console.debug('ADW: Whitelist Tracker: Applying:', whitelistTrackerData);
    if (whitelistTrackerData.length > 0) {
        await updateDynamicTrackerWhitelistRules(whitelistTrackerData);
    }

    const ruleStats = await getRuleStats();
    console.debug('ADW: Rule Stats: ', {ruleStats});
}


async function buildLookupCaches() {

    const ads1StaticRulesUrl = chrome.runtime.getURL("db/mbgc.mv3.ads_1.json");
    const ads2StaticRulesUrl = chrome.runtime.getURL("db/mbgc.mv3.ads_2.json");
    const malwareStaticRulesUrl = chrome.runtime.getURL("db/mbgc.mv3.malware_1.json");

    console.debug('BLC: Fetching static rules', {ads1StaticRulesUrl, ads2StaticRulesUrl, malwareStaticRulesUrl});

    const staticRuleFetch = async (url) => {
        const options = {};
        options.method = options.method || "GET";
        options.headers = options.headers || {};
        options.headers["pragma"] = "no-cache";
        options.headers["cache-control"] = "no-cache, no-store";

        const response = await fetch(url, options)
        return response.json();
    }

    console.debug('BLC: Fetching static rules', {ads1StaticRulesUrl, ads2StaticRulesUrl, malwareStaticRulesUrl});

    const adStaticRules1 = await staticRuleFetch(ads1StaticRulesUrl);
    const adStaticRules2 = await staticRuleFetch(ads2StaticRulesUrl);
    const malwareStaticRules = await staticRuleFetch(malwareStaticRulesUrl);

    console.debug('BLC: Static rules fetched', {adStaticRules1, adStaticRules2, malwareStaticRules});

    ads1LookupCache = new StaticRuleLookupCache("ads1", adStaticRules1);
    ads2LookupCache = new StaticRuleLookupCache("ads2", adStaticRules2);
    malwareLookupCache = new StaticRuleLookupCache("malware", malwareStaticRules);
}

async function loadEasylist() {
    const databaseManager = DatabaseManager.getInstance();
    let easylistContent;
    try {
        let elReader = new IndexedDatabaseFile("mbgc.db.easylist.2");
        easylistContent = await elReader.readBundled();
    } catch (error) {
        console.error("EL: Error reading easylist database", error);
        return
    }

    if (easylistContent.extended_hide == null) {
        easylistContent.extended_hide = [];
    }

    if (malwarebytes.FEATURE_FLAGS[FLAG_ENABLE_BLOCK_GDPR] === true) {
        let epReader = new IndexedDatabaseFile("mbgc.db.easyprivacy.2");
        let easyPrivacyContent = await epReader.readBundled();
        if (easyPrivacyContent.extended_hide == null) {
            easyPrivacyContent.extended_hide = [];
        }

        databaseManager.setDatabase("easylistprivacy", easyPrivacyContent);
        optimizeEasylistDomains("easylistprivacy", "domains");
        optimizeEasylistDomains("easylistprivacy", "domain_exceptions");
        optimizeEasylistPages("easylistprivacy", "pages");
    } else {
        databaseManager.setDatabase("easylistprivacy", getEmptyEasylistItemsElement()['easylistprivacy']); // flag is not enabled and we do not want to get exceptions elsewhere
    }

    if (!('extended_hide' in easylistContent)) {
        easylistContent['extended_hide'] = [];
    }
    databaseManager.setDatabase("easylist", easylistContent);
    optimizeEasylistDomains("easylist", "domains");
    optimizeEasylistDomains("easylist", "domain_exceptions");
    optimizeEasylistPages("easylist", "pages");
}

