async function optimizeEasylistPages(dbName, sectionName) {
    const databaseManager = DatabaseManager.getInstance();
    const db = await databaseManager.getDatabaseByName(dbName, settings);
    let onlyNames = db[sectionName];
    // let's create a dictionary here, consisting of domain name as key and full url as value
    let onlyNamesDict = {};
    for (let i = 0; i < onlyNames.length; i++) {
        // /ad/banners/*
        // /ad/common_
        // /ad/dfp/*
        // /ad/err?
        // /ad/getban?
        // /ad/html/*
        // /ad/image/*
        // /ad/images/*$domain=~studiocalling.it
        let name = onlyNames[i].split("$")[0].replace("?", "").replace(/\*/g, "");
        if (!name.startsWith("/")) {
            continue;
        }
        if (onlyNamesDict[name] === undefined) {
            onlyNamesDict[name] = [];
        }
        onlyNamesDict[name].push(onlyNames[i]);
    }
    databaseManager.updateDatabase(dbName, {fieldsToUpdate: { [sectionName]: onlyNamesDict }});
}

