/**
 * Optimizes the easylist domains by creating a dictionary with domain names as keys and full URLs as values.
 * @param {string} dbName - The name of the database.
 * @param {string} sectionName - The name of the section in the database.
 */
async function optimizeEasylistDomains(dbName, sectionName) {
    const databaseManager = DatabaseManager.getInstance();
    const db = await databaseManager.getDatabaseByName(dbName, settings);
    let onlyNames = db[sectionName];
    // let's create a dictionary here, consisting of domain name as key and full url as value
    let onlyNamesDict = {};
    for (let i = 0; i < onlyNames.length; i++) {
        // ||surveymonkey.com/jspop.aspx?
        // ||sweeva.com/images/banner250.gif
        // ||syndicate.payloadz.com^
        // ||t.co^$subdocument,domain=kshow123.tv
        // ||taboola.com/vpaid/
        // ||track.effiliation.com^$image,script
        let name = extractDomainName(onlyNames[i]);
        if (onlyNamesDict[name] === undefined) {
            onlyNamesDict[name] = [];
        }
        onlyNamesDict[name].push(onlyNames[i]);
    }
    databaseManager.updateDatabase(dbName, {fieldsToUpdate: { [sectionName]: onlyNamesDict }});
}

/**
 * Extracts the domain name from an easylist entry.
 *
 * @param {string} easylistEntry - The easylist entry to extract the domain name from.
 * @returns {string} The extracted domain name.
 */
export function extractDomainName(easylistEntry) {
    let parts = easylistEntry.split("$");
    let name = parts[0].split("^")[0].replace(/^(?:https?:\/\/)?(?:[^\\/]+\.)?([^.\\/]+\.[^.\\/]+).*$/, "$1");
    return name;
}

function isUserBlockedItem(urlOfTab, urlOfAd) {
    if (isMV3()) {
        // let the rulesets handle the blocks
        return false;
    }
    if (urlOfTab && isBrowserPage(urlOfTab)) {
        return false;
    }

    return userBlocksHandler.isBlocked(urlOfAd) || userBlocksHandler.isBlocked(urlOfTab);
}


/**
 * Checks if the given URL is blocked in content controls.
 * This function returns true if the URL is blocked in content controls, false otherwise
 * independent of the manifest version.
 * @param {string} urlOfTab - The URL of the tab to check.
 * @returns {boolean} Returns true if the URL is blocked in content controls, false otherwise.
 */
function isBlockedInContentControls(urlOfTab) {
    if (urlOfTab && isBrowserPage(urlOfTab)) {
        return false;
    }

    // check for subdomains
    const domains = urlDomains(urlOfTab);
    if (domains.length > 1) {
        for (let subdomain of domains) {
            if (contentControls.existsForUrl(subdomain)) {
                return true;
            }
        }
    }

    return contentControls.existsForUrl(urlOfTab) || contentControls.existsForUrl(urlHost(urlOfTab));
}

