malwarebytes.exclusionIntToConstant = (exclusionInt) => {
    switch (exclusionInt) {
        case 2:
        case 3:
            return EXCLUSION_ADS;
        case 4:
            return EXCLUSION_MALWARE;
        case 6:
            return EXCLUSION_SCAMS;
        case 7:
            return EXCLUSION_PUPS;
    }
    throw new Error(`${exclusionInt} is not recognised`);
};

malwarebytes.isHeuristicAdsIndividualExcluded = (host) => {
    return malwarebytes.EXCLUSION_INDIVIDUAL[host] && malwarebytes.EXCLUSION_INDIVIDUAL[host][HEURISTIC_AD_BLOCK];
};

malwarebytes.isEasyListAdsIndividualExcluded = (host) => {
    return malwarebytes.EXCLUSION_INDIVIDUAL[host] && malwarebytes.EXCLUSION_INDIVIDUAL[host][EASYLIST_AD_BLOCK];
};

malwarebytes.isEasyListPrivacyIndividualExcluded = (host) => {
    return malwarebytes.EXCLUSION_INDIVIDUAL[host] && malwarebytes.EXCLUSION_INDIVIDUAL[host][EASYLIST_PRIVACY_BLOCK];
};

malwarebytes.isAttrReportAdsIndividualExcluded = (host) => {
    return malwarebytes.EXCLUSION_INDIVIDUAL[host] && malwarebytes.EXCLUSION_INDIVIDUAL[host][ATTRIBUTION_REPORTING];
};

malwarebytes.isPingTrackerIndividualExcluded = (host) => {
    return malwarebytes.EXCLUSION_INDIVIDUAL[host] && malwarebytes.EXCLUSION_INDIVIDUAL[host][PING_TRACKER];
};

malwarebytes.removeAllExclusionsInMeta = (passthrough) => {
    return new Promise((resolve) => {
        for (let tab in malwarebytes.TABS) {
            if (Object.hasOwn(malwarebytes.TABS, tab)) {
                malwarebytes.TABS[tab].meta["isExcludedAds"] = false;
                malwarebytes.TABS[tab].meta["isExcludedScams"] = false;
                malwarebytes.TABS[tab].meta["isExcludedMalware"] = false;
                malwarebytes.TABS[tab].meta["isExcludedTrackers"] = false;
                // invalidate the whitelist cache
                Object.keys(malwarebytes.WHITELISTED).forEach((key) => {
                    delete malwarebytes.WHITELISTED[key];
                });
            }
        }
        resolve(passthrough);
    });
};

malwarebytes.updateExclusionsInMeta = (host, exclusionsArr, passthrough, isAdd) => {
    return new Promise((resolve) => {
        for (let tab in malwarebytes.TABS) {
            if (Object.hasOwn(malwarebytes.TABS, tab)) {
                if (malwarebytes.TABS[tab].site && malwarebytes.TABS[tab].site.host === host) {
                    //Go through META flags and fix everything that is in the exclusions param
                    exclusionsArr.forEach((val) => {
                        //Map val int to an exclusions flag
                        let excl = malwarebytes.exclusionIntToConstant(val);
                        switch (excl) {
                            case EXCLUSION_ADS:
                                malwarebytes.TABS[tab].meta["isExcludedAds"] = isAdd;
                                break;
                            case EXCLUSION_SCAMS:
                                malwarebytes.TABS[tab].meta["isExcludedScams"] = isAdd;
                                break;
                            case EXCLUSION_MALWARE:
                                malwarebytes.TABS[tab].meta["isExcludedMalware"] = isAdd;
                                break;
                            case EXCLUSION_PUPS:
                                malwarebytes.TABS[tab].meta["isExcludedMalware"] = isAdd;
                                break;
                            default:
                                console.error("Meta not actually updated: Unrecognised '" + val + "' / '" + excl + "'");
                        }
                        // invalidate the whitelist cache
                        Object.keys(malwarebytes.WHITELISTED).forEach((key) => {
                            delete malwarebytes.WHITELISTED[key];
                        });
                    });
                    console.log("Meta updated.");
                    console.log(malwarebytes.TABS[tab]);
                }
            }
        }
        resolve(passthrough);
    });
};

/**
 * Posts an exclusion to the Native App, so they both don't block
 * @param {string} url - URL that was detected or host that was added to the Allow List
 * @param {boolean} [isHost] - If the URL is already a host
 * @param {boolean} [isPermanent] - If the exclusion is permanent
 */
malwarebytes.postNativeExclusion = (url, isHost, isPermanent) => {
    if (!nativeApp) {
        if (chrome.runtime.connectNative) {
            // attempt to reconnect
            malwarebytes.connectNative();
        }
        return;
    }

    const host = isHost ? url : urlHost(url);
    console.log('PD: Posting message to native app about: ', host);
    nativeApp.postMessage(
        {
            eBGAction: [
                { [isValidIP(host) ? "eIpv4" : "eUrl"]: host },
                { eExclude: isPermanent }
            ]
        }
    );
};

/**
 * Posts a request to remove an item from the Allow List
 * @param {string} host - host to be removed
 */
malwarebytes.postNativeExclusionRemoval = (host) => {
    if (!nativeApp) {
        return;
    }

    console.log('PNER: Posting to native app re allowlist removal of ', host);
    nativeApp.postMessage(
        {
            eBGAction: [
                { [isValidIP(host) ? "remIpv4" : "remUrl"]: host },
            ]
        }
    );
};

/**
 * Posts the global toggles
 */
malwarebytes.postNativeTogglesState = () => {
    if (!nativeApp) {
        return;
    }

    const result = malwarebytes.computeTogglesState();
    console.debug('PNTS: Posting toggles state to native app: ', result);
    nativeApp.postMessage(
        { eBGAction: [{ eGlobalBgToggles: result }] }
    );
};

