/**
 * Records detection data for a given tab and updates the badge count.
 *
 * @param {string} recordType - The type of record to log (e.g., 'malware', 'phishing').
 * @param {number} tabId - The ID of the tab where the detection occurred.
 * @param {string} tabHost - The host of the tab where the detection occurred.
 * @param {string} blockedContentId - The ID of the blocked content.
 * @param {boolean} blocked - Indicates whether the content was blocked
 * @param {boolean} [isSilent=false] - Indicates whether the detection is silent, false by default.
 */
malwarebytes.recordAll = (recordType, tabId, tabHost, blockedContentId, blocked, isSilent = false) => {
    //malwarebytes.recordForDate: Records counts per type per day
    malwarebytes.recordForDate(recordType, 1, isSilent);
    //malwarebytes.recordForTab: Records details of detection, per tab
    malwarebytes.recordForTab(recordType, tabId, tabHost, blockedContentId, blocked, isSilent);
    //malwarebytes.syncDetectionData(); //CALLED WHEN PAGE FINISHED!, TOO EXPENSIVE HERE!
    malwarebytes.updateBadgeCount(tabId, isSilent);
};

