/**
 * Checks if the given stats object is valid.
 *
 * @param {object} statsObj - The stats object to be checked.
 * @return {boolean} True if the stats object is valid, false otherwise.
 */
malwarebytes.checkValidStatsObject = (statsObj) => {
    const keys = ['ads', 'malwares', 'scams', 'content'];
    const hasAllKeys = keys.every((key) => Object.hasOwn(statsObj, key));
    const hasValidValues = keys.every((key) => typeof statsObj[key] === 'number');
    return hasAllKeys && hasValidValues;
};

malwarebytes.cleanStats = (statsObj) => {
    for (const statKey in statsObj) {
        if (typeof statsObj[statKey] !== 'number') {
            console.debug(`STATS: rolling 30 days ${statKey} stats due to invalid value`);
            malwarebytes.roll30DaysStats(statKey);
        } else if (statsObj[statKey].toString().length > 13) {
            console.debug(`STATS: rolling 30 days ${statKey} stats due to exp value`);
            malwarebytes.roll30DaysStats(statKey);
        }
    }
};

