malwarebytes.recordForDate = (type, count, isSilent) => {
    let dateKey = malwarebytes.today();

    // If malwarebytes.RECORD is a string (e.g '{"k": <some_val>}'), convert malwarebytes.RECORD to an object
    if (typeof malwarebytes.RECORD === 'string') {
        malwarebytes.RECORD = JSON.parse(malwarebytes.RECORD);
    }

    if (!malwarebytes.RECORD[dateKey]) {
        malwarebytes.RECORD[dateKey] = {
            ts: malwarebytes.todayTimestamp(),
            ...ALL_TIME_STATS_TEMPLATE
        };
    }

    malwarebytes.cleanStats(malwarebytes.ALL_TIME_STATS);

    switch (type) {
        case RecordType.AD:

            if (isSilent) {
                malwarebytes.RECORD[dateKey].silentAds += count;
                malwarebytes.ALL_TIME_STATS.silentAds += count;
            } else {
                malwarebytes.RECORD[dateKey].ads += count;
                malwarebytes.ALL_TIME_STATS.ads += count;
            }
            break;
        case RecordType.MALWARE:
            if (isSilent) {
                malwarebytes.RECORD[dateKey].silentMalwares += count;
                malwarebytes.ALL_TIME_STATS.silentMalwares += count;
            } else {
                malwarebytes.RECORD[dateKey].malwares += count;
                malwarebytes.ALL_TIME_STATS.malwares += count;
            }
            break;
        case RecordType.SCAM:

            if (isSilent) {
                malwarebytes.RECORD[dateKey].silentScams += count;
                malwarebytes.ALL_TIME_STATS.silentScams += count;
            } else {
                malwarebytes.RECORD[dateKey].scams += count;
                malwarebytes.ALL_TIME_STATS.scams += count;
            }
            break;
        case RecordType.CONTENT:
            if (isSilent) {
                malwarebytes.RECORD[dateKey].silentContent += count;
                malwarebytes.ALL_TIME_STATS.silentContent += count;
            } else {
                malwarebytes.RECORD[dateKey].content += count;
                malwarebytes.ALL_TIME_STATS.content += count;
            }
            break;
        default:
            console.error("Error: detection not recorded. Type must be one of RecordType");
    }
};

malwarebytes.getRecordKeysSorted = () => {
    let keys = [];
    for (let record in malwarebytes.RECORD) {
        if (Object.hasOwn(malwarebytes.RECORD, record)) {
            keys.push(record);
        }
    }
    keys.sort(function (a, b) {
        return malwarebytes.RECORD[a].ts < malwarebytes.RECORD[b].ts ? -1 : 1;
    });
    console.debug("GRKS: getRecordKeysSorted keys, ", keys);
    return keys;
};

