malwarebytes.recallDetectionData_lookupRecords = (minRequired, maxRequired) => {
    return new Promise((resolve, reject) => {
        //TODO: SPLIT FURTHER
        if (Date.parse(minRequired).clearTime().equals(Date.parse(maxRequired).clearTime())) { //if MIN==MAX check for exact in record
            if (malwarebytes.RECORD[minRequired]) { //if record contains exact date use it, otherwise load it
                console.debug("recallDetectionData: Single result is in record");
                resolve(malwarebytes.RECORD);
            } else {
                console.debug("recallDetectionData: Loading from min");
                malwarebytes.loadAllDetectionRecords().then((resolved) => {
                    resolve(resolved);
                }, (error) => {
                    console.error(error);
                    reject(error); //pass the error upstream
                });
            }
        } else { //else Check RECORD, see if it's range encompasses min and max,
            console.debug("recallDetectionData: Sorting record");
            let sortedKeys = malwarebytes.getRecordKeysSorted();
            let firstKey = sortedKeys[0];
            let lastKey = sortedKeys[sortedKeys.length - 1];
            let firstRecord = malwarebytes.RECORD[firstKey];
            let lastRecord = malwarebytes.RECORD[lastKey];
            let minDate = Date.parse(minRequired);
            let maxDate = Date.parse(maxRequired);
            if (sortedKeys.length > 0) {
                console.debug("recallDetectionData: First: " + firstKey + " (" + firstRecord.ts + ") <= " +
                    minRequired + " (" + minDate.getTime() + ") : " +
                    (firstRecord.ts <= minDate.getTime()));

                console.debug("recallDetectionData: Last: " + lastKey + " (" + lastRecord.ts + ") >= " +
                    maxRequired + " (" + maxDate.getTime() + ") : " +
                    (lastRecord.ts >= maxDate.getTime()));
            }
            if (sortedKeys.length > 0 && firstRecord.ts <= minDate.getTime() &&
                lastRecord.ts >= maxDate.getTime()) {
                console.debug("recallDetectionData: All of requested range in records cache");
                resolve(malwarebytes.RECORD);
            } else { //the range we want is bigger than the loaded record, load from sync storage
                console.debug("recallDetectionData: Loading for range");
                malwarebytes.loadAllDetectionRecords().then((resolved) => {
                    console.debug("loadAllDetectionRecords: successful resolve", resolved);
                    resolve(resolved);
                }, (error) => {
                    console.error(error);
                    reject(error); //pass the error upstream
                });
            }
        }
    });
};

malwarebytes.recallDetectionData_filterResults = (resolved, minRequired, maxRequired) => {
    return new Promise((resolve) => {
        console.debug("recallDetectionData: then: ", resolved);
        //NOTE: records nested within the result as this is how it's returned from storage and this keeps it interchangeable
        let result;
        if (!minRequired && !maxRequired) {
            console.debug("recallDetectionData: then - return all results");
            //No limits - return everything
            result = resolved;
        } else {
            console.debug("recallDetectionData: then - return limit results");
            result = {};
            let startDate;
            let endDate;
            //limit the returned rows to our range
            if (!minRequired) {
                //need everything until max
                startDate = Date.today().addWeeks(-10); //this is the max range we support
                endDate = Date.parse(maxRequired).clearTime().addDays(1); //addDays because it ends at midnight
            } else if (!maxRequired) {
                //need everything after min
                startDate = Date.parse(minRequired).clearTime();
                endDate = startDate.addWeeks(10).addDays(1); //10 weeks is the max range we support
            } else {
                //need everything between min and max
                startDate = Date.parse(minRequired).clearTime();
                endDate = Date.parse(maxRequired).clearTime().addDays(1); //addDays because it ends at midnight
                console.debug("recallDetectionData: range - " + minRequired + ", " + maxRequired);
                if (startDate.equals(endDate)) {
                    console.debug("recallDetectionData: then - return single result ", minRequired);
                    result[minRequired] = resolved[minRequired];
                    resolve(result);
                    return;
                }
            }
            console.debug(
                "recallDetectionData: then - return between [" +
                minRequired + " (" + startDate + "), " + maxRequired + " (" + endDate + ")]: ",
                resolved
            );
            for (let key in resolved) {
                if (Object.hasOwn(resolved, key)) {
                    //NOTE: NEED TO WORK FROM TIMESTAMPS HERE: PARSING KEYS WILL ALWAYS RESULT IN CURRENT YEAR AND BREAK JANUARY
                    if (new Date(resolved[key].ts).between(startDate, endDate)) {
                        result[key] = resolved[key];
                    }
                }
            }
        }
        resolve(result);
    });
};

malwarebytes.recallDetectionData = (minRequired, maxRequired) => {
    //First if we have something in cache, make sure we sync so storage reflects the same
    return malwarebytes.syncDetectionData().then((_ignored) => {
        return malwarebytes.recallDetectionData_lookupRecords(minRequired, maxRequired);
    }).then((resolved) => {
        console.debug("RecallData: filter results");
        return malwarebytes.recallDetectionData_filterResults(resolved, minRequired, maxRequired);
    }, (error) => {
        console.debug("RecallData: error ", error);
        return Promise.reject(error);
    });
};
