malwarebytes.loadAllDetectionRecords = () => {
    return new Promise((resolve, reject) => {
        /* The code below is for backward compatibility. It can be deleted after 2024/03/01
        It can be removed down to this marker <--> */
        chrome.storage.sync.get(['records'], function (syncResult) {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError.message);
                return;
            }
            const records = syncResult.records;
            if (records) {
                chrome.storage.local.set({ records }, function () {
                    if (chrome.runtime.lastError) {
                        reject(chrome.runtime.lastError.message);
                        return;
                    }
                    chrome.storage.sync.remove(['records'], function () {
                        if (chrome.runtime.lastError) {
                            reject(chrome.runtime.lastError.message);
                            return;
                        }
                        resolve(records);
                    });
                });
            } else {
                /* <--> */
                chrome.storage.local.get(['records'], function (localResult) {
                    if (chrome.runtime.lastError) {
                        reject(chrome.runtime.lastError.message);
                        return;
                    }
                    resolve(localResult.records);
                });
            }
        });
    });
};

malwarebytes.loadAllStatsData = () => { // this function contains silent records counts
    return new Promise((resolve, reject) => {
        /* The code below is for backward compatibility. It can be deleted after 2024/03/01
        It can be removed down to this marker >--> */
        chrome.storage.sync.get(['allTimeStats'], function (syncResult) {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError.message);
                return;
            }
            const allTimeStats = syncResult.allTimeStats;
            if (allTimeStats) {
                chrome.storage.local.set({ allTimeStats }, function () {
                    if (chrome.runtime.lastError) {
                        reject(chrome.runtime.lastError.message);
                        return;
                    }
                    chrome.storage.sync.remove(['allTimeStats'], function () {
                        if (chrome.runtime.lastError) {
                            reject(chrome.runtime.lastError.message);
                            return;
                        }
                        if (Object.keys(allTimeStats).length !== Object.keys(ALL_TIME_STATS_TEMPLATE).length) {
                            resolve({...ALL_TIME_STATS_TEMPLATE});
                            return;
                        }
                        resolve(allTimeStats);
                    });
                });
            } else {
                /* >--> */
                chrome.storage.local.get(['allTimeStats'], function (localResult) {
                    if (chrome.runtime.lastError) {
                        reject(chrome.runtime.lastError.message);
                        return;
                    }

                    if (
                        (localResult && localResult.allTimeStats) &&
                        Object.keys(localResult.allTimeStats).length !== Object.keys(ALL_TIME_STATS_TEMPLATE).length
                    ) {
                        console.debug('CN: All time stats from local is not valid, returning template');
                        resolve({...ALL_TIME_STATS_TEMPLATE});
                        return;
                    }

                    console.debug(
                        'CN: All time stats from local is valid, returning it: ', {res: localResult.allTimeStats}
                    );
                    resolve(localResult.allTimeStats);
                });
            }
        });
    });
};

malwarebytes.syncDetectionData_mergeDetectionRecordsWithCache = (recordsFromDb) => {
    return new Promise((resolve) => {
        //  We can't load the page record before blocking and counting ads - because we would be blocking the page load
        //    that means the data in the db needs to be SUMMED with the cache data when syncing.
        //  We need to make sure we either then do not keep a cache or have some identifier or flag so that we know
        //    in future sync ops, that the data is duplicate and should be OVERWRITTEN instead of summed
        //  The flag is RECORD_LOADED and is set in syncDetectionData_replaceCache NOT in loadAllDetectionRecords
        //    because we need to wait until all the data is merged together before it is valid
        if (!recordsFromDb) {
            recordsFromDb = {};
        }
        if (malwarebytes.RECORD) {
            for (let cachedRecordKey in malwarebytes.RECORD) {
                if (Object.hasOwn(malwarebytes.RECORD, cachedRecordKey)) {
                    if (recordsFromDb[cachedRecordKey]) {
                        if (malwarebytes.RECORD_LOADED === true) {
                            //A REPLACE IS NEEDED (The cache is newer content)
                            recordsFromDb[cachedRecordKey] = malwarebytes.RECORD[cachedRecordKey];
                        } else {
                            //A MERGE (SUMMATION) IS NEEDED
                            /* Example Model:
                                malwarebytes.RECORD[currentDate.toString('yyyy-MM-dd')] = {
                                    ts: 12468986078,
                                    ads: 3,
                                    fakeNews: 1,
                                    trackers:7,
                                    malwares: 5,
                                    scams: 1,
                                    pups: 0,
                                    clickbaits: 23
                                }; */
                            let record = recordsFromDb[cachedRecordKey];
                            record.ads += malwarebytes.RECORD[cachedRecordKey].ads;
                            record.silentAds += malwarebytes.RECORD[cachedRecordKey].silentAds;
                            record.malwares += malwarebytes.RECORD[cachedRecordKey].malwares;
                            record.silentMalwares += malwarebytes.RECORD[cachedRecordKey].silentMalwares;
                            record.scams += malwarebytes.RECORD[cachedRecordKey].scams;
                            record.silentScams += malwarebytes.RECORD[cachedRecordKey].silentScams;
                            record.content += malwarebytes.RECORD[cachedRecordKey].content;
                            record.silentContent += malwarebytes.RECORD[cachedRecordKey].silentContent;
                            recordsFromDb[cachedRecordKey] = record;
                        }
                    } else {
                        //AN INSERT IS NEEDED
                        recordsFromDb[cachedRecordKey] = malwarebytes.RECORD[cachedRecordKey];
                    }
                }
            }
        }
        resolve(recordsFromDb);
    });
};

malwarebytes.syncAllStatsData_mergeAllTimeStasWithCache = (statsFromDb) => {
    return new Promise((resolve) => {
        if (typeof statsFromDb === "undefined" || Object.keys(statsFromDb).length === 0) {
            malwarebytes.roll30DaysStats();
        }
        if (malwarebytes.checkValidStatsObject(malwarebytes.ALL_TIME_STATS)) {
            if (malwarebytes.ALL_TIME_STATS_LOADED === true) {
                statsFromDb = malwarebytes.ALL_TIME_STATS;
            } else {
                statsFromDb.ads += malwarebytes.ALL_TIME_STATS.ads;
                statsFromDb.silentAds += malwarebytes.ALL_TIME_STATS.silentAds;
                statsFromDb.malwares += malwarebytes.ALL_TIME_STATS.malwares;
                statsFromDb.silentMalwares += malwarebytes.ALL_TIME_STATS.silentMalwares;
                statsFromDb.scams += malwarebytes.ALL_TIME_STATS.scams;
                statsFromDb.silentScams += malwarebytes.ALL_TIME_STATS.silentScams;
                statsFromDb.content += malwarebytes.ALL_TIME_STATS.content;
                statsFromDb.silentContent += malwarebytes.ALL_TIME_STATS.silentContent;
            }
        }
        resolve(statsFromDb);
    });
};
malwarebytes.syncDetectionData_replaceCache = (mergedData) => {
    return new Promise((resolve) => {
        //Mark data as loaded here, any future sync will REPLACE rather than SUMMATE the data
        malwarebytes.RECORD = mergedData;
        malwarebytes.RECORD_LOADED = true;
        resolve(true);
    });
};
malwarebytes.syncAllStasData_replaceCache = (mergedData) => {
    return new Promise((resolve) => {
        //Mark data as loaded here, any future sync will REPLACE rather than SUMMATE the data
        malwarebytes.ALL_TIME_STATS = mergedData;
        const installationDate = malwarebytes.getInstallationDate();
        if (installationDate !== undefined && installationDate !== null) {
            malwarebytes.ALL_TIME_STATS_LOADED = true;
        } else {
            malwarebytes.ALL_TIME_STATS_LOADED = false;
        }
        resolve(true);
    });
};
malwarebytes.syncDetectionData_cleanupOldData = (records) => {
    return new Promise((resolve, _ignored) => {
        //Start with today's ts and offset it to 30 days ago at midnight
        let dateThreshold = Date.today().addDays(-30);
        dateThreshold.setHours(0);
        dateThreshold.setMinutes(0);
        dateThreshold.setSeconds(0);
        dateThreshold.setMilliseconds(0);
        dateThreshold = dateThreshold.getTime();
        //Look through all the data we have and delete anything older than the timestamp we just
        // Records format: { "Jun 25": { ts: 1923865923 }}
        for (let dateKey in records) {
            if (Object.hasOwn(records, dateKey)) {
                if (records[dateKey].ts < dateThreshold) {
                    console.debug("Deleting outdated record: " + dateKey);
                    delete records[dateKey];
                }
            }
        }
        resolve(records);
    });
};

malwarebytes.syncDetectionData = () => {
    return malwarebytes.loadAllDetectionRecords().then((allRecords) => {
        return malwarebytes.syncDetectionData_mergeDetectionRecordsWithCache(allRecords);
    }).then((mergedRecords) => {
        return malwarebytes.syncDetectionData_cleanupOldData(mergedRecords);
    }).then((cleanedMergedRecords) => {
        return malwarebytes.syncDetectionData_replaceCache(cleanedMergedRecords);
    }).then((_ignored) => {
        return malwarebytes.persistDailyData(); //NOTE: Copies the updated cache back into db
    }).catch((e) => {
        return Promise.reject(e);
    });
};
malwarebytes.syncAllStatsData = () => {
    return malwarebytes.loadAllStatsData().then((allTimeStats) => {
        return malwarebytes.syncAllStatsData_mergeAllTimeStasWithCache(allTimeStats);
    }).then((mergedRecords) => {
        return malwarebytes.syncAllStasData_replaceCache(mergedRecords);
    }).then((_ignored) => {
        return malwarebytes.persistDailyStatsData(); //NOTE: Copies the updated cache back into db
    }).catch((e) => {
        return Promise.reject(e);
    });
};

