malwarebytes.msgWebsiteCheckboxGet = (callback, request) => {
    //OLD Logic did this, in Website script:  !exclusionInts || !Array.isArray(exclusionInts) || !exclusionInts.includes( mbui.constToInt(exclusionConst) );
    exclusionHandler.getExclusions().then((_ignored) => {
        return malwarebytes.isOverriddenForDomain(request.payload.const, request.payload.url);
    }).then((isOverridden) => {
        switch (request.payload.const) {
            case EXCLUSION_ADS:
                if (isOverridden) {
                    //If it IS excluded (2 is stored), the checkbox should show false
                    //If it IS NOT excluded (-2 is stored), the checkbox should show true
                    //If it nothing is stored, the checkbox should show true
                    console.debug("Returning overridden result for ads cb");
                    callback({
                        success: malwarebytes.isExcludedForDomain(
                            EXCLUSION_ADS, request.payload.url
                        ) !== true
                    });
                } else {
                    console.debug("Returning result by querying settings for ads cb");
                    settings.getFromStorage(SETTING_ADS).then((settingValue) => {
                        callback({ success: settingValue !== false });
                    });
                }
                break;
            case EXCLUSION_SCAMS:
                if (isOverridden) {
                    //If it IS excluded, the checkbox should show false
                    callback({
                        success: malwarebytes.isExcludedForDomain(
                            EXCLUSION_SCAMS, request.payload.url
                        ) !== true
                    });
                } else {
                    settings.getFromStorage(SETTING_SCAMS).then((settingValue) => {
                        callback({ success: settingValue !== false });
                    });
                }
                break;
            case EXCLUSION_MALWARE:
                if (isOverridden) {
                    //If it IS excluded, the checkbox should show false
                    callback({
                        success: malwarebytes.isExcludedForDomain(
                            EXCLUSION_MALWARE, request.payload.url
                        ) !== true
                    });
                } else {
                    settings.getFromStorage(SETTING_MALWARE).then((settingValue) => {
                        callback({ success: settingValue !== false });
                    });
                }
                break;
            default:
                console.error("Unhandled case '" + request.payload.const + "' in msgWebsiteCheckboxGet");
                callback({ success: true }); //default state
        }
    }).catch((errMsg) => {
        callback({ error: errMsg });
    });
    return true; //be asynchronous
};

malwarebytes.msgGetAllowGranular = (callback, {payload}) => {
    let {url, blockUrl} = payload;
    const host = urlHost(url.toLowerCase());
    blockUrl = blockUrl.toLowerCase();

    if (
        malwarebytes.EXCLUSION_INDIVIDUAL[host] &&
        Object.hasOwn(malwarebytes.EXCLUSION_INDIVIDUAL[host], blockUrl)
    ) {
        return callback({
            value: malwarebytes.EXCLUSION_INDIVIDUAL[host][blockUrl]
        });
    }

    chrome.storage.local.get(["exclusionIndividual"], ({ exclusionIndividual = {} }) => {
        malwarebytes.EXCLUSION_INDIVIDUAL = exclusionIndividual;
        return callback({
            value:
                exclusionIndividual[host] &&
                exclusionIndividual[host][blockUrl]
        });
    });
    return true;
};

malwarebytes.msgSetAllowGranular = (callback, {payload}) => {
    let {url, blockUrl, value} = payload;
    const host = urlHost(url.toLowerCase());
    blockUrl = blockUrl.toLowerCase();
    const newExclusions = {
        exclusionIndividual: {
            ...malwarebytes.EXCLUSION_INDIVIDUAL,
            [host]: {
                ...malwarebytes.EXCLUSION_INDIVIDUAL[host],
                [blockUrl]: value,
            },
        },
    };

    chrome.storage.local.set(newExclusions, async () => {
        malwarebytes.EXCLUSION_INDIVIDUAL[host] = newExclusions.exclusionIndividual[host];
        const newExclusionsRule = {
            host,
            tracker: blockUrl,
            priority: DYNAMIC_RULE_INDIVIDUAL_GRANULAR_PRIORITY
        };
        await changeIndividualExclusionMV3(newExclusionsRule, value);
        clearLoggedUrls();
        callback({ value });
    });

    return true;
};

async function changeIndividualExclusionMV3 (rule, changeType) {
    if (!isMV3() || !isValidUrl(rule.tracker)) { // skip features like easlylist, ping tracker etc. handled in other way
        return;
    }
    if (changeType) { // true = allow, false = block
        await exclusionHandler.addIndividualExclusion(rule);
    } else {
        await exclusionHandler.removeIndividualExclusion(rule);
    }
}

/**
 * Appends the heuristic-ad-block item to the ads dictionary if necessary,
 * when heuristic-ad-block is individual excluded, no item is added to the tab info upon page refresh
 * avoiding the user turning it back on. This method fixes it.
 * @param {string} host - The URL's hostname
 * @param {Object} blockedItems - The blocked items that will be sent to the popup page
 * @return {Object} updated blockedItems object
 */
malwarebytes.appendHeuristicAds = (host, blockedItems) => {
    if (!malwarebytes.EXCLUSION_INDIVIDUAL[host]) {
        //No individual exclusions for the host, nothing to fix
        return blockedItems;
    }

    if (!Object.hasOwn(malwarebytes.EXCLUSION_INDIVIDUAL[host], 'heuristic-ad-block')) {
        //heuristic-ad-block has not been excluded, nothing to fix
        return blockedItems;
    }

    if (blockedItems.ads && blockedItems.ads[host] &&
        Object.hasOwn(blockedItems.ads[host], 'heuristic-ad-block')) {
        //Already in the object
        return blockedItems;
    }

    if (!blockedItems.ads) {
        blockedItems.ads = {};
    }
    if (!blockedItems.ads[host]) {
        blockedItems.ads[host] = {};
    }
    blockedItems.ads[host]['heuristic-ad-block'] = 0;
    return blockedItems;
};

