malwarebytes.msgRecordsGet = (callback, request) => {
    if (!request) {
        callback({ error: "Request not supplied for records get" });
        return false;
    }
    console.debug("RecordsGet: recall data using constraints: ", request.payload);
    malwarebytes.recallDetectionData(request.payload ? request.payload.min : null,
        request.payload ? request.payload.max : null).then(function (records) {
        callback({
            success: {
                isPremium: malwarebytes.isPremium(),
                records: records,
                todayKey: malwarebytes.today()
            }
        });
    }, function (err) {
        console.error("MRG: RecordsGet: Failure ", err);
        callback({ error: err });
    });
    return true; //be asynchronous
};

malwarebytes.roll30DaysStats = (key) => {
    const min = Date.today().addDays(-30).toString('MMM d yyyy');
    const max = Date.today().toString('MMM d yyyy');
    malwarebytes.recallDetectionData(min, max).then((res) => {
        if (!malwarebytes.checkValidStatsObject(res)) {
            malwarebytes.ALL_TIME_STATS_LOADED = true;

            if (!malwarebytes.ALL_TIME_STATS.ads) {
                malwarebytes.ALL_TIME_STATS.ads = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.silentAds) {
                malwarebytes.ALL_TIME_STATS.silentAds = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.malwares) {
                malwarebytes.ALL_TIME_STATS.malwares = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.silentMalwares) {
                malwarebytes.ALL_TIME_STATS.silentMalwares = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.scams) {
                malwarebytes.ALL_TIME_STATS.scams = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.silentScams) {
                malwarebytes.ALL_TIME_STATS.silentScams = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.content) {
                malwarebytes.ALL_TIME_STATS.content = 0;
            }
            if (!malwarebytes.ALL_TIME_STATS.silentContent) {
                malwarebytes.ALL_TIME_STATS.silentContent = 0;
            }

            return;
        }
        const recArr = Object.entries(res);
        let totalAds = recArr.reduce((acc, item) => {
            return acc + item[1].ads;
        }, 0);
        let totalSilentAds = recArr.reduce((acc, item) => {
            return acc + item[1].silentAds;
        }, 0);
        let totalMalwares = recArr.reduce((acc, item) => {
            return acc + item[1].malwares;
        }, 0);
        let totalSilentMalwares = recArr.reduce((acc, item) => {
            return acc + item[1].silentMalwares;
        }, 0);
        let totalScams = recArr.reduce((acc, item) => {
            return acc + item[1].scams;
        }, 0);
        let totalSilentScams = recArr.reduce((acc, item) => {
            return acc + item[1].silentScams;
        }, 0);
        let totalContent = recArr.reduce((acc, item) => {
            return acc + item[1].content;
        }, 0);
        let totalSilentContent = recArr.reduce((acc, item) => {
            return acc + item[1].silentContent;
        }, 0);
        const totalData = {
            ads: totalAds,
            malwares: totalMalwares,
            scams: totalScams,
            content: totalContent,
            silentAds: totalSilentAds,
            silentMalwares: totalSilentMalwares,
            silentScams: totalSilentScams,
            silentContent: totalSilentContent
        };
        switch (key) {
            case 'ads':
                console.debug('STATS: Rolling 30 days ads stats');
                malwarebytes.ALL_TIME_STATS.ads = totalData.ads;
                malwarebytes.ALL_TIME_STATS.silentAds = totalData.silentAds;
                break;
            case 'malwares':
                console.debug('STATS: Rolling 30 days malwares stats');
                malwarebytes.ALL_TIME_STATS.malwares = totalData.malwares;
                malwarebytes.ALL_TIME_STATS.silentMalwares = totalData.silentMalwares;
                break;
            case 'scams':
                console.debug('STATS: Rolling 30 days scams stats');
                malwarebytes.ALL_TIME_STATS.scams = totalData.scams;
                malwarebytes.ALL_TIME_STATS.silentScams = totalData.silentScams;
                break;
            case 'content':
                console.debug('STATS: Rolling 30 days content stats');
                malwarebytes.ALL_TIME_STATS.content = totalData.content;
                malwarebytes.ALL_TIME_STATS.silentContent = totalData.silentContent;
                break;
            default:
                console.debug('STATS: Rolling 30 days stats');
                malwarebytes.ALL_TIME_STATS = totalData;
                break;
        }
        malwarebytes.ALL_TIME_STATS_LOADED = true;
        chrome.storage.local.set({ "savedOldUserStats": true });
        chrome.storage.local.set({ "allTimeStats": totalData });
    }).catch((err) => {
        console.error("MRG: RecordsGet: Failure ", err);
        malwarebytes.ALL_TIME_STATS_LOADED = true;
        malwarebytes.ALL_TIME_STATS = {...ALL_TIME_STATS_TEMPLATE};
    });
};

