malwarebytes.msgTabDataGet = (callback, request) => {
    try {
        let tab = malwarebytes.TABS[request.payload]; //Note: payload here should be just a tab id
        if (tab && tab.site) {
            callback({
                success: {
                    "host": tab.site.friendlyUrl || tab.site.host,
                    "blocked": malwarebytes.appendHeuristicAds(tab.site.host, tab.blocked) &&
                        malwarebytes.appendEasylistAds(tab.site.host, tab.blocked) &&
                        malwarebytes.appendAttrReportAds(tab.site.host, tab.blocked) &&
                        malwarebytes.appendPingTracker(tab.site.host, tab.blocked) &&
                        malwarebytes.appendEasylistPrivacy(tab.site.host, tab.blocked),
                    "excluded": malwarebytes.EXCLUSION_INDIVIDUAL[tab.site.host] || {},
                }
            });
        } else {
            callback({ error: "Background replied: No tab data defined for: " + request.payload + "!" });
        }
    } catch (e) {
        callback({ error: "Data get failed. Background replied: " + e.message });
    }
    return false;
};

