malwarebytes.msgAddAllow = (callback, isFromExtension, { payload }, sender) => {
    let isContentShell = false;
    //Content-shell can whitelist only for itself
    if (urlHost(sender.url) == payload.host) {
        payload.allow = EXCLUSION_SCAMS; //Only Scams from content-shell
        isContentShell = true;
    }

    console.debug("AddAllow: Payload: ", { payload, isFromExtension, isContentShell });
    if (isFromExtension || isContentShell) {
        const host = urlHost(payload.host);
        handleExcludedHost(host, payload.allow, payload.fromAllowTab, callback);
    } else {
        callback({ error: "Attempted to call add-allow for a content script" });
    }
    return true;
};

export const handleExcludedHost = async (host, allow, fromAllowTab, callback) => {
    try {
        const resolve = await exclusionHandler.exclude(host, allow)
        console.debug(
            "Exclusions when finishing Add-Allow: ", exclusionHandler.EXCLUSIONS
        );
        clearLoggedUrls();
        if (resolve.error) {
            console.error("MAA: Rejecting AddAllow Message:", resolve.error);
            callback({ error: resolve });
        } else {

            // allow can be a string or an array
            const multiExclusions = Array.isArray(allow) ? allow : allow.split(',');

            for (let i = 0; i < multiExclusions.length; i++) {
                if (multiExclusions[i] !== EXCLUSION_ADS) {
                    malwarebytes.postNativeExclusion(host, true, true);
                }
                // Don't send telemetry if exclusion was added from the allow tab
                if (settings.get('sendTelemetry') !== false && !fromAllowTab) {
                    telemetryExclusion(multiExclusions[i], EXCLUSION_TYPE_DOMAIN_TEMPORARY, host);
                }

                // Reset the exclusion tab counters
                malwarebytes.resetRecordForTabAllowTypeByhost(host, multiExclusions[i]);
            }
            callback(resolve); //Returning the new full merged record (ints)
        }
    } catch (error){
        console.error("Rejecting AddAllow Message: ", error);
        callback({ error });
    }
}

