malwarebytes.logLocalDbInfo = () => {
    return new Promise((res, rej) => {
        chrome.storage.local.get(null, ({ databases, ...rest }) => {
            if (chrome.runtime.lastError) {
                rej(chrome.runtime.lastError.message);
            } else if (databases) {
                let dbVerInfo = Object.keys(databases).map((dbName) => (
                    { [dbName]: databases[dbName].version }
                ));

                res(console.log(JSON.stringify({ dbVerInfo, ...rest })));
            } else {
                res();
            }
        });
    });
};

malwarebytes.msgDownloadLogs = async (callback, fromIDB = false) => {
    const blockedItems = await userBlocksHandler.getBlockedItems();
    const allowedItems = await exclusionHandler.getExclusions();
    const contentControlItems = await malwarebytes.getContentControlItems();
    const browserInfo = await getBrowserInformation();
    console.log("MDL: Browser name and version:", browserInfo.name, browserInfo.version, JSON.stringify(browserInfo));
    console.debug('MDL: Downloading logs');
    console.log("MDL: User settings: ", JSON.stringify(settings));
    console.log("MDL: Blocked items: ", blockedItems);
    console.log("MDL: Allowed items: ", JSON.stringify(allowedItems));
    console.log('MDL: Content Control Items: ', JSON.stringify(contentControlItems));
    console.log('MDL: Feature Flags', JSON.stringify(malwarebytes.FEATURE_FLAGS));
    console.log('MDL: Optional permissions', JSON.stringify(await chrome.permissions.getAll()));
    console.log('MDL: Ungranted permissions', JSON.stringify(await getUngrantedPermissions()));
    let logs = '';
    try {
        await logEnvironmentInfo();
        await logRulesetInfo();
        console.debug('MDL: Getting logs - log local db info');
        await malwarebytes.logLocalDbInfo();
        if (fromIDB) {
            console.debug('MDL: Getting logs from IDB');
            logs = await malwarebytes.logFromIDB();
        } else {
            logs = malwarebytes.SESSION_LOG;
            console.debug('MDL: Getting logs from session', logs);
        }
    } catch (_e) {
        console.debug('MDL: Error getting logs', _e);
        // ignore error, just take what we can get
    }

    try {
        const maxSize = (fromIDB) ? maxLogSize : maxSessionLogSize;
        const fullLog = logs + malwarebytes.CONSOLE_TRACE;

        const clampedFullLog = fullLog.length > maxSize ? fullLog.slice(fullLog.length - maxSize) : fullLog;
        callback({ success: true, data: clampedFullLog });
    } catch (error) {
        console.debug('MDL: Error generating logfile', error);
        callback({ error });
    }
};

