malwarebytes.msgExportSettings = async (config, callback) => {
    try {
        console.debug('MSG_EXPORT: Exporting settings', { config });
        const exportData = {
            version: chrome.runtime.getManifest().version,
        };

        if (config['allow-list'] === true) { // remove exclusions if not exporting
            exportData['allowList'] = await exclusionHandler.getExclusions();
        }

        if (config['blocked-items'] === true) {
            exportData['blockedItems'] = await userBlocksHandler.getBlockedItems();
        }

        if (config['content-control'] === true) {
            exportData['contentControls'] = Array.from(await contentControls.getAll());
        }

        if (config['settings'] === true) {
            let allSettings = await settings.getFromStorage(null);
            allSettings = cleanObjectPropsByTemplate(
                allSettings,
                ALLOWED_EXPORTED_SETTINGS.settings,
                true
            );
            exportData['settings'] = allSettings
        }

        console.debug('MSG_EXPORT: Data to export', { exportData });
        callback({ data: exportData });
    } catch (err) {
        console.error('MSG_EXPORT: Failed to export settings', err);
        callback({ error: err });
    }
};

malwarebytes.msgImportSettings = async (data, callback) => {
    try {
        console.debug('MSG_IMPORT: Importing settings', { data: JSON.stringify(data) });

        if (data.settings) {
            console.debug('MSG_IMPORT: Importing settings', data.settings);
            // merge the settings with the current settings
            const currentSettings = await settings.getFromStorage(null);
            const newSettings = { ...currentSettings, ...data.settings };
            // to avoid importing invalid settings
            const validSettings = cleanObjectPropsByTemplate(
                newSettings,
                ALLOWED_EXPORTED_SETTINGS.settings,
                true
            );
            await settings.setToStorage(validSettings);
        }

        if (data.contentControls) {
            console.debug('MSG_IMPORT: Importing content-control', data.contentControls);
            const contentControlItems = data.contentControls;
            await contentControls.import(contentControlItems);
        }

        if (data.allowList) {
            console.debug('MSG_IMPORT: Importing allow-list', data.allowList);
            const exclusions = data.allowList
            await exclusionHandler.importExclusions(exclusions);
        }

        if (data.blockedItems) {
            if (Array.isArray(data.blockedItems)) {
                console.debug('MSG_IMPORT: Skipping legacy blocked-items import', data.blockedItems);
            } else {
                console.debug('MSG_IMPORT: Importing blocked-items', data.blockedItems);
                const blockedItems = data.blockedItems;
                await userBlocksHandler.importBlockedItems(blockedItems);
                console.debug('MSG_IMPORT: Blocked-items imported', blockedItems);
            }
        }

        callback({ success: true });
    } catch (err) {
        console.error('MSG_IMPORT: Failed to import settings', err);
        callback({ error: err });
    }
};

