malwarebytes.isDetectableScam = (tabUrl, url, tabIdOpt) => {
    if (malwarebytes.isOwnExtensionPage(tabUrl)) {
        console.debug('IDS: Skipped a scam scan for own event page');
        return false;
    }
    const host = urlHost(url);
    if (host && isWhitelistedScamsByIpLiteral(host, localIpWhitelisting)) {
        console.logOnce(url, 'IDS: Skipped a scam scan for ip literal ', url);
        return false;
    }

    if (malwarebytes.isWhitelistedScams(url)) {
        console.debug('IDS: Skipped a scam scan for whitelisted url ', url);
        return false;
    }

    if (malwarebytes.isProtectionActive(EXCLUSION_SCAMS, tabUrl, tabIdOpt)) {
        let isWhitelistedScams = malwarebytes.isWhitelistedScams(tabUrl);
        //NOTE: ONLY FOR DEBUGGING: malwarebytes.zzzzz_repeatResults(isWhitelistedScams, tabUrl, url, tabIdOpt);
        if (isWhitelistedScams) {
            console.debug("IDS: determined should not check scams for " + tabUrl + " because it IS whitelisted");
        } else {
            console.debug("IDS: determined should check scams for " + tabUrl + " because is NOT whitelisted");
        }
        return !isWhitelistedScams;
    } else {
        console.debug("IDS: determined should not check scams for " + tabUrl + " because layer inactive");
    }
    return false;
};

malwarebytes.isExcludedTemporarily = (urlOfTab, tabId) => {
    //temporary exclusions cover ALL types of detection
    if (tabId) {
        if (malwarebytes.EXCLUSION_TEMPORARY && urlOfTab) {
            let hostess = urlHost(urlOfTab);
            if (malwarebytes.EXCLUSION_TEMPORARY.host === hostess) {
                if (tabId == malwarebytes.EXCLUSION_TEMPORARY.tab) {
                    return true;
                } else {
                    console.debug(
                        hostess + " is temporarily excluded for tab " +
                        malwarebytes.EXCLUSION_TEMPORARY.tab + ", but not not for this tab " + tabId
                    );
                }
            }
        }
    } else {
        console.debug("No optional tabId specified for exclusionsMatchSync. Skipping isExcludedTemporary check");
    }
    return false;
};

