malwarebytes.isExcludedIndividuallyMV3 = (details) => {
    if(details.isMainFrame || !isMV3()) { return false; }
    if (!details.url) {
        return false;
    }

    const blockedUrl = urlHost(details.url);

    let initiator;
    if (details.initiator) {
        initiator = details.initiator;
    } else if (details.originUrl) {
        initiator = details.originUrl;
    }
    if (!initiator) {
        return false;
    }

    const tabUrl = urlHost(initiator);

    return malwarebytes.EXCLUSION_INDIVIDUAL[tabUrl]
    && malwarebytes.EXCLUSION_INDIVIDUAL[tabUrl][blockedUrl];
}

malwarebytes.recordMV3ExcludedIndividual = (details) => {
    const tabId = details.tabId;
    if (!details.initiator || !details.url) {
        return;
    }

    const tabUrl = urlHost(details.initiator);
    const blockedUrl = urlHost(details.url);
    malwarebytes.TABS[tabId].blocked.ads = malwarebytes.TABS[tabId].blocked.ads || {};
    malwarebytes.TABS[tabId].blocked.ads[tabUrl] = malwarebytes.TABS[tabId].blocked.ads[tabUrl] || {};
    malwarebytes.TABS[tabId].blocked.ads[tabUrl][blockedUrl] = 0;

}

/**
 * If Global-Killswitch is off, Protection is off (ignoring and disabling all switches)
 * If Temp-Exclusion active, Protection is off (ignoring all switches)
 * If Local is defined, Protection matches it (ignoring settings)
 * Otherwise, Protection matches settings switch
 *
 * K/sw,  Temp,  Local,  Setting,  Result
 * On     Off    Undef   On        On
 * On     Off    Off     On        Off
 * On     Off    On      On        On
 * On     Off    On      Off       On
 *
 * On     Off    Undef   Off       Off
 * On     Off    Off     Off       Off
 * On     On     Undef   On        Off
 * On     On     Undef   Off       Off
 * On     On     On      On        Off
 * On     On     On      Off       Off
 * On     On     Off     Off       Off
 * On     On     Off     On        Off
 * Off    Off    Undef   On        Off
 * Off    Off    Undef   Off       Off
 * Off    Off    On      On        Off
 * Off    Off    On      Off       Off
 * Off    Off    Off     Off       Off
 * Off    Off    Off     On        Off
 * Off    On     Undef   Off       Off
 * Off    On     Undef   On        Off
 * Off    On     On      On        Off
 * Off    On     On      Off       Off
 * Off    On     Off     Off       Off
 * Off    On     Off     On        Off
 *
 * NOTE: This needs to be kept SYNC
 * @param {string} exclusionTypeConst can be EXCLUSION_ADS, EXCLUSION_SCAMS, EXCLUSION_MALWARE
 * @param {string} urlOfTab - tab url
 * @param {string} urlOfAd - unused
 * @param {number} tabIdOpt - optional tab id
 * @return {bool}
 */

malwarebytes.isProtectionActive = (exclusionTypeConst, urlOfTab, tabIdOpt) => {
    //The app originally checked malwarebytes.SETTINGS.enableProtection === false because undefined value
    //  meant on, by default. This is now changed - ALL PROTECTION STARTS SWITCHED OFF.
    //  after the user is assigned to a group, we switch back on as necessary
    if (settings.get('enableProtection') !== true) {
        console.log("IPA: Determined false - killswitch active (value " + settings.get('enableProtection') + ")");
        return false;
    }
    if (malwarebytes.isExcludedTemporarily(urlOfTab, tabIdOpt)) {
        console.log("IPA: Determined false - temporary exclusion active");
        return false;
    }
    //NOTE: This tells us if there is a value, NOT what the value is
    //It appears this line always returns true on Mac-Firefox, because of previous-install's sync data - it should be wiped on install
    let isSwitchManuallyOverridden = malwarebytes.isDomainValueOverriddenSync(
        exclusionTypeConst, urlOfTab, exclusionHandler.EXCLUSIONS
    );
    switch (exclusionTypeConst) {
        case EXCLUSION_ADS:
            //NOTE: This case is rarely used as ADS relies on meta values (pre-loaded) to be fast
            //   this case wis probably called just for that pre-load
            return malwarebytes.isProtectionActive_determination(
                EXCLUSION_ADS,
                settings.get('enableProtectionAds'),
                urlOfTab,
                isSwitchManuallyOverridden
            );
        case EXCLUSION_SCAMS:
            //NOTE: Replaces: malwarebytes.SETTINGS.enableProtection !== false && malwarebytes.SETTINGS.enableProtectionScams !== false
            return malwarebytes.isProtectionActive_determination(
                EXCLUSION_SCAMS,
                settings.get('enableProtectionScams'),
                urlOfTab,
                isSwitchManuallyOverridden
            );
        case EXCLUSION_MALWARE:
            //NOTE: Replaces malwarebytes.SETTINGS.enableProtection !== false && malwarebytes.SETTINGS.enableProtectionMalware !== false
            // Now isOverridden? check override value :otherwise: check settings
            return malwarebytes.isProtectionActive_determination(
                EXCLUSION_MALWARE,
                settings.get('enableProtectionMalware'),
                urlOfTab,
                isSwitchManuallyOverridden
            );
        default:
            console.error("Unhandled case '" + exclusionTypeConst + "' in isProtectionActive");
            return true; //default state
    }
};

malwarebytes.isProtectionActive_determination = (
    exclusionConst,
    settingValue,
    urlOfTab,
    isSwitchManuallyOverridden
) => {
    let isExcludedForDomain = false;
    let isExcludedForFullUrl = false;
    let result = true;
    if (isSwitchManuallyOverridden) {
        isExcludedForDomain = malwarebytes.isExcludedForDomain(exclusionConst, urlOfTab);
        result = !isExcludedForDomain;
    } else {
        result = settingValue !== false;
    }
    if (result) {
        isExcludedForFullUrl = malwarebytes.isExcludedForFullUrl(exclusionConst, urlOfTab);
        if (isExcludedForFullUrl) {
            console.log("IPA excluded " + urlOfTab + " by full url");
        }
        result = !isExcludedForFullUrl;
    }

    return result;
};

/**
 * Return TRUE if website switch was toggled EITHER On or Off manually
 * FALSE if non-manual/undefined (falling through to settings value)
 * @param {string} exclConst - EXCLUSION_ADS, EXCLUSION_SCAMS, EXCLUSION_MALWARE
 * @param {string} urlOfHostTab - tab url
 * @return {Promise}
 */
malwarebytes.isDomainValueOverridden = (exclConst, urlOfHostTab) => {
    return malwarebytes.getExclusions().then((exclusions) => {
        return Promise.resolve(malwarebytes.isDomainValueOverriddenSync(exclConst, urlOfHostTab, exclusions));
    }).catch((eMsg) => {
        return Promise.reject(eMsg);
    });
};

/**
 * Returns TRUE when the WEBSITE tab switch has been manually set by a user
 * - EVEN IF SET TO FALSE.
 *
 * A True from this method means "NOT UNDEFINED"
 * @param {string} exclConst - EXCLUSION_ADS, EXCLUSION_SCAMS, EXCLUSION_MALWARE
 * @param {string} urlOfHostTab - tab url
 * @param {{}} allExclusions - all exclusions
 * @return {bool}
 */
malwarebytes.isDomainValueOverriddenSync = (exclConst, urlOfHostTab, allExclusions) => {
    if (!urlOfHostTab) {
        return false;
    }
    let host;
    try {
        host = urlHost(urlOfHostTab);
    } catch (e) {
        console.debug("DVO: Failed to call urlHost on " + urlOfHostTab, e);
        host = urlOfHostTab;
    }

    let strippedDomain;
    if (host.startsWith("www.")) {
        strippedDomain = host.substring(4);
    }
    let overrides;
    if (strippedDomain) {
        overrides = allExclusions[strippedDomain] || allExclusions[host]; //malwarebytes.EXCLUSIONS[host];
    } else {
        overrides = allExclusions[host];
    }
    let exclInt = ExclusionHandler.exclusionConstantToInt(exclConst);
    let result = false;
    if (overrides) {
        if (!Array.isArray(overrides)) {
            overrides = [overrides];
        }

        overrides.forEach((excl) => {
            if (exclInt === excl || exclInt === (-1 * excl)) {
                result = true;
            }
        });
    }

    return result;
};

malwarebytes.isExcludedForFullUrl = (category, urlOfTab) => {
    let categoryInt = ExclusionHandler.exclusionConstantToInt(category);
    for (let allowedWebsite in exclusionHandler.EXCLUSIONS) {
        if (Object.hasOwn(exclusionHandler.EXCLUSIONS, allowedWebsite)) {
            //note: every exclusion is by ExclusionsTypeURL, the rest were never used
            if (
                malwarebytes.isFullUrlMatch(allowedWebsite, urlOfTab) &&
                exclusionHandler.EXCLUSIONS[allowedWebsite] &&
                Array.isArray(exclusionHandler.EXCLUSIONS[allowedWebsite])
            ) {
                if (exclusionHandler.EXCLUSIONS[allowedWebsite].includes(categoryInt)) {
                    console.debug("IFU: " + allowedWebsite + " is excluded for full url");
                    return true;
                }
            }
        }
    }
    return false;
};

malwarebytes.isFullUrlMatch = (allowedWebsite, currentUrlOfTab) => {
    if (!allowedWebsite || !currentUrlOfTab) {
        return false;
    }
    let cleanAllowedWebsite = malwarebytes.cleanHost(allowedWebsite);
    let cleanUrlOfTab = malwarebytes.cleanHost(currentUrlOfTab);
    console.debug("IFU: Comparing [exclusion] " + cleanAllowedWebsite + " to [tab] " + cleanUrlOfTab);
    return cleanUrlOfTab ? cleanUrlOfTab.toLowerCase() === cleanAllowedWebsite.toLowerCase() : false;
};

malwarebytes.cleanHost = (dirtyHost) => {
    return dirtyHost.toLowerCase()
        .replace(/[<>]/g, "") //get rid of brackets
        .replace(/http[s]?:\/\//g, "") //get rid of protocol
        .replace("www.", "") //get rid of redundant subdomain
        .replace(/\/$/, "").trim() //get rid of trailing slash
        // get rid of port
        .replace(/:\d+$/, "");
};

malwarebytes.isExcludedForDomain = (category, urlOfTab) => {
    let categoryInt = ExclusionHandler.exclusionConstantToInt(category);
    let domains = urlDomains(urlOfTab);
    const exclusionsSnapshot = exclusionHandler.getExclusionsSnapshot();
    for (let key in exclusionsSnapshot) {
        if (Object.hasOwn(exclusionsSnapshot, key)) {
            //note: every exclusion is by ExclusionsTypeURL, the rest were never used
            if (
                malwarebytes.isKeyMatch(key, urlOfTab, domains) &&
                exclusionsSnapshot[key] && Array.isArray(exclusionsSnapshot[key])
            ) {
                if (exclusionsSnapshot[key].includes(categoryInt)) {
                    console.debug(`IEFD: ${key} is excluded for domain`);
                    return true;
                }
            }
        }
    }
    console.debug("IEFD: " + urlOfTab + " is NOT excluded for domain", category);
    return false;
};

malwarebytes.isOverriddenForDomain = (category, urlOfTab) => {
    return new Promise((resolve) => {
        let categoryInt = ExclusionHandler.exclusionConstantToInt(category);
        console.debug(
            "OFD: Looking for " + categoryInt + " in exclusions:",
            JSON.stringify(exclusionHandler.EXCLUSIONS)
        );
        let domains = urlDomains(urlOfTab);
        let exclusions = exclusionHandler.EXCLUSIONS;
        for (let key in exclusions) {
            if (Object.hasOwn(exclusions, key)) {
                console.debug("OFD: Found the domain " + key + ":", exclusions[key]);
                //note: every exclusion is by ExclusionsTypeURL, the rest were never used
                if (
                    malwarebytes.isKeyMatch(key, urlOfTab, domains) &&
                    exclusions[key] &&
                    Array.isArray(exclusions[key])
                ) {
                    if (exclusions[key].includes(categoryInt) ||
                        exclusions[key].includes(categoryInt * -1)) {
                        console.debug("OFD: " + key + " is overridden for domain");
                        return resolve(true);
                    }
                }
            }
        }
        return resolve(false);
    });
};

malwarebytes.isKeyMatch = function (key, url, domains) {
    console.debug(`IKM: Checking if ${key} is in domains for ${url}`);
    console.debug('IKM: Domains to check: ', domains);
    if (key.startsWith("www.")) {
        key = key.substring(4);
    }
    if (url.startsWith("https://")) {
        url = url.substring(8);
    }
    if (url.startsWith("http://")) {
        url = url.substring(7);
    }
    if (url.startsWith("www.")) {
        url = url.substring(4);
    }
    if (domains) {
        if (domains.find((dom) => {
            if (key === dom) {
                return true;
            }
        })) {
            return true;
        }
    }
    return key === url;
};

malwarebytes.isWhitelisted = (url, whitelistDb, typeName) => {
    if (!whitelistDb || !whitelistDb.cleanName) {
        console.error(`IWL: Missing whitelist database for ${typeName}`);
        return false;
    }
    const hash = fastHash(url + typeName + whitelistDb.cleanName);
    if (malwarebytes.WHITELISTED[hash]) {
        return malwarebytes.WHITELISTED[hash];
    }
    // console.debug(`IWLM: Checking if ${url} is whitelisted
    //     in ${whitelistDb.name} v:${whitelistDb.version} for ${typeName}`, { url });
    let invalidState = !whitelistDb || !whitelistDb.data || !whitelistDb.data.ready();
    let match = false;
    if (invalidState) {
        console.log(`IWL: ${typeName} wl in invalid state. Whitelisting by default.`);
    } else {
        let domains = urlDomains(url);
        match = matchesAny(domains, whitelistDb);
        if (match) {
            console.logOnce(url,
                `IWL: ${typeName} wl domain matches ${url}. Entry is whitelisted
                in ${whitelistDb.name} v:${whitelistDb.version} . Domains checked: `,
                domains
            );
        }
    }
    const result = invalidState || match;
    malwarebytes.WHITELISTED[hash] = result;
    return result;
};

malwarebytes.isWhitelistedMalware = (url) => {
    if (!url) {
        return false;
    }

    const wlMalwareDb = DatabaseManager.getInstance().getCachedDatabaseByName('whitelist_malware');
    return malwarebytes.isWhitelisted(url, wlMalwareDb, "Malware");
};

malwarebytes.isWhitelistedScamsByPattern = (url) => {
    const databaseManager = DatabaseManager.getInstance();
    const wlScamsPatternsDb = databaseManager.getCachedDatabaseByName('whitelist_scams_patterns');
    if (wlScamsPatternsDb) {
        return wlScamsPatternsDb.data.some((pattern) => {
            if (pattern.test(url)) {
                console.logOnce(url, `IWL: Scams wl pattern matched. ${url} is whitelisted`);
                return true;
            }
        });
    } else {
        console.error(new Error("Missing whitelist scams database!"));
    }
};

malwarebytes.isWhitelistedScams = (url) => {
    let host = urlHost(url);
    if (host === "localhost") {
        console.logOnce(url, `IWS: IS whitelisted because host is localhost for ${url}`);
        return true;
    }
    if (malwarebytes.isWhitelistedScamsByPattern(url)) {
        console.logOnce(url, `IWS: IS whitelisted by pattern for ${url}`);
        return true;
    }
    if (isWhitelistedScamsByIpLiteral(host, localIpWhitelisting)) {
        console.logOnce(url, `IWS: IS whitelisted by IP for ${url}`);
        return true;
    }

    const databaseManager = DatabaseManager.getInstance();
    const wlScamsManualDb = databaseManager.getCachedDatabaseByName('whitelist_scams_manual');
    const top1mDb = databaseManager.getCachedDatabaseByName('top1m');
    return !!( malwarebytes.isWhitelisted(url, wlScamsManualDb, "Scams")
            || malwarebytes.isWhitelisted(url, top1mDb, "Scams"));
};

malwarebytes.isWhitelistedAds = (url) => {
    if (!url) {
        return false;
    }
    const wlAdsDb = DatabaseManager.getInstance().getCachedDatabaseByName('whitelist_ads');
    return malwarebytes.isWhitelisted(url, wlAdsDb, "Ads");
};

malwarebytes.queryFeatureFlag = (key) => {
    return ({ isEnabled: malwarebytes.FEATURE_FLAGS[key] });
};

malwarebytes.msgGetHeuristicDatabase = async (host) => {
    const databaseManager = DatabaseManager.getInstance();
    let data = {};
    if (malwarebytes.FEATURE_FLAGS[FLAG_ENABLE_HEURISTIC_BLOCKING] === true) {
        data = await databaseManager.getDatabaseByName('heuristics', settings) || {};

        const list = await databaseManager.getDatabaseByName('whitelist_tracker', settings);
        for (let entry of list.data) {
            if (entry.tracker === "heuristics" && entry.host == host) {
                data = {};
                break;
            } else if (entry.tracker.startsWith("heuristics|") && entry.host == host) {
                data = data.filter((item) => item.r.includes(entry.tracker.split("|")[1]));
            }
        }

    }
    return data;
};

// we may need host and url information later to use exceptions
malwarebytes.msgEasylistElementsToHide = async (host) => {
    const databaseManager = DatabaseManager.getInstance();

    let emptyResult = await getEmptyEasylistItemsElement();
    const easylistDb = await databaseManager.getDatabaseByName('easylist', settings);
    if (host === "" || easylistDb === undefined) { // there is an edge case where the database is not loaded yet
        return emptyResult;
    }

    host = cleanHostname(host);
    // retrieve all specific hide rules for the host
    let domains = urlDomains(host);
    let result = getEmptyEasylistItemsElement();

    let clickables = [
        {'item': 'button#onetrust-reject-all-handler', 'action':'click'},
        {'item': '#onetrust-close-btn-container > button', 'action': 'click'},
        {'item': 'button.ot-pc-refuse-all-handler', 'action': 'click'},
    ];

    const easylistDatabases = await databaseManager.getDatabases(settings, ['easylist', 'easylistprivacy']);

    [
        {name:'easylist', result:result.easylist},
        {name:'easylistprivacy', result:result.easylistprivacy}
    ].forEach((param) => {
        domains.forEach((domain) => {
            let tmp = easylistDatabases[param.name].specific_hide[domain];
            if (tmp) {
                param.result.specific_hide = param.result.specific_hide.concat(tmp);
            }
            tmp = easylistDatabases[param.name].exception_rules[domain];
            if (tmp) {
                param.result.exception_rules = param.result.exception_rules.concat(tmp);
            }
            tmp = easylistDatabases[param.name].extended_hide[domain];
            if (tmp) {
                param.result.extended_hide = [...param.result.extended_hide,...tmp];
            }
        });
    });

    const whitelistTrackerDb = await databaseManager.getDatabaseByName('whitelist_tracker', settings);
    if (whitelistTrackerDb) {
        const list = whitelistTrackerDb.data;
        for (let entry of list) {
            const elEnabled = entry && entry.host && entry.host === host && entry.tracker.startsWith("easylist"); // easylistEnabled
            const htenabled = entry && entry.host && entry.host === host && entry.tracker === "heuristics";
            if (elEnabled || htenabled) {
                // a tracker could be just "easylist" or specifically "easylist|.content-ads"
                if (entry.tracker === "easylist" || entry.tracker === "heuristics") {
                    // if it is just easylist or a heuristic whitelisting return the default
                    console.debug("MEETH: Site is whitelisted by a 'whitelist_tracker' entry for " + host);
                    return emptyResult;
                } else {
                    // get the filter after the pipe sign
                    const parts = entry.tracker.split("|");
                    if (parts.length == 2) {
                        // eslint-disable-next-line max-len
                        console.debug(`MEETH: Site is whitelisted by a 'whitelist_tracker' entry for [${host}:${parts[1]}]`);
                        const filterList = parts[1].split(",");
                        [
                            {name:'easylist', result:result.easylist},
                            {name:'easylistprivacy', result:result.easylistprivacy}
                        ].forEach((param) => {
                            param.result.ids = easylistDatabases[param.name].ids.filter((id) => {
                                return !filterList.includes(id);
                            });
                            param.result.classes = easylistDatabases[param.name].classes.filter((cls) => {
                                return !filterList.includes(cls);
                            });
                            param.result.tags = easylistDatabases[param.name].tags.filter((tag) => {
                                return !filterList.includes(tag);
                            });
                            param.result.selectors = easylistDatabases[param.name].selectors.filter((sel) => {
                                return !filterList.includes(sel);
                            });
                        });
                        return result;
                    }
                }
            }
        }
    }

    // we will add this functionality later with BG-2226 Heuristic database rules
    if (host === "nytimes.com") {
        clickables = clickables.concat([{'item': 'button.fides-reject-all-button', 'action': 'click'}]);
    }
    result.easylist.clickables = clickables;

    return {'easylist': {
        'ids': easylistDatabases.easylist.ids,
        'classes': easylistDatabases.easylist.classes,
        'tags': easylistDatabases.easylist.tags,
        'selectors': easylistDatabases.easylist.selectors,
        'extended_hide': result.easylist.extended_hide,
        'exception_rules': result.easylist.exception_rules,
        'specific_hide': result.easylist.specific_hide,
        'clickables': clickables
    },
    'easylistprivacy': {
        'ids': easylistDatabases.easylistprivacy.ids,
        'classes': easylistDatabases.easylistprivacy.classes,
        'tags': easylistDatabases.easylistprivacy.tags,
        'selectors': easylistDatabases.easylistprivacy.selectors,
        'extended_hide': result.easylistprivacy.extended_hide,
        'exception_rules': result.easylistprivacy.exception_rules,
        'specific_hide': result.easylistprivacy.specific_hide,
        'clickables': []
    }};
};

//////////////////// TAB/CONTENT LISTENING /////////////////////////////////////////////////////////////////////////////

/**
 * Adds a new item to TABS, if it is not already there
 */
malwarebytes.tryNewTab = ({ tabId, url, sld, domains }) => {
    if (tabId && tabId > -1) {
        if (!malwarebytes.TABS[tabId]) {
            onNewTab(tabId, url, sld, domains);
        } else {
            let host = urlHost(url);
            if (malwarebytes.TABS[tabId].site.host !== host) {
                onNewTab(tabId, url, sld, domains);
            }
        }
    }
};


/**
 * Called when a tab starts loading the top level of a page
 *
 * @param {number} tabId - ID of the tab that was updated
 * @param {{status: string, url: string}} changeInfo - Contains properties for the tab properties that have changed
 * @param {{url: string}} tab - The new state of the tab (additional values passed are irrelevant)
 *
 */
malwarebytes.tabsOnUpdateLoading = (tabId, changeInfo, tab) => {
    if (malwarebytes.TABS[tabId] && malwarebytes.TABS[tabId].status === 'loading') {
        return;
    }

    if (changeInfo.url) {
        if (!changeInfo.url.startsWith("chrome://")) {
            onNewTab(
                tabId,
                changeInfo.url,
                domain(changeInfo.url),
                urlDomains(changeInfo.url)
            );
        }
    } else {
        // Catch when refresh is occuring to reset counters
        onNewTab(
            tabId,
            tab.url,
            domain(tab.url),
            urlDomains(tab.url),
        );
    }
};

/**
 * Called when a tab finishes loading the top level of a page
 * @param {number} tabId ID of the tab that was updated
 */
malwarebytes.tabsOnUpdateComplete = (tabId) => {
    //the temp exclusion was only good for the one load
    //NOTE: I've actually decided to keep temporary as long as tab lives - because content-tss needs it, ongoing
    //The page continued loading, good time to sync our DB data with the newly updated CACHE data
    malwarebytes.syncDetectionData();
    malwarebytes.syncAllStatsData();
    malwarebytes.iconRefresh(tabId);
    malwarebytes.keepTabInSync(tabId);
};

malwarebytes.keepTabInSync = (tabId) => {
    if (tabId >= 0 && malwarebytes.TABS[tabId]) {
        // Flagging as complete so we do not accidently overwrite before we are are ready
        // See malwarebytes.tabsOnUpdateLoading
        malwarebytes.TABS[tabId].status = 'complete';
        // Ensure we are in sync
        (chrome.action || chrome.browserAction).getBadgeText({ tabId }, (text) => {
            if (!text) {
                return;
            }

            const totalBlocked = Object.values(
                malwarebytes.TABS[tabId].blocked.totals
            ).reduce((accum, cv) => accum + cv, 0);

            if (
                totalBlocked !== Number(text) &&
                malwarebytes.TABS[tabId].blockedHistory
            ) {
                // revert back to the history because we overwrote it
                malwarebytes.TABS[tabId].blocked = JSON.parse(
                    JSON.stringify(malwarebytes.TABS[tabId].blockedHistory)
                );
            }
        });
    }
};

malwarebytes.tabsOnRemoved = (tabId, info) => {
    console.debug("Chrome: onRemoved(tabId, info): ", info);
    // regurlarly purge the cache
    if (malwarebytes.SAFE_CACHE[tabId]) {
        malwarebytes.SAFE_CACHE[tabId] = { ...malwarebytes.SAFE_CACHE[tabId] };
        delete malwarebytes.SAFE_CACHE[tabId];
    }
    if (malwarebytes.TAB_MALWARE_NOTIFIED[tabId]) {
        delete malwarebytes.TAB_MALWARE_NOTIFIED[tabId];
    }
    if (malwarebytes.EXCLUSION_TEMPORARY && malwarebytes.EXCLUSION_TEMPORARY.tab === tabId) {
        delete malwarebytes.EXCLUSION_TEMPORARY;
    }
};

malwarebytes.iconRefreshAll = () => {
    chrome.tabs.query({}, (tabs) => {
        for (const tab of tabs) {
            malwarebytes.iconRefresh(tab.id);
        }
    });
};

malwarebytes.iconRefresh = (tabId) => {
    if (tabId) {
        chrome.tabs.get(tabId, async (tab) => {
            if (!chrome.runtime.lastError) {
                //NOTE: enable protections is now FALSE by default (while undefined). See note in isProtectionActive
                let isProtectionEnabled = await settings.getFromStorage("enableProtection") === true;
                //NOTE: Show disabled if all disabled - probably need the other 3
                let isAnyLayerActive = false;
                if (isProtectionEnabled) {
                    if (!tab.url) {
                        return;
                    }
                    // isAnyLayerActive can only be true if isProtectionEnabled is true
                    isAnyLayerActive =
                        malwarebytes.isProtectionActive(EXCLUSION_MALWARE, tab.url, tabId) ||
                        malwarebytes.isProtectionActive(EXCLUSION_SCAMS, tab.url, tabId) ||
                        malwarebytes.isProtectionActive(EXCLUSION_ADS, tab.url, tabId);
                }
                malwarebytes.setBGIcon(tabId, isAnyLayerActive);
            }
        });
    } else {
        console.debug("Icon refresh - null tabId. Having to refresh-all instead");
        malwarebytes.iconRefreshAll();
    }
};

// Temporarily cache safe sites
malwarebytes.cacheSafeness = (tabID, url) => {
    if (malwarebytes.SAFE_CACHE[tabID]) {
        malwarebytes.SAFE_CACHE[tabID] = { ...malwarebytes.SAFE_CACHE[tabID], [url]: true };
    } else {
        malwarebytes.SAFE_CACHE[tabID] = { [url]: true };
    }
};

malwarebytes.uncacheSafeness = (tabId, url) => {
    if (!malwarebytes.SAFE_CACHE[tabId]) {
        return;
    }

    let cache = malwarebytes.SAFE_CACHE[tabId];
    if (url in cache) {
        delete cache[url];
    }
    malwarebytes.SAFE_CACHE[tabId] = cache;
};

malwarebytes.isOwnExtensionPage = (tabUrl, { url, originUrl } = {}) =>
    [tabUrl, url].some(
        (u = "") => u.startsWith(chromePagePath) ||
            u.startsWith(mozPagePath) ||
            u.startsWith(mozAddonPagePath) ||
            u.startsWith(edgeAddonPagePath) ||
            u.startsWith(edgePagePath)
    ) ||
    (originUrl && originUrl.startsWith(mozPagePath));

malwarebytes.handleDebugUrls = (details) => {
    // Debug SECTION

    if (details.url === 'https://www.malwarebytes.com/browserguard/download-debug-logs?yes=true') {
        console.debug('BTW: Hit redirect to download URL');
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/downloading.html') };
    }

    // speed hack to return early without checking the rest of the URLs
    if (!details.url.startsWith('https://www.malwarebytes.com/browserguard/debugger?action=')) {
        return null;
    }
    const params = new URL(details.url).searchParams;
    if (!params.has("action")) {
        return null;
    }
    const action = params.get('action');

    if (action === 'user-group-a') {
        console.debug('BTW: Switching to User Group A');
        malwarebytes.debugUserGroupA();
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=user-group-a') };
    }

    if (action === 'my-data') {
        console.debug('BTW: Logging my debug data');
        malwarebytes.debugMyData();
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=my-data') };
    }

    if (action === 'full-stats') {
        console.debug('BTW: Creating full stats');
        malwarebytes.debugStatsFull();
        malwarebytes.ALL_TIME_STATS_LOADED = false;
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=full-stats') };
    }

    if (action === 'factory-reset') {
        console.debug('BTW: Running factory reset');
        malwarebytes.resetExtension();
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=factory-reset') };
    }

    if (action === 'reset-monthly-notification-date') {
        chrome.storage.local.set({ lastMonthlyNotification: "1970-01-01T00:00:00" });
        console.debug('BTW: Debug action, monthly notification start date reset');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=reset-monthly-notification-date'
            )
        };
    }

    if (action === 'trigger-monthly-notification') {
        console.debug('BTW: Monthly notification display triggered');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-monthly-notification'
            )
        };
    }

    if (action === 'trigger-search-hijacking-notification') {
        console.debug('BTW: Monthly notification display triggered');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-search-hijacking-notification'
            )
        };
    }

    if (action === 'trigger-release-notification') {
        console.debug('BTW: Release notification display triggered');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-release-notification'
            )
        };
    }
    if (action === 'trigger-release-notification-notes') {
        console.debug('BTW: Release notification (with release notes) display triggered');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-release-notification-notes'
            )
        };
    }
    if (action === 'get-dbs-info') {
        console.debug('BTW: Getting DBs information');
        const fullParam = new URL(details.url).search;
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html' + fullParam
            )
        };
    }

    if (action === 'trigger-shell-notification') {
        console.debug('BTW: Shell notification display triggered');
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-shell-notification'
            )
        };
    }

    if (action === 'toggle-reputation') {
        console.debug('BTW: Toggling Reputation DB state');
        const repState = malwarebytes.debugToggleRepuationDB() ? 'on' : 'off';
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=toggle-reputation-${repState}`
            )
        };
    }

    if (action === 'toggle-local-ip-allow') {
        console.debug('BTW: Toggling Local IP Allow');
        const allowState = malwarebytes.debugToggleLocalIpWhiteListing() ? 'on' : 'off';
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=toggle-local-ip-allow-${allowState}`
            )
        };
    }

    if (action === 'populate-detection-history') {
        console.debug('BTW: Populating Detection History');
        malwarebytes.debugDetectionHistory();
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=populate-detection-history') };
    }

    if (action === 'test-channel-update') {
        console.debug('BTW: Test Channel Update Page');
        return {
            redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=test-channel-update')
        };
    }

    if (action === 'staging-channel-update') {
        console.debug('BTW: Staging Channel Update Page');
        return {
            redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=staging-channel-update')
        };
    }

    if (action === 'send-test-user-telemetry') {
        malwarebytes.sendTestUserActionTelemetry(false);
        return {
            redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=send-test-user-telemetry')
        };
    }

    if (action === 'get-dynamic-rules') {
        return {
            redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=get-dynamic-rules')
        };
    }

    const databaseManager = DatabaseManager.getInstance();

    if (action === 'set-feature') {
        // documentation is here: https://malwarebytes.atlassian.net/wiki/spaces/ENGR/pages/2661024065/BG+Feature+Flags#Setting-an-individual-feature-using-the-debug-URL
        if (!(params.has("key") && params.has("value"))) {
            return null;
        }
        const key = params.get('key');
        const value = JSON.parse(params.get('value')); // to "smart guess" the value i.e. boolean or integer, see documentation
        console.debug(`BTW: Set the feature ${key} to ${value} (it was ${malwarebytes.FEATURE_FLAGS[key]})`);
        malwarebytes.FEATURE_FLAGS[key] = value;
        const featureFlagsDb = databaseManager.getCachedDatabaseByName('featureflags');
        const featureFlagsDbData = featureFlagsDb.data;
        featureFlagsDbData[key] = value;
        databaseManager.updateDatabase('featureflags', {setData: featureFlagsDbData});

        if (key === FLAG_ENABLE_BLOCK_GDPR) {
            setTimeout( async () => {
                await loadEasylist();
            }, 100);
        }
        if (key === FLAG_ENABLE_HEURISTIC_BLOCKING) {
            setTimeout( async () => {
                try {
                    const heuristicsJsonDb = databaseManager.getCachedDatabaseByName('heuristics_json');
                    const content = heuristicsJsonDb.data;
                    databaseManager.setDatabase('heuristics', await processHeuristics(content));
                } catch (error) {
                    console.error("ID: Error processing heuristics database", error);
                }
            }, 100);
        }
        return { redirectUrl: chrome.runtime.getURL(`app/eventpages/debugger.html?${params.toString()}`) };
    }

    const whitelistMalwareDb = databaseManager.getCachedDatabaseByName('whitelist_malware');
    if (action === 'whitelist-malware-for-browserguard-local') {
        if (!matchesAny(['browserguard.local'], whitelistMalwareDb)) {
            // whitelist_malware.data is an IndexedDatabase instance, so we can add a record this way
            whitelistMalwareDb.data.add('browserguard.local');
        }
        console.debug('BTW: Whitelisted browserguard.local for malware');
        // eslint-disable-next-line max-len
        return { redirectUrl: chrome.runtime.getURL('app/eventpages/debugger.html?action=whitelist-malware-for-browserguard-local') };
    }

    if (action === 'override-ads-telemetry-duration') {
        const key = params.get('key');
        const periodInMinutes = JSON.parse(params.get('value'));

        if (!(key === 'periodInMinutes' && !isNaN(periodInMinutes)) ) {
            return null;
        }

        malwarebytes.overrideAdTelemAlarm(periodInMinutes);
        console.log('BTW: Override sending ads telemetry duration');

        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=override-ads-telemetry-duration'
            )
        };
    }

    if (action === 'trigger-sending-block-telemetry-now') {
        pushBlockTelem();
        console.log('BTW: Triggered sending block telemetry now');

        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=trigger-sending-block-telemetry-now'
            )
        };
    }

    if (action === 'check-url') {
        const domain = params.get('q');
        if (domain) {
            return {
                redirectUrl: chrome.runtime.getURL(
                    `app/eventpages/debugger.html?action=check-url&q=${domain}`
                )
            };
        }
    }

    if (action === 'add-heuristic') {
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=add-heuristic`
            )
        };
    }

    if (action === "enable-visual-debugging") {
        malwarebytes.enableVisualDebugging();
        return {
            redirectUrl: chrome.runtime.getURL(
                'app/eventpages/debugger.html?action=enable-visual-debugging'
            )
        };
    }

    if (action === "reset-breach-alert-last-displayed") {
        const threeDaysAgo = new Date() - 1000 * 60 * 60 * 24 * 3;
        setBreachLastShow(threeDaysAgo);
        // to make the random number is within the DB
        const example = breachesDB[Math.floor(Math.random() * breachesDB.length)];
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=${action}&date=${threeDaysAgo}&example=${example.site}`
            )
        };
    }

    if (action === "permission-requests") {
        const operation = params.get('operation');
        const permission = params.get('permission');
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=${action}&operation=${operation}&permission=${permission}`
            )
        };
    }

    if (action === "simulate-cloud-message-response") {
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=${action}`
            )
        };
    }

    if (action === "fp-checker-test") {
        return {
            redirectUrl: chrome.runtime.getURL(
                `app/eventpages/debugger.html?action=${action}`
            )
        };
    }

    return null;
};

malwarebytes.enableVisualDebugging = () => {
    settings.set('visualDebugging', true);
}

/**
 * Called when chrome is about to send any request (load a resource, page, form, etc.)
 * @param {Object} details - https://developer.mozilla.org/en-US/docs/Mozilla/Add-ons/WebExtensions/API/webRequest/onBeforeRequest#details
 * @return {{cancel: bool, redirectUrl: string}} - Should return whether to block the request in the format: return { cancel: false };
 */
malwarebytes.onBeforeTabWebRequest = (details) => {
    const databaseManager = DatabaseManager.getInstance();

    if (isMV3() && !details.domains) {
        const domains = urlDomains(details.url) || [];
        Object.defineProperty(details, 'domains', { value: domains, writable: true, configurable: true });
    }

    // skip local urls
    if (details.url.startsWith('file://')) {
        return { cancel: false };
    }

    // skip youtube live emojis
    if (details.url.startsWith('https://yt3.ggpht.com') || (details.url.startsWith('https://www.youtube.com') && details.url.endsWith('.svg'))) {
        return { cancel: false };
    }

    //IF the host of the tab is the TEMPORARY exclusion, we don't check anything at all
    //But after all requests finish (onCompletion) we have to clear the value again
    if (details.tabId >= 0 && malwarebytes.TABS[details.tabId] && malwarebytes.TABS[details.tabId].site){
        let tabUrl = malwarebytes.TABS[details.tabId].site.url || "";

        if (tabUrl && tabUrl.startsWith("file://")) {
            console.debug("BTW: Skipping all scans for local-file");
        } else if (malwarebytes.isOwnExtensionPage(tabUrl, details)) {
            console.debug(
                // eslint-disable-next-line max-len
                `BTW: Skipping all scans for own extension event page (tabURL: ${tabUrl} | detailsURL: ${details.url} | originURL: ${details.originUrl})`
            );
        } else {
            //Check the temp exclusion status of the main frame (to level)
            if (malwarebytes.isExcludedTemporarily(malwarebytes.TABS[details.tabId].site.url, details.tabId)) {
                console.log("BTW: Excluded Temporary: " + malwarebytes.TABS[details.tabId].site.url);
                if (details.isMainFrame) {
                    const urlHostname = urlHost(details.url);
                    urlHost(malwarebytes.TABS[details.tabId].site.url) === urlHostname &&
                        malwarebytes.postNativeExclusion(urlHostname, true);
                }
                return { cancel: false };
            }

            //Also check the exclusion status for this particular request - it only does work when (any) temp excl is
            // set and it applies to this tab, so efficiency isn't too much of a concern yet
            if (malwarebytes.isExcludedTemporarily(details.url, details.tabId)) {
                console.log("BTW: Excluded Temporary: " + details.url);
                return { cancel: false };
            }


            //NOTE: (presumably) we check for a settings object because we do not want to block in case we dont know if scan is switched off
            if (settings) {
                if (
                    malwarebytes.SAFE_CACHE[details.tabId] &&
                    malwarebytes.SAFE_CACHE[details.tabId][details.url && details.url.split('?')[0]]
                ) {
                    return {};
                }

                if (isUserBlockedItem(tabUrl, details.url)) {
                    let tabHistory = malwarebytes.TABS[details.tabId] ?
                        malwarebytes.TABS[details.tabId].meta.history.map((item) => item.url) :
                        [tabUrl];
                    try {
                        const historyBlockedItemEntry = {
                            site: details.url,
                            type: 'content',
                            subtype: 'content',
                            date: Date.now()
                        };
                        detectionHistory.addItem(historyBlockedItemEntry);
                        onDetectionTelemetry('user_block', 'content', details.url, tabUrl, tabHistory);
                    } catch (e) {
                        console.warn('IUBI: Error sending telemetry', e);
                    }
                    return { cancel: true };
                }

                if (isBlockedInContentControls(details.url)) {
                    console.debug('IBIC: Is blocked in content controls', details);
                    let tabHistory = malwarebytes.TABS[details.tabId] ?
                        malwarebytes.TABS[details.tabId].meta.history.map((item) => item.url) :
                        [tabUrl];

                    try {
                        const historyContentControlEntry = {
                            site: details.url,
                            type: 'content',
                            subtype: 'content',
                            date: Date.now()
                        };
                        detectionHistory.addItem(historyContentControlEntry);
                        onDetectionTelemetry('user_block', 'content', details.url, tabUrl, tabHistory);
                    } catch (e) {
                        console.warn('IBIC: Error sending telemetry', e);
                    }

                    const host = urlHost(details.url);
                    malwarebytes.recordAll(RecordType.CONTENT, details.tabId, host, urlHost(host), true);

                    const redirectURL =
                        chrome.runtime.getURL(`app/eventpages/block-content-control.html?url=${details.url}`);
                    return { redirectUrl: redirectURL };
                }

                const sourceUrl = details.initiator || details.originUrl || details.documentUrl;
                const requestUrl = details.url;
                const requestDomain = domain(requestUrl);
                const sourceUrlDomains = urlDomains(sourceUrl);

                const allowedItems = exclusionHandler.getExclusionsSync();
                const allowedDomain = sourceUrlDomains.length > 0 ?
                    sourceUrlDomains[sourceUrlDomains.length - 1] : requestDomain;

                const allowlistedForScam = allowedItems[allowedDomain] ?
                    allowedItems[allowedDomain].includes(6) : false;
                const allowlistedForMalware = allowedItems[allowedDomain] ?
                    allowedItems[allowedDomain].includes(4) : false;
                const allowlistedForScamOrMalware = allowlistedForScam ||  allowlistedForMalware;

                // is "scammers.us" trying to reach local network?
                if (malwarebytes.FEATURE_FLAGS[FLAG_ENABLE_BLOCK_LOCAL_PORT_SCANNING] === true) {
                    if (!(isPrivateNetwork(sourceUrl)) && isPrivateNetwork(requestUrl)) {
                        const isWhitelistedForScam =
                            malwarebytes.isWhitelistedScams(sourceUrlDomains[sourceUrlDomains.length - 1]);
                        const isWhitelistedForMalware =
                            malwarebytes.isWhitelistedMalware(sourceUrlDomains[sourceUrlDomains.length - 1]);
                        const which = [allowlistedForScamOrMalware, isWhitelistedForScam, isWhitelistedForMalware];
                        const names = ['Allowlisted', 'Scam', 'Malware'];
                        if (allowlistedForScamOrMalware || isWhitelistedForScam || isWhitelistedForMalware) {
                            const whichNames = which.map((x, i) => x ? names[i] : '').filter((x) => x);
                            console.log('BTW: Local port access is allowed due to whitelisting: ', whichNames);
                        } else {
                        // eslint-disable-next-line max-len
                            console.log(`BTW: (NETWORK_BLOCK) ${sourceUrl} is trying to reach local network at ${requestUrl}`);
                            return {cancel: true};
                        }
                    }
                }

                if (malwarebytes.FEATURE_FLAGS[FLAG_ENABLE_BLOCK_EASYLIST_ADS] === true) {
                    const easylistDb = databaseManager.getCachedDatabaseByName('easylist');
                    if (easylistDb && easylistDb.domains) {
                        // eslint-disable-next-line max-len
                        let isActive = malwarebytes.isProtectionActive(EXCLUSION_ADS, tabUrl, details.tabId);
                        // eslint-disable-next-line max-len
                        let isWhitelistedAd = malwarebytes.isWhitelistedAds(requestUrl) || malwarebytes.isWhitelistedAds(sourceUrl);
                        let isWhiteListedTracker = malwarebytes.isWhiteListedInTrackers(requestUrl) ||
                            malwarebytes.isWhiteListedInTrackers(sourceUrl)
                        if (isActive && !isWhitelistedAd && !isWhiteListedTracker) {
                            if (shouldBlockAdDomains(tabUrl, requestUrl, details)) {
                                if (sourceUrl == null || tabUrl == requestUrl) {
                                    simpleStorageSet({
                                        lastDBused: `Easylist: ${easylistDb.version}` });
                                    return doRedirect(
                                        details.tabId, null, details.url, "ad", "adserver", null
                                    );
                                }
                                // eslint-disable-next-line max-len
                                console.log(`BTW: (NETWORK_BLOCK) easylist ad domain found on ${sourceUrl} for ${details.url}`);
                                // eslint-disable-next-line max-len
                                malwarebytes.recordAll(RecordType.AD, details.tabId, urlHost(tabUrl), urlHost(requestDomain), true);
                                malwarebytes.updateBadgeCount(details.tabId);
                                // if (isMV3()) {
                                //     return { redirectUrl: chrome.runtime.getURL('app/eventpages/welcome.html') };
                                // }
                                return { cancel: true };
                            }
                            getIncrementalPathSegments(requestUrl).forEach((path) => {
                                // eslint-disable-next-line max-len
                                if (path.length > 1 && Object.prototype.hasOwnProperty.call(easylistDb.pages, path)) {
                                // eslint-disable-next-line max-len
                                    console.debug(`BTW: (NETWORK_BLOCK) easylist ad page found on ${sourceUrl} for ${details.url}`);
                                    // eslint-disable-next-line max-len
                                    malwarebytes.recordAll(RecordType.AD, details.tabId, urlHost(tabUrl), urlHost(requestDomain), true);
                                    malwarebytes.updateBadgeCount(details.tabId);
                                    return { cancel: true };
                                }
                            });
                        }
                    }
                }

                // Scan for third-party ads - the request is third-party if it is not a main frame (top level), it has an identifiable sld and it does not match the sld attached to this tab
                let isThirdParty = (
                    !details.isMainFrame &&
                    details.sld &&
                    malwarebytes.TABS[details.tabId] &&
                    malwarebytes.TABS[details.tabId].site &&
                    malwarebytes.TABS[details.tabId].site.sld !== details.sld
                );
                // refreshing it helps with phantom entries - BG-359
                tabUrl = malwarebytes.TABS[details.tabId].site ? malwarebytes.TABS[details.tabId].site.url : "";
                // NOTE: moving below to it's own method so tabUrl is scoped still does not prevent phantom attribution
                if (isDetectableAd(tabUrl, details.url, details.domains, details.tabId, isThirdParty)) {
                    console.log("OAD: Ad match found for " + details.url + " on " + tabUrl);
                    return onAd({
                        tabId: details.tabId,
                        url: details.url,
                        domains: details.domains,
                        tabUrl,
                        blockMessage: `ad match found for ${details.url} on ${tabUrl}`,
                        isSilent: false
                    });
                }

                if (malwarebytes.FEATURE_FLAGS[FLAG_ENABLE_SUSPICIOUS_POST_OR_PUT_DETECTION] === true) {
                    if (details.method === "PUT" ||
                        details.method === "POST") {
                        //https://raw.githubusercontent.com/gwarser/filter-lists/master/lan-block.txt
                        if (domain(sourceUrl) !== domain(requestUrl)) {
                            // eslint-disable-next-line max-len
                            if (!malwarebytes.isWhitelistedScams(tabUrl) && !malwarebytes.isWhitelistedScams(details.url) && !malwarebytes.isWhitelistedScams(sourceUrl)) {
                                // eslint-disable-next-line max-len
                                let tabUrl = malwarebytes.TABS[details.tabId].site ? malwarebytes.TABS[details.tabId].site.url : "";
                                let blockMessage = `phishing attempt found on ${sourceUrl} for ${details.url}`;
                                let url = new URL(details.url);

                                // eslint-disable-next-line max-len
                                const currentFeatureDB = JSON.stringify(databaseManager.getCachedDatabaseByName('featureflags'));

                                // eslint-disable-next-line max-len
                                const dbEntries = DatabaseManager.getInstance().getCachedDatabases(['whitelist_scams_manual', 'top1m']);
                                const selectedCheckedDBs = Object.values(dbEntries);

                                if (riskyTlds.some((riskyTld) => url.hostname.endsWith(`.${riskyTld}`))) {
                                    // eslint-disable-next-line max-len
                                    console.log(`BTW: (NETWORK_BLOCK) suspicious request made to high risk TLD, possible phishing!`, {url});
                                    simpleStorageSet({ lastDBused: null });
                                    return onScam({
                                        tabId: details.tabId,
                                        tabURL: tabUrl,
                                        url: details.url,
                                        type: "scam",
                                        subtype: `phishing_suspicious_post`,
                                        // eslint-disable-next-line max-len
                                        rule: `${threatNamePrefix.heuristic}phishing_suspicious_post [${currentFeatureDB}]`,
                                        blockMessage,
                                        isSilent: false,
                                        selectedCheckedDBs,
                                    });
                                }
                            }
                        }
                    }
                }

                const pageExtension = urlExt(tabUrl);
                const initiator = details.initiator || details.originUrl; // chrome: "initiator" & firefox: "originUrl"
                const safeFilePattern = safePatternFilename(details.url, pageExtension, initiator);
                //Scan for each type of malware classification (different databases for all)
                const allCachedDatabases = databaseManager.getCachedDatabases();
                let blockMessage;
                let domainLevelResult = [
                    { db: allCachedDatabases.adware, name: "adware" },
                    { db: allCachedDatabases.hijack, name: "hijack" },
                    { db: allCachedDatabases.malvertising, name: "malvertising" },
                    { db: allCachedDatabases.ransomware, name: "ransomware" },
                    { db: allCachedDatabases.spyware, name: "spyware" },
                    { db: allCachedDatabases.trojan, name: "trojan" },
                    { db: allCachedDatabases.fraud, name: "fraud" },
                    { db: allCachedDatabases.pharma, name: "pharma" },
                    { db: allCachedDatabases.phishing, name: "phishing" },
                    { db: allCachedDatabases.riskware, name: "riskware" },
                    { db: allCachedDatabases.spam, name: "spam" }
                ].find((typ) => {
                    if (isDetectableMalware(tabUrl, details.url, details.domains, typ['db'], details.tabId)) {
                        if (typ.name === 'riskware' && safeFilePattern) {
                            console.debug('BTW: Skipped riskware detection due to Safe Pattern');
                            malwarebytes.postNativeExclusion(details.url);
                            return false;
                        } else {
                            blockMessage = `malware (${typ["name"]}) match found on ${tabUrl} for ${details.url}`;
                            return true; //actual result value will be the elem, this lambda is only the callback
                        }
                    }
                });

                if (domainLevelResult) {
                    return onMalware({
                        tabId: details.tabId,
                        tabURL: tabUrl,
                        url: details.url,
                        type: "malware",
                        subtype: domainLevelResult['name'],
                        blockMessage,
                        isMainFrame: details.isMainFrame
                    });
                }
                const debugUrlCancelResult = malwarebytes.handleDebugUrls(details);
                if (debugUrlCancelResult !== null) {
                    return debugUrlCancelResult;
                }

                try {
                    // Check for a full URL match first - original code here: https://github.mb-internal.com/Incubation/browser-extension/commit/ad629ba04a33b33e24b71ade8d74b0f91aa37901
                    let cleanedUrl = details.url.replace("http://", "").replace("https://", "");
                    if (cleanedUrl.endsWith("/")) {
                        cleanedUrl = cleanedUrl.substr(0, cleanedUrl.length - 1);
                    }
                    // Important: Use the RAW url for checking exclusions but the CLEAN url for checking database
                    if (malwarebytes.isProtectionActive(EXCLUSION_MALWARE, tabUrl, details.tabId) &&
                        !allowlistedForMalware &&
                        !malwarebytes.isWhitelistedMalware(details.url)) {
                        const malwareUrlsDb = databaseManager.getCachedDatabaseByName('malware_urls');
                        if (malwareUrlsDb) {
                            // If we are using the cached database then the malware urls are an Array as opposed to a Set
                            if ( !allowlistedForMalware &&
                                ((
                                    malwareUrlsDb.data &&
                                    malwareUrlsDb.data.has &&
                                    malwareUrlsDb.data.has(cleanedUrl)
                                ) ||
                                    (
                                        malwareUrlsDb.data &&
                                        malwareUrlsDb.data.includes &&
                                        malwareUrlsDb.data.includes(cleanedUrl)
                                    )
                                ))
                            {
                                return onMalware({
                                    tabId: details.tabId,
                                    tabURL: malwarebytes.TABS[details.tabId] && malwarebytes.TABS[details.tabId].site ?
                                        malwarebytes.TABS[details.tabId].site.url : "",
                                    url: details.url,
                                    type: "malware",
                                    subtype: "full-url-malware",
                                    blockMessage: `malware url match found for ${tabUrl} in database malware_urls`,
                                    isMainFrame: details.isMainFrame
                                });
                            }
                        } else {
                            console.error("BTW: Missing malware url database!");
                        }
                    }
                } catch (err) {
                    console.warn("BTW: ERR checking mal-urls: ", err);
                }

                let regexRule;
                const fpChecker = new FPChecker(false);
                if (malwarebytes.isProtectionActive(EXCLUSION_SCAMS, tabUrl, details.tabId) &&
                    !allowlistedForMalware &&
                    !malwarebytes.isWhitelistedMalware(details.url))  {
                    // eslint-disable-next-line max-len
                    const malwarePartialUrlsJsonDb = databaseManager.getCachedDatabaseByName('malware_partial_urls_json');
                    //Now check for regex rules, keep these after the plaintext rules as they are more expensive
                    if (malwarePartialUrlsJsonDb) {
                        regexRule = malwarebytes.regexJsonMatch(
                            malwarePartialUrlsJsonDb,
                            details.url);
                        if ((regexRule.match)) {
                            try {
                                // TODO: move this check to the webrequest-mv3 callback
                                const shouldBlockCached = fpChecker.shouldBlockCached(regexRule.id);
                                if (shouldBlockCached !== null) {
                                    if (!shouldBlockCached) {
                                        return { cancel: true };
                                    }
                                } else {
                                    // nothing in cache
                                    // Trigger a check for this rule ID to build our local cache but don't wait for it to complete
                                    fpChecker.shouldBlock(regexRule.id);
                                }
                            } catch (error) {
                                console.error("Error checking hubble for FP prevention", error);
                            }

                            //Check Allow List for full-url exclusions for this type (scam)
                            // - note we only check the tab, not the individual requests
                            const db = databaseInfo("malware_partial_urls_json");
                            //By redirecting before we even hit the download url, we don't need to cancel anything
                            simpleStorageSet({ lastDBused: null });
                            return malwarebytes.onSuspiciousDownload({
                                tabId: details.tabId,
                                tabURL: details.url,
                                referrer: details.domains[0],
                                url: details.url,
                                filename: details.url,
                                shouldRedirect: true,
                                shouldBeWhitelistedBySpecificUrl: true,
                                rule: `${threatNamePrefix.regex}${regexRule.id}`, // rule id correlates to the cosmos db
                                blockTag: "(PAGE_BLOCK)",
                                blockMessage: `Partial url match found for ${details.url}` +
                                    `${(db) ? `Database: ${JSON.stringify(db)}` : ""} | Rule ID: ${regexRule.id}`,
                                isSilent: regexRule.silent,
                                selectedCheckedDBs: [ databaseManager.getCachedDatabaseByName('whitelist_malware') ]
                            });
                        }
                    }
                } else {
                    console.debug(
                        "BTW: NO partial url match found for " + details.url +
                    " because scams check is disabled"
                    );
                }

                //Then scan for reputation and scam blocks
                if (!allowlistedForScam && malwarebytes.isDetectableScam(tabUrl, details.url, details.tabId)) { //refers to tab main domain
                    const malwarePatternsJsonDb = databaseManager.getCachedDatabaseByName('malware_patterns_json');
                    if (malwarePatternsJsonDb) {
                        regexRule = malwarebytes.regexJsonMatch(malwarePatternsJsonDb, details.url);
                        if (regexRule.match) {
                            try {
                                const shouldBlockCached = fpChecker.shouldBlockCached(regexRule.id);
                                if (shouldBlockCached !== null) {
                                    if (!shouldBlockCached) {
                                        return { cancel: true };
                                    }
                                } else {
                                    // nothing in cache
                                    // Trigger a check for this rule ID to build our local cache but don't wait for it to complete
                                    fpChecker.shouldBlock(regexRule.id);
                                }
                            } catch (error) {
                                console.error("Error checking hubble for FP prevention", error);
                            }

                            return onScam({
                                tabId: details.tabId,
                                tabURL: malwarebytes.TABS[details.tabId] && malwarebytes.TABS[details.tabId].site ?
                                    malwarebytes.TABS[details.tabId].site.url : "",
                                url: details.url,
                                type: "scam",
                                subtype: "malware-pattern",
                                blockMessage: `malware pattern match found for ${details.url}
                                               | Rule ID: ${regexRule.id}`,
                                rule: `${threatNamePrefix.regex}${regexRule.id}`, // rule id correlates to the cosmos db
                                isMainFrame: details.isMainFrame,
                                isSilent: regexRule.silent,
                                selectedCheckedDBs: [databaseManager.getCachedDatabaseByName('whitelist_malware')]
                            });
                        } else {
                            console.debug(
                                `BTW: NO malware pattern match found for ${details.url}`
                            );
                        }
                    } else {
                        console.error("BTW: Missing malware pattern database!");
                    }

                    const reputationDb = databaseManager.getCachedDatabaseByName('reputation');
                    if (
                        !safeFilePattern &&
                        isDetectableMalware(
                            tabUrl, details.url, details.domains, reputationDb, details.tabId
                        ) &&
                        detectReputation
                    ) {
                        return onMalware({
                            tabId: details.tabId,
                            tabURL: tabUrl,
                            url: details.url,
                            type: "malware",
                            subtype: "reputation",
                            blockMessage: `scam (malware-reputation) match found for ${details.url} on ${tabUrl}`,
                            isMainFrame: details.isMainFrame
                        });
                    } else {
                        console.debug(`BTW: NO scam (malware-reputation) match found for ${details.url} on ${tabUrl}`);
                    }


                    const initiatorIsWhitelisted = initiator && malwarebytes.isWhitelistedScams(initiator);
                    const initiatorIsManualWhitelisted = initiator && !malwarebytes.isProtectionActive(
                        EXCLUSION_SCAMS, initiator, details.tabId
                    );
                    console.debug("BTW: Page file extension is safeFilePattern? ", safeFilePattern);

                    if (
                        settings.get('enableProtectionGtld') &&
                        (!safeFilePattern && malwarebytes.RISKY_TLDS.includes(urlTLD(details.url))) &&
                        !isSafeReferral(details.url, initiator, initiatorIsWhitelisted || initiatorIsManualWhitelisted)
                    ) { //refers to request resource
                        const blockMessage = `scam (risky-tld) match found for ${details.url} on ${tabUrl}`;
                        if (!malwarebytes.isWhitelistedScams(details.url)) { //note: isDetectableScam checked whitelist against tabUrl, this isn't redundant
                            if (malwarebytes.TABS[details.tabId]) {
                                if (
                                    malwarebytes.TABS[details.tabId].lastScam &&
                                    malwarebytes.TABS[details.tabId].lastScam === details.url
                                ) {
                                    //Match (we've already recorded this scam url) - as a simple solution, we dont count two SCAMS within same second
                                    if (new Date().getTime() - malwarebytes.TABS[details.tabId].lastScamTime < 1000) {
                                        //no need to count anything, so lets just do the redirect part
                                        malwarebytes.TABS[details.tabId].lastScam = details.url;
                                        malwarebytes.TABS[details.tabId].lastScamTime = new Date().getTime();
                                        console.log(`BTW: (PAGE_BLOCK) ${blockMessage}`); //Need to print the message because we don't call onScam
                                        logEnvironmentInfo().then(() => {}); // this function is async
                                        return doRedirect(
                                            details.tabId, null, details.url, "scam", "suspiciousTLD", null
                                        );
                                    }
                                }
                                malwarebytes.TABS[details.tabId].lastScam = details.url;
                                malwarebytes.TABS[details.tabId].lastScamTime = new Date().getTime();
                            }
                            //otherwise count the scam, then do the redirect
                            simpleStorageSet({ lastDBused: null });
                            const dbsToCheck = databaseManager.getCachedDatabases(['top1m', 'whitelist_scams_manual']);
                            const selectedCheckedDBs = Object.values(dbsToCheck);
                            return onScam({
                                tabId: details.tabId,
                                tabURL: tabUrl,
                                url: details.url,
                                type: "scam",
                                subtype: "suspiciousTLD",
                                rule: `${threatNamePrefix.heuristic}suspiciousTLD`,
                                blockMessage,
                                isSilent: false,
                                selectedCheckedDBs: selectedCheckedDBs
                            });
                        }
                    }

                    //Note: This location is not a mistake - Rakesh decided to make this use scams whitelist despite
                    //  being pups, that's why this block is nested within the scams section
                    //NOTE: Here we test to see if we have a potential-download event
                    // (I.e. is URL a probable executable, as it wont trigger onDownloadEvent until a real file download is initiated)
                    // This is a requirement of arbitrary BG-409
                    if (
                        !safeFilePattern &&
                        malwarebytes.RISKY_EXECUTABLES.includes(pageExtension) &&
                        !initiatorIsManualWhitelisted &&
                        !(initiatorIsWhitelisted && isValidRedirect(details.url)) // do not block cdn/storage site download when referred from an allowed domain
                    ) {
                        //Note: firefox handles certain url's ending in exe poorly, so instead of allowing us to
                        //  redirect like a normal tab or like chrome does, firefox forces the user to stay on their
                        //  own bright red warning page. So for firefox we could open a new tab to show
                        //  we took action, so the user isn't stumped about no download happening - however we would
                        //  need to find a way to detect the red page is showing... which we cant do from here
                        //NOTE: the red warning page has an "ignore the risk" link. After that is clicked we can
                        //  block normally, so we need to make sure we dont show a new tab in those cases
                        //NOTE: Another great firefox moment - after you ignore the risk once, you can't get it to
                        //  show the page again, which makes this untestable unless you find a second trigger page
                        simpleStorageSet({ lastDBused: null });
                        const dbsToCheck = databaseManager.getCachedDatabases(['whitelist_scams_manual', 'top1m']);
                        const selectedCheckedDBs = Object.values(dbsToCheck);
                        return malwarebytes.onSuspiciousDownload({
                            tabId: details.tabId,
                            tabURL: tabUrl,
                            referrer: details.domains[0],
                            url: tabUrl,
                            filename: tabUrl,
                            shouldRedirect: true,
                            shouldBeWhitelistedBySpecificUrl: true,
                            blockTag: "(PAGE_BLOCK)",
                            blockMessage: `${pageExtension} is a high risk downloadable type`,
                            isSilent: false,
                            selectedCheckedDBs: selectedCheckedDBs
                        });
                    } else {
                        console.debug("BTW: NO high risk extension match found for " + tabUrl);
                    }
                } else {
                    console.debug("BTW: Skipping scam scans");
                }

                // Don't cache youtube values because it breaks isDetectableAd func
                if (details.sld && details.sld.toLowerCase() !== "youtube.com") {
                    malwarebytes.cacheSafeness(details.tabId, details.url && details.url.split('?')[0]);
                }
                //NO Block
                [details.url, initiator, tabUrl].forEach((url) => url && trackLoggedUrl(url));
            } else {
                console.log("BTW: Skipped scans. Settings is missing");
            }
        }
    }
    return { cancel: false }; //default, if we didn't detect anything
};


/**
 * Determines whether to block ad domains based on the page URL, ad URL, and details.
 * @param {string} pageUrl - The URL of the page.
 * @param {string} adUrl - The URL of the ad.
 * @param {object} details - Additional details about the request.
 * @returns {boolean} - True if the ad domain should be blocked, false otherwise.
 */

export function shouldBlockAdDomains(pageUrl, adUrl, details) {
    if (!isValidString(pageUrl) || !isValidString(adUrl)) {
        return false;
    }

    const easylistDb = DatabaseManager.getInstance().getCachedDatabaseByName('easylist');
    if (!easylistDb) {
        console.debug('SBAD: Easylist database not found in cache');
        return false;
    }

    let pageSiteName = urlDomains(pageUrl);
    pageSiteName = pageSiteName[pageSiteName.length - 1];
    let adSiteName = urlDomains(adUrl);
    adSiteName = adSiteName[adSiteName.length - 1];
    let pageDomainEntry = easylistDb.domains[pageSiteName];
    let pageDomainExclusionEntry = easylistDb.domain_exceptions[pageSiteName];

    if (pageSiteName === adSiteName) {
        // same domain, first check if we have an exclusion
        if (pageDomainExclusionEntry) {
            for (let filter of pageDomainExclusionEntry) {
                if (matchesFilter(adUrl, pageSiteName, filter, details.type) &&
                    !filter.includes("~third-party")) {
                    console.debug("SBAD: Easylist excluded ad domain: " + adUrl);
                    return false;
                }
            }
        }
        if (pageDomainEntry) {
            for (let filter of pageDomainEntry) {
                if (matchesFilter(adUrl, pageSiteName, filter, details.type) &&
                    !filter.includes("third-party")) {
                    console.debug("SBAD: Easylist blocked ad domain: " + adUrl + " for filter: " + filter);
                    return true;
                }
            }
        }
        return false;
    }

    let adDomainEntry = easylistDb.domains[adSiteName];
    if (adDomainEntry) {
        for (let filter of adDomainEntry) {
            if (matchesFilter(adUrl, pageSiteName, filter, details.type)) {
                console.debug("SBAD: Easylist blocked ad domain: " + adUrl + " for filter: " + filter);
                return true;
            }
        }
    }

    return false;
}

/**
 * Checks if a resource URL matches a given filter.
 * @param {string} resourceUrl - The URL of the resource to be checked.
 * @param {string} pageDomain - The domain of the current page.
 * @param {string} filter - The filter to be applied.
 * @param {string} optionalType - The optional type of the resource.
 * @returns {boolean} - True if the resource URL matches the filter, false otherwise.
 */
function matchesFilter(resourceUrl, pageDomain, filter, optionalType) {
    let parts = filter.split("$");
    let urlPart = parts[0];
    let options = parts[1] && parts[1].split(",") || [];
    for (let option of options) {
        if (option.startsWith("domain=")) {
            let domains = option.split("=")[1].split("|");
            for (let domain of domains) {
                if (domain.startsWith("~") && domain.slice(1) === pageDomain) {
                    return false;
                }
            }
            if (domains.length > 0 && domains.indexOf(pageDomain) === -1) {
                return false;
            }
        }
        if (option === "subdocument") {
            if (optionalType !== "sub_frame") {
                return false;
            }
        }
        if (option === "popup") {
            if (optionalType !== "popup") {
                return false;
            }
        }
        if (option === "document") {
            if (optionalType !== "main_frame") {
                return false;
            }
        }
        if (option === "image") {
            if (optionalType !== "image") {
                return false;
            }
        }
        if (option === "script") {
            if (optionalType !== "script") {
                return false;
            }
        }
        if (option === "stylesheet") {
            if (optionalType !== "stylesheet") {
                return false;
            }
        }
        if (option === "generichide") {
            return false;
        }
    }

    let filterToRegex = urlPart.replace(/[-\\$+?.()|[\]{}]/g, "\\$&")
        .replace(/\*/g, ".*")
        .replace(/\^$/, "([?/].*)?$")
        .replace(/\^/, "[?/]");
    let regex = new RegExp(filterToRegex);

    return regex.test(resourceUrl);
}

/**
 * Checks if the given URL matches any regular expression pattern in the provided database.
 *
 * @param {Object} db - The database object containing the regular expression patterns.
 * @param {string} url - The URL to be matched against the regular expression patterns.
 * @returns {Object} - An object indicating whether a match was found, the matching regular expression, and whether it should be silent.
 */
malwarebytes.regexJsonMatch = (db, url) => {
    if (db && db.data) {
        for (let pattern of db.data) {
            if (pattern.r && pattern.r.test && pattern.r.test(url)) {
                return { match: true, source: pattern.r.source, silent: !!pattern.s, id: pattern.id };
            }
        }
    }
    return { match: false, source: "", silent: false };
}

malwarebytes.isWhiteListedInTrackers = (url) => {
    const databaseManager = DatabaseManager.getInstance();
    const whitelistTrackerDb = databaseManager.getCachedDatabaseByName('whitelist_tracker');

    if (whitelistTrackerDb) {
        let hosts = urlDomains(url);
        for (let host of hosts) {
            const list = whitelistTrackerDb.data;
            for (let entry of list) {
                if (entry && entry.host && entry.host === host && entry.tracker) {
                    // a tracker could be just "easylist" or specifically "easylist|.content-ads"
                    if (entry.tracker === "easylist") {
                        console.debug("IWLIT: Site is whitelisted by a 'whitelist_tracker' easylist entry for " + host);
                        return true;
                    }
                }
            }
        }
    }
    return false;
};

malwarebytes.isTrackerWhitelistedForDomain = (tabId, adUrl) => {
    if (!tabId || !adUrl) {
        return false;
    }

    if (!malwarebytes.TABS[tabId] || !malwarebytes.TABS[tabId].site) {
        return false;
    }

    let hostDomain = malwarebytes.TABS[tabId].site ? malwarebytes.TABS[tabId].site.sld : "";
    let adTracker = urlHost(adUrl);
    if (adTracker === hostDomain) { //3rd party only
        return false;
    }

    const databaseManager = DatabaseManager.getInstance();
    const whitelistTrackerDb = databaseManager.getCachedDatabaseByName('whitelist_tracker');

    if (whitelistTrackerDb) {
        return whitelistTrackerDb.data.some((entry) => {
            let isExisting = false;
            if (entry && entry.host && entry.tracker) {
                if (entry.host === hostDomain && entry.tracker === adTracker) {
                    isExisting = true;
                }
            }
            return isExisting;
        });
    }
    return false;
};

/**
 * Called when chrome is about to send any request but no tabId is specified
 * @return {{cancel: false}}
 */
malwarebytes.onBeforeTablessWebRequest = (/*details*/) => {
    //is there any way to match this to a tab yet?
    //logging?
    return { cancel: false }; //default, allow it to load
};

chrome.tabs.onUpdated.addListener((tabId, changeInfo, tab) => {
    if (changeInfo.status === "complete") {
        malwarebytes.tabsOnUpdateComplete(tabId);
    } else if (changeInfo.status === "loading") {
        malwarebytes.tabsOnUpdateLoading(tabId, changeInfo, tab);
    } else if (changeInfo.status === undefined) {
        malwarebytes.iconRefresh(tabId);
    }
    syncFriendlyUrl(tab);
});

const syncFriendlyUrl = (tab) => {
    if (!tab.id || !tab.url) {
        return;
    }
    if (malwarebytes.TABS[tab.id] && malwarebytes.TABS[tab.id].site) {
        malwarebytes.TABS[tab.id].site.friendlyUrl = urlHost(tab.url);
    }
};

chrome.tabs.onRemoved.addListener(malwarebytes.tabsOnRemoved);

chrome.windows.onRemoved.addListener((_windowId) => {
    // brute force clear out entire cache when window closed
    malwarebytes.SAFE_CACHE = {};

    // attempt to save logs
    malwarebytes.flushLogs();
});

/**
 * We need to know when a tab was replaced, because chrome invisibly swaps
 * tabs (and corresponding ID's) for the sake of pre-rendering. More here:
 * https://stackoverflow.com/a/23995989/984830
 */
chrome.tabs.onReplaced.addListener((tabIdAdded, tabIdRemoved) => {
    if (!malwarebytes.TABS[tabIdAdded]) {
        console.debug("Chrome has swapped tab " + tabIdRemoved + " for " + tabIdAdded + " => Replacing in namespace");
        malwarebytes.TABS[tabIdAdded] = malwarebytes.TABS[tabIdRemoved];
        malwarebytes.TABS[tabIdAdded].tabId = tabIdAdded;
        malwarebytes.TABS[tabIdRemoved] = null;
    } else {
        console.debug("Chrome has swapped tab " + tabIdRemoved + " for " + tabIdAdded + " => Removing unused tab");
        malwarebytes.TABS[tabIdRemoved] = null;
    }
});

chrome.tabs.onCreated.addListener((tab) => {
    chrome.tabs.get(tab.id, async (tab) => {
        if (!chrome.runtime.lastError) {
            let isProtectionEnabled = await settings.getFromStorage("enableProtection");
            malwarebytes.setBGIcon(tab.id, isProtectionEnabled);
        }
    });
});

