malwarebytes.getUserGroup = () => {
    //Get saved group if exists
    return new Promise((resolve, reject) => {
        chrome.storage.local.get(['user_group'], function (result) {
            chrome.runtime.lastError ?
                reject(chrome.runtime.lastError.message) :
                resolve(result.user_group);
        });
    }).then((resolvedGroup) => {
        if (resolvedGroup) {
            console.debug("GUG: Valid - " + resolvedGroup);
            return Promise.resolve(resolvedGroup);
        } else {
            console.log("GUG: Invalid - No stored user_group - will determine");
            return malwarebytes.determineUserGroup().then((determinedGroup) => {
                console.debug("GUG: Determined " + determinedGroup);
                return Promise.resolve(determinedGroup);
            });
        }
    });
};

malwarebytes.determineUserGroup = () => {
    return new Promise((resolveI, rejectI) => {
        chrome.storage.local.set({ "user_group": GROUP_A }, () => {
            return chrome.runtime.lastError ?
                rejectI(chrome.runtime.lastError.message) :
                resolveI(GROUP_A);
        });
    }).then((determinedGroup) => {
        //SWITCH ON PROTECTION LAYERS NOW, IF WE ARE IN GROUP A
        //  BUT BE CAREFUL NOT TO SWITCH THEM ON, IF IT WAS MANUALLY SET
        //    (That is: Switch only if undefined)
        if (determinedGroup === GROUP_A) {
            return settings.getFromStorage("enableProtection").then((epValue) => {
                if (epValue === undefined) {
                    settings.setToStorage({enableProtection: true}).then(() => {
                        return Promise.resolve(determinedGroup);
                    });
                } else {
                    return Promise.resolve(determinedGroup);
                }
            });
        } else {
            return Promise.resolve(determinedGroup);
        }
    });
};

malwarebytes.getUserEmail = () => {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get(['user_email'], function (result) {
            chrome.runtime.lastError ?
                reject(chrome.runtime.lastError.message) :
                result.user_email ? resolve(result.user_email) :
                    reject(new Error("No stored user_email"));
        });
    });
};

malwarebytes.userWelcomed = () => {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get(['user_welcomed'], (result) => {
            if (chrome.runtime.lastError) {
                return reject(chrome.runtime.lastError.message);
            }
            if (result.user_welcomed) {
                resolve(result.user_welcomed);
            } else if (result.user_welcomed === undefined) {
                // This is an old version -- catch up old users to the new format
                // TODO: phase out this check
                chrome.storage.local.set({ user_welcomed: true }, () => (
                    console.log("UW: Updated old user to having being welcomed")
                ));
                resolve(true);
            } else {
                reject(new Error("No stored user_welcomed"));
            }
        });
    });
};

malwarebytes.hasFinishedOnboarding = () => {
    //Get User Group
    return malwarebytes.getUserGroup().then((group) => {
        console.debug("HFO: testing for user in group " + group);
        if (group === GROUP_A) {
            return malwarebytes.userWelcomed().then((welcomed) => {
                console.debug("HFO: Yes - ", welcomed);
                return Promise.resolve(
                    { result: true, group: GROUP_A }
                );
            }, (reason) => {
                console.debug("HFO: No - ", reason);
                return Promise.resolve(
                    { result: false, group: GROUP_A }
                );
            });
        } else {
            return malwarebytes.getUserEmail().then((resolvedEmail) => {
                console.debug("HFO: Yes - " + resolvedEmail);
                return Promise.resolve(true);
            }, (rejectedReason) => {
                console.debug("HFO: No - " + rejectedReason);
                return Promise.resolve(false);
            });
        }
    }).catch((err) => {
        console.error("Failed to check hasFinishedOnboarding: ", err);
        return Promise.reject(err);
    });
};

