malwarebytes.migrate_excludeTypeConvert = (oldType) => {
    if (oldType === "malware/scams") {
        return EXCLUSION_MALWARE + "," + EXCLUSION_SCAMS;
    } else if (oldType === "ads/clickbait") {
        return EXCLUSION_ADS;
    } else {
        console.error("Don't know how to convert '" + oldType + "' to consts");
        return null;
    }
};

malwarebytes.migrate_exclude = (exclude) => {
    //Date + type info can be lost, it is never used
    return malwarebytes.exclude(exclude.data, malwarebytes.migrate_excludeTypeConvert(exclude.category));
};

malwarebytes.migrate = () => {
    //The only thing that directly migrates is local.exclusions to sync.exclusions + nodes
    console.log('Migrate: looking for old exclusions');
    chrome.storage.local.get(["exclusions"], function (results) {
        /** "exclusions": [{
                "category": "malware/scams",
                "data": "malwarebytes.app",
                "date": 1562587325665,
                "type": "domain"
            }] */
        if (Array.isArray(results.exclusions)) {
            //NOTE: previousPromise starts off as the second param
            results.exclusions.reduce((previousPromise, nextExclusion) => {
                return previousPromise.then(() => {
                    console.debug('Migrate: About to process: ', nextExclusion);
                    return malwarebytes.migrate_exclude(nextExclusion);
                });
            }, Promise.resolve()).then(() => {
                //Delete the redundant local data we have migrated
                chrome.storage.local.remove("exclusions", function () {
                    console.log('Migrate: Cleanup complete');
                });
            }).catch((error) => {
                console.log('Migrate: Failed to complete: ', error);
            });
        } else {
            console.debug('Migrate: No old exclusions found');
        }
    });
};

