malwarebytes.clearCachedLogs = () => {
    return new Promise((resolve, reject) => {
        if (!indexedDB || isIncognito()) {
            console.debug("LFI: IndexedDB not available in this browser");
            return resolve('');
        }

        const request = indexedDB.deleteDatabase(logStoreName);
        request.onerror = (err) => {
            console.error('CCL: Failed to clear cached logs', err);
            return reject(err);
        };
        request.onsuccess = () => {
            console.debug('CCL: Successfully cleared cached logs');
            return resolve();
        };
    });
};

// TODO: Consolidate all common IndexDB code into shared function
malwarebytes.logFromIDB = () => {
    return new Promise((res) => {
        if (!indexedDB || isIncognito()) {
            console.debug("LFI: IndexedDB not available in this browser");
            return res('');
        }

        const request = indexedDB.open(logStoreName, 1);
        let db;
        let tx;
        let store;
        // this fires on first time database is created
        request.onupgradeneeded = () => {
            db = request.result;
            store = db.createObjectStore(logStoreName, { keyPath: "logs" });
        };

        request.onerror = (e) => {
            console.error("LFI: error opening log IDB: ", e);
            res('');
        };

        request.onsuccess = async () => {
            console.debug("LFI: Succesfully opened IndexDB Cached data store");
            db = request.result;
            tx = db.transaction(logStoreName, "readwrite");
            store = tx.objectStore(logStoreName);

            db.onerror = (e) => {
                console.error("LFI: IndexDB Error: ", e);
                res('');
            };

            const storedLogs = await malwarebytes.logGet(store);

            tx.oncomplete = () => db.close();
            res(storedLogs || '');
        };
    });
};

malwarebytes.logGet = (store) => {
    return new Promise((res) => {
        const logGet = store.get('current');
        logGet.onsuccess = () => res(logGet.result && logGet.result.data);
        logGet.onerror = (_e) => res(null);
    });
};

malwarebytes.flushSessionLog = (log) => {
    malwarebytes.SESSION_LOG += log;
    if (malwarebytes.SESSION_LOG.length > maxSessionLogSize) {
        malwarebytes.SESSION_LOG = malwarebytes.SESSION_LOG.slice(malwarebytes.SESSION_LOG.length - maxSessionLogSize);
    }
};

// NOTE: Errors are knowingly suppressed
malwarebytes.flushLogs = () => {
    let log = malwarebytes.CONSOLE_TRACE;
    malwarebytes.CONSOLE_TRACE = '';
    malwarebytes.flushSessionLog(log);
    return new Promise((res) => {
        if (!indexedDB || isIncognito()) {
            return res(null);
        }

        const request = indexedDB.open(logStoreName, 1);
        let db;
        let tx;
        let store;

        // this fires on first time database is created
        request.onupgradeneeded = () => {
            db = request.result;
            store = db.createObjectStore(logStoreName, { keyPath: "logs" });
        };

        request.onerror = (e) => {
            console.error("LTI: error opening log IDB: ", e);
            res(null);
        };

        request.onsuccess = async () => {
            db = request.result;
            tx = db.transaction(logStoreName, "readwrite");
            store = tx.objectStore(logStoreName);

            db.onerror = res;

            let savedLogs = await malwarebytes.logGet(store);

            console.debug('LTI: Retrieved saved logs');
            // purge the first half -- we don't want it to get too big.
            // If the user has a very small machine we get capped at 10mb
            // and the logs get deleted anyways. Best practice either way to keep things slim
            if (savedLogs && savedLogs.length > maxLogSize) {
                savedLogs = savedLogs.slice(savedLogs.length - maxLogSize);
            }

            const dataPut = store.put({
                logs: 'current',
                data: savedLogs ? savedLogs + log : log
            });

            dataPut.onsuccess = () => {
                console.debug('LTI: Successfully Saved Logs');
                tx.oncomplete = () => db.close();
                res();
            };
            dataPut.onerror = (e) => {
                console.debug('LTI: Failed to Save Logs, ', e);
                tx.oncomplete = () => db.close();
                res();
            };
        };
    });
};

const doubleQuoteRegex = new RegExp('"', 'g');
// used for debug mode to save logs to memory
malwarebytes.logger = (console) => {
    const logToStorage = (message = "", level) => {
        // hacky way of checking byte size (every .6~ mb save to cache)
        if (malwarebytes.CONSOLE_TRACE.length > 781250) {
            malwarebytes.flushLogs();
        }
        malwarebytes.CONSOLE_TRACE +=
            `{"@timestamp": "${new Date().toISOString()}", ` +
            `"session": "${sessionId}", ` +
            `"message": "${message.toString().replace(doubleQuoteRegex, "'")}", ` +
            `"level": "${level}"}\n`;
    };

    return {
        info: (...message) => {
            logToStorage(message, 'INFO');
            console.info(...message);
        },
        log: (...message) => {
            logToStorage(message, 'INFO');
            console.log(...message);
        },
        warn: (...message) => {
            settings.getFromStorage(SETTING_VERBOSE_LOGGING).then((verboseLogging) => {
                if (verboseLogging) {
                    logToStorage(message, 'WARN');
                }
            });
            console.warn(...message);
        },
        debug: (...message) => {
            settings.getFromStorage(SETTING_VERBOSE_LOGGING).then((verboseLogging) => {
                if (verboseLogging) {
                    logToStorage(message, 'DEBUG');
                }
            });
            console.debug(...message);
        },
        error: (...message) => {
            logToStorage(message, 'ERROR');
            console.error(...message);
        },
        trace: (...message) => console.trace(...message),
        logOnce: malwarebytes.logOnce
    };
};

malwarebytes.logOnce = (url, ...message) => {
    if (malwarebytes.LOGGED_URLS.has(url)) {
        console.debug(...message);
    } else {
        console.log(...message);
        trackLoggedUrl(url);
    }
};

malwarebytes.closePopup = (callback) => {
    try {
        window.close();
        callback(true);
    } catch (e) {
        callback(false);
    }
}

function trackLoggedUrl(url) {
    if (!malwarebytes.LOGGED_URLS.has(url)) {
        malwarebytes.LOGGED_URLS.add(url);
    }
}

function clearLoggedUrls() {
    malwarebytes.LOGGED_URLS = new Set();
}

