const msgCheckPermission = (callback, request) => {
    const { permissions } = request.payload || {};
    if (!permissions || !Array.isArray(permissions)) {
        callback({ hasPermission: false, error: "Invalid permissions array" });
        return;
    }

    chrome.permissions.contains({ permissions }, (result) => {
        if (chrome.runtime.lastError) {
            callback({ hasPermission: false, error: chrome.runtime.lastError.message });
        } else {
            callback({ hasPermission: !!result });
        }
    });
};

