chrome.alarms.onAlarm.addListener(async (alarm) => {
    if (alarm.name === 'sendTelemetryAlarm') {
        malwarebytes.sendClientTelemetry();
    } else if (alarm.name === 'updateDBAlarm') {
        malwarebytes.updateDatabases();
        nativeApp && nativeApp.postMessage({ "eBGAction": [{ "ePing": "Unused" }] });
    } else if (alarm.name === 'sendAdTelemetryAlarm') {
        const adTelemDailyLimitDisabled = await settings.getFromStorage('disableAdTelemDailyLimit');
        if (adTelemDailyLimitDisabled) {
            console.debug('AD_TELEM: Daily limit disabled, sending ad/tracker telemetry');
            malwarebytes.sendClientTelemetry(true);
        } else {
            console.debug('AD_TELEM: Checking if 24 hours have passed since last ad/tracker telemetry');
            if (await hasOneDayPassed()) {
                malwarebytes.sendClientTelemetry(true);
            }
        }
    } else if (alarm.name === 'additionalRuleSpaceCheckAlarm') {
        if (isMV3()) {
            checkForAdditionalRuleSpace();
        }
    } else if (alarm.name === 'showMonthlyNotificationAlarm') {
        malwarebytes.showMonthlyNotification();
    } else if (alarm.name === 'showNewReleaseNotificationAlarm'){
        malwarebytes.showNewReleaseNotificationSync();
    } else if (alarm.name === SHOW_PERMISSION_REQUEST_ALARM) {
        await malwarebytes.onPermissionReqAlarm();
    } else if (alarm.name === 'initUpdateDBAlarm') {
        meas('init_update_db_begin');
        return malwarebytes.updateDatabases().then((_ignored) => {
            meas('init_update_db_complete', 'init_update_db_begin');
        });
    } else if (alarm.name === 'trimDynamicWhitelistAlarm') {
        await malwarebytes.resetDynamicWhitelist();
    }
});

malwarebytes.overrideAdTelemAlarm = async (periodInMinutes) => {
    // set flag to disable hasOneDayPassed check
    await settings.setToStorage({"disableAdTelemDailyLimit": true});
    if (TARGET_BROWSER === 'Firefox') {
        // Firefox does not support the third, callback, argument
        return new Promise((resolve) => {
            chrome.alarms.create("sendAdTelemetryAlarm", { periodInMinutes });
            console.debug(`AD_TELEM: Successfully reset 'sendAdTelemetryAlarm' to ${periodInMinutes} minutes`);
            resolve();
        });
    } else {
        return new Promise((resolve) => {
            chrome.alarms.create('sendAdTelemetryAlarm', { periodInMinutes }, () => {
                console.debug(`AD_TELEM: Successfully reset 'sendAdTelemetryAlarm' to ${periodInMinutes} minutes`);
                resolve();
            });
        });
    }
};

