/* scam detection logic, handles communication from content-tss scanner */
chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
    if (!malwarebytes || !malwarebytes.READY_TO_ACCEPT_REQUESTS) {
        console.log("REQ: Heuristic request deferred - extension is being initialized");
        return;
    }

    const dbEntries = DatabaseManager.getInstance().getCachedDatabases(['whitelist_scams_manual', 'top1m']);
    const selectedCheckedDBs = Object.values(dbEntries);
    if (message.type === "detection") {
        if (message.parameters.type === "scam") {
            let isDetectableScam = malwarebytes.isDetectableScam(sender.tab.url, message.parameters.url, sender.tab.id);
            if (isDetectableScam) {
                sendResponse({ detect: true });
                simpleStorageSet({ lastDBused: null });
                onScam({
                    tabId: sender.tab.id,
                    tabURL: message.parameters.url,
                    referrer: message.parameters.url,
                    url: message.parameters.url,
                    type: message.parameters.type,
                    subtype: message.parameters.subtype,
                    rule: message.parameters.subtype && `${threatNamePrefix.heuristic}${message.parameters.subtype}`,
                    isSilent: false,
                    selectedCheckedDBs,
                });
            } else {
                sendResponse({ detect: false });
            }
        } else {
            sendResponse({ detect: false });
        }
    }

    //NOTE: this block originally checked only isDetectableScam
    //  it has never checked the Allow List - as we shouldn't reach this point if we are on the Allow List
    if (message.type === "isExcluded") {
        if (message.parameters.type === "scam") {
            if (sender.tab && sender.tab.url) {
                let isDetectableScam = malwarebytes.isDetectableScam(
                    sender.tab.url, message.parameters.url, sender.tab.id
                );

                const excluded = !isDetectableScam;
                if (excluded) {
                    sendResponse({ excluded: !isDetectableScam });
                } else {
                    // eslint-disable-next-line max-len
                    const excluded = malwarebytes.isExcludedTemporarily(message.parameters.url, sender.tab.id) || malwarebytes.isExcludedTemporarilyForFullUrl(message.parameters.url, sender.tab.id);
                    console.debug("BKG: checking is-TSS-ExcludedTemporarily: ", { message, excluded });
                    sendResponse({ excluded: excluded });
                }
            }
        }  else if (message.parameters.type === "hijack") {
            const host = urlHost(message.parameters.url);
            const excluded = malwarebytes.EXCLUSION_HIJACK[host] != null;
            sendResponse({ excluded: excluded });
        } else {
            sendResponse({ excluded: malwarebytes.isExcludedTemporarily(message.parameters.url, sender.tab.id) });
        }
    }

    if (message.type === 'telemetryPhishingTunnel') {
        const url = message.parameters.url;
        telemetryMalware('phishing', 'tunnel', url, url, [], '');
        sendResponse(true);
    }
});

