// Basic checks for whitelisting used for download events.
const isBasicWhitelisted = (
    isFinalUrlWhitelisted,
    isUrlWhitelisted,
    url,
    finalUrl,
    mime,
    isReferrerWhitelisted
) => {
    if (isFinalUrlWhitelisted && url === "about:blank") {
        return true;
    } // don't block valid sites from referring to blank html page
    if (
        (isUrlWhitelisted || isReferrerWhitelisted) &&
        isValidRedirect(finalUrl)
    ) {
        return true;
    } // don't block file downloads from cdn/storage site when from trusted site
    if (
        finalUrl === undefined &&
        isReferrerWhitelisted &&
        isValidRedirect(url)
    ) {
        return true;
    } // don't block file downloads from cdn/storage site when from trusted site -- Firefox version
    if (mime && mime.includes("image/")) {
        return true;
    } // don't block images
    return false;
};


const safePatternFilenameChecks = ({ url, finalUrl, referrer }) => (
    [url, finalUrl].some((u) =>
        u &&
        safePatternFilename(u, urlExt(u), referrer)
    )
);

const safeReferral = ({ url, finalUrl, referrer }) => {
    if (isBlobUrl(finalUrl || url) && referrer && malwarebytes.isWhitelistedScams(referrer)) {
        return true;
    }
    return malwarebytes.RISKY_TLDS.includes(urlTLD(finalUrl || url)) &&
        isSafeReferral(
            finalUrl || url,
            referrer,
            referrer && malwarebytes.isWhitelistedScams(referrer)
        );
};

async function onDownloadEvent(id) {
    if (!malwarebytes || !malwarebytes.READY_TO_ACCEPT_REQUESTS) {
        console.log("REQ: Download request deferred - extension is being initialized");
        return;
    }

    let download = downloads[id];
    if (!download) {
        return;
    }

    let { url, finalUrl, mime, referrer, filename, blocked } = download;
    if (urlDomains(finalUrl || url).some((domain) => ALWAYS_ALLOW[domain])) {
        return console.log(
            'ODE: Skipped heuristics for always allowed site ', finalUrl || url
        );
    }

    if (blocked) {
        return console.debug("ODE: Detected an already-blocked download: ", download);
    }

    const tab = await new Promise((res) =>
        chrome.tabs.query(
            { currentWindow: true, active: true },
            (tabs) => res(tabs[0]))
    );

    referrer = referrer || (tab && tab.url); //Fallback to currentTab.url
    let paused;
    const isFinalUrlWhitelisted = finalUrl ? malwarebytes.isWhitelistedScams(finalUrl) : true; // Firefox doesn't pass a FinalUrl, so we just default to true in this case
    const isReferrerWhitelisted = referrer ? malwarebytes.isWhitelistedScams(referrer) : true;
    const isFinalUrlABlockPage = finalUrl && finalUrl.includes("app/eventpages/block-mv3.html?referrer=");
    const isReferrerABlockPage = referrer && referrer.includes("app/eventpages/block-mv3.html?referrer=");
    const isFromBlockPage = isFinalUrlABlockPage || isReferrerABlockPage;
    const isUrlWhitelisted = malwarebytes.isWhitelistedScams(url);
    const urlzExt = urlExt(finalUrl || url);
    const dlEvent = true;
    console.log("ODE: Download event for: " + url + " / " + finalUrl);

    if (safePatternFilenameChecks(download)) {
        return console.log(
            'ODE: Skipping download heuristics due to safe filename pattern'
        );
    }

    if (safeReferral({ url, finalUrl, referrer })) {
        return console.log(
            "ODE: Skipping download heuristics due to safe referral"
        );
    }

    if (
        [referrer, url, finalUrl].some(
            (uri) =>
                uri &&
                !malwarebytes.isProtectionActive(
                    EXCLUSION_SCAMS,
                    uri,
                    tab && tab.id
                )
        )
    ) {
        return console.log("ODE: protection is not active on page");
    } // don't block if there is an exclusion in place

    const tabId = tab && tab.id;

    if (
        [referrer, url, finalUrl].some(
            (uri) => uri && malwarebytes.isExcludedTemporarily(uri, tabId)
        )
    ) {
        return console.log(
            "ODE: Detected a temporarily excluded download: ",
            download
        );
    }

    if (malwarebytes.isOwnExtensionPage(referrer || url) && !isFromBlockPage) { //https://malwarebytes.atlassian.net/browse/BG-2963
        return console.log('ODE: Skipped blocking download from own event page');
    }

    if (!download.ip && filename) { // only label as IP if data filled in
        download.ip = true;
    } else {
        return;
    }

    const dbManager = DatabaseManager.getInstance();
    const dbsToCheck = await dbManager.getDatabases(settings, ['whitelist_scams_manual', 'top1m']);
    const selectedCheckedDBs = Object.values(dbsToCheck);

    if (
        !(isUrlWhitelisted && isFinalUrlWhitelisted) &&
        !isBasicWhitelisted(isFinalUrlWhitelisted, isUrlWhitelisted, url, finalUrl, mime, isReferrerWhitelisted)
    ) {
        if (
            chromeDangerTypes[download.danger] ||
            [url, finalUrl, filename].some(isDangerousExtension) ||
            isDangerousMimeType(mime)
        ) {
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Potential download-danger detected. ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        // BG-606 - Block downloads for with a MIME type related to .exe but with a different extension
        if (mimesPEs.includes(mime)) {
            if (!filename.endsWith("exe")) {
                download.blocked = true;
                cancelDownload(download.id);
                simpleStorageSet({ lastDBused: null });
                return malwarebytes.onSuspiciousDownload({
                    referrer,
                    url,
                    filename,
                    dlEvent,
                    blockMessage: `Blocked a download due to potential mimetype spoofing: ${JSON.stringify(download)}`,
                    isSilent: false,
                    selectedCheckedDBs,
                });
            }
        }

        if ([finalUrl, url, filename].some(isSuspiciousExtension)) {
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked a download from a non-whitelisted source: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (
            isUnsafeProtocol(finalUrl || url) &&
            isUnsafeExtOverHttp(urlzExt, filename)
        ) { // BG-852
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked a download done over http: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (hasSusDlOnRoot(finalUrl || url, mime)) { // BG-851
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked a suspicious "office" doc: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (isUnsafeOverIp(finalUrl || url, mime, urlzExt, filename)) { // BG-853
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked an unsafe download over IP: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (isMalRandoDigiWordPattern(finalUrl || url, filename)) { // BG-859
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked a download due to malware pattern: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (isSusUrlPattern([finalUrl, url], filename)) {
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked download due to unusual URL pattern: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }

        if (isSusTldDownload(finalUrl || url, filename)) {
            download.blocked = true;
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename,
                dlEvent,
                blockMessage: `Blocked download due to unusual TLD pattern: ${JSON.stringify(download)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }
    }

    if (
        download.totalBytes <= 2097152 && // 2097152 === 2~ mb
        TARGET_BROWSER !== 'Firefox' // 31.8.20 there's a weird bug in Firefox where the side effect of pausing the download, CANCELS the download! This makes it unresumable :\
    ) {
        console.debug(`ODE: Pausing Download ${id}`);
        chrome.downloads.pause(id, () => {
            if (chrome.runtime.lastError) {
                console.log("ODE: Download failed to be paused", chrome.runtime.lastError);
            } else {
                console.debug(`ODE: Download paused ${id}`);
                paused = true;
            }
        });
    }

    if (!(isUrlWhitelisted && isFinalUrlWhitelisted)) {
        const dl = await determineIsPe(download);
        if (dl.isPe && !(isFinalUrlWhitelisted && url === 'about:blank')) {
            if (
                !(isUrlWhitelisted && isValidRedirect(finalUrl)) &&
                !(finalUrl === undefined && isReferrerWhitelisted && isValidRedirect(url))
            ) {
                download.blocked = true;
                cancelDownload(download.id);
                simpleStorageSet({ lastDBused: null });
                return malwarebytes.onSuspiciousDownload({
                    referrer,
                    url,
                    filename: dl.filename,
                    dlEvent,
                    blockMessage: `Blocked a download due to PE detection: ${JSON.stringify(dl)}`,
                    isSilent: false,
                    selectedCheckedDBs,
                });
            } else if (isUnsafeProtocol(finalUrl || url)) { // BG-852
                download.blocked = true;
                cancelDownload(download.id);
                simpleStorageSet({ lastDBused: null });
                return malwarebytes.onSuspiciousDownload({
                    referrer,
                    url,
                    filename: dl.filename,
                    dlEvent,
                    blockMessage: `Blocked a PE download done over http: ${JSON.stringify(download)}`,
                    isSilent: false,
                    selectedCheckedDBs,
                });
            }
        }
    } else if (
        !(isFinalUrlWhitelisted && url === 'about:blank') &&
        !(peExts.test(download.filename || ''))
    ) { // BG-645
        const dl = await determineIsPe(download);
        if (dl.isPe) {
            download.blocked = true; // this is all a good canidate for state management
            cancelDownload(download.id);
            simpleStorageSet({ lastDBused: null });
            return malwarebytes.onSuspiciousDownload({
                referrer,
                url,
                filename: dl.filename,
                dlEvent,
                blockMessage: `Blocked a download due to PE detection with mis-matched mime (whitelist ignored): ` +
                    `${JSON.stringify(dl)}`,
                isSilent: false,
                selectedCheckedDBs,
            });
        }
    }

    if (!isReferrerWhitelisted && filename && socGholishFilenamePattern.test(filename)) {
        download.blocked = true;
        cancelDownload(download.id);
        simpleStorageSet({ lastDBused: null });
        return malwarebytes.onSuspiciousDownload({
            referrer,
            url,
            filename,
            dlEvent,
            rule: `${threatNamePrefix.regex}SocGholishPattern`,
            blockMessage: `Blocked a download due to SocGholish filename pattern: ${JSON.stringify(download)}`,
            isSilent: false,
            selectedCheckedDBs,
        });
    }

    if (paused) {
        chrome.downloads.resume(id, () => {
            if (chrome.runtime.lastError) {
                console.log("ODE: Download failed to be resumed", chrome.runtime.lastError);
            } else {
                console.debug(`ODE: Download resumed ${id}`);
                paused = false;
            }
        });
    }
}

malwarebytes.onSuspiciousDownload = ({
    tabId,
    tabURL,
    referrer,
    url,
    filename,
    shouldRedirect,
    shouldBeWhitelistedBySpecificUrl,
    rule,
    dlEvent,
    blockTag = "(DL_BLOCK)",
    blockMessage,
    isSilent,
    selectedCheckedDBs
}) => {
    const subtype = dlEvent ? "suspiciousContent" : "riskyPattern";
    // record the scam block
    simpleStorageSet({ lastDBused: null });
    onScam({
        tabId,
        tabURL,
        referrer,
        url,
        type: "scam",
        subtype,
        filename,
        rule: rule || `${threatNamePrefix.heuristic}${subtype}`,
        isSilent,
        selectedCheckedDBs: selectedCheckedDBs
    });

    // then do the redirect
    let newUrl = createBlockUrl(
        referrer,
        url,
        "scam",
        subtype,
        tabId,
        filename,
        null,
        rule
    );
    if (shouldBeWhitelistedBySpecificUrl) {
        newUrl += "&rules=specific";
    }

    if (blockMessage) {
        logEnvironmentInfo().then(() => {
            console.log(`OSD: ${blockTag} ${blockMessage}`);
        });
    }

    if (isSilent) {
        console.log("OSD: Silent block for scam");
        return;
    }

    if (shouldRedirect) {
        console.debug("OSD: Redirecting download tab for scam");
        return { redirectUrl: newUrl };
    } else {
        console.debug("OSD: Creating download block tab for scam");
        chrome.tabs.create({ url: newUrl, active: true });
    }
};

