const throttledConsoleLog = throttleFunc(console.log, 100);

chrome.downloads && chrome.downloads.onChanged.addListener((details) => {
    let id = details.id;
    throttledConsoleLog("DLC: Details have changed: ", JSON.stringify(details));
    // console.log("DLC: Details have changed: ", JSON.stringify(details));
    if (downloads[id] && downloads[id].url && downloads[id].url.startsWith("file://")) {
        throttledConsoleLog("DLC: Skipping download event for apparent local file download: ", downloads[id]);
    } else if (
        !downloads[id] &&
        (!details.url || details.url.startsWith && details.url.startsWith("file://"))
    ) {
        throttledConsoleLog("DLC: Skipping download event for apparent local file details: ", JSON.stringify(details));
    } else if (details.url && details.url.startsWith(`blob:${chromePagePath}`)) {
        throttledConsoleLog('DLC: Skipping local extension download');
    } else {
        if (details.url && details.url.current) {
            downloads[id].url = details.url.current;
        }
        if (details.mime && details.mime.current) {
            downloads[id].mime = details.mime.current;
        }
        if (details.filename && details.filename.current) {
            downloads[id].filename = details.filename.current;
        }
        onDownloadEvent(id);
        if (details.state && details.state.current === "complete" && downloads[id].blocked) {
            chrome.downloads.removeFile(id);
        }
        if (details.state && (details.state.current === "complete" || details.state.current === "interrupted")) {
            delete downloads[id];
        }
    }
});

