export const blockUrlsFromContextMenu = async (info) => {
    let urlSet = malwarebytes.CONTEXT_MENU_URL_CACHE;
    if (!urlSet) {
        urlSet = new Set();
    }

    if (info.linkUrl) {
        urlSet.add(info.linkUrl);
    }

    if (info.srcUrl) {
        urlSet.add(info.srcUrl);
    }

    // convert to array
    const urls = Array.from(urlSet);

    const xpathSet = malwarebytes.CONTEXT_MENU_XPATH_CACHE;
    if (!xpathSet) {
        return;
    }
    const xpaths = Array.from(xpathSet);
    const xpath = xpaths.length > 0 ? xpaths[0] : null;
    if (!xpath) {
        console.debug('MV3_USER_BLOCK: No xpath to block');
        return;
    }

    console.debug('MV3_USER_BLOCK: Blocking xpaths and urls: ', { xpath, urls });

    const tabs = await chrome.tabs.query({active: true, currentWindow: true});
    if (tabs.length === 0) {
        return;
    }
    const tabId = tabs[0].id;
    const tabUrl = tabs[0].url;
    const domain = urlHost(tabUrl);

    flashBadge(tabId).then();

    const blockItem = {domain: domain, blockUrls: urls, xpath: xpath};
    await userBlocksHandler.blockItem(blockItem);

    for (let url of urls) {
        malwarebytes.uncacheSafeness(tabId, url && url.split('?')[0]);
    }

    if (info.srcUrl && (info.mediaType === "image" || info.mediaType === "video")) {
        if (info.mediaType === "image") {
            //Clearing the browser's memory cache is expensive, only do it for images
            //This prevents blocked images to be loaded when the tab is refreshed
            chrome.webRequest.handlerBehaviorChanged();
        }
        sendTabMessage(tabId, { type: "blockMedia", mediaType: info.mediaType, src: info.srcUrl });
    }
    chrome.tabs.sendMessage(tabId, { type: "blockXPath", xpath: xpath });

    malwarebytes.CONTEXT_MENU_XPATH_CACHE.clear();
    malwarebytes.CONTEXT_MENU_URL_CACHE.clear();
};

