function onAd({ tabId, domains, tabUrl, blockMessage, isSilent = false}) {
    const host = urlHost(tabUrl);
    const switchedOff = malwarebytes.isExcludedForDomain(EXCLUSION_ADS, host);
    if (switchedOff) {
        return {};
    }

    if (malwarebytes.EXCLUSION_INDIVIDUAL[host] &&
        malwarebytes.EXCLUSION_INDIVIDUAL[host][domains[0]]) {
        console.log(`OA: Didn't block ${domains[0]} because user excluded on this page`);
        return {};
    }
    console.log(`OA: (NETWORK_BLOCK) ${blockMessage}`);
    malwarebytes.recordAll(RecordType.AD, tabId, urlHost(host), urlHost(domains[0]), true, isSilent); // this updates the value used by badge
    malwarebytes.updateBadgeCount(tabId);

    if (isSilent) {
        console.log("OA: Didn't block but recorded since this rule is silent)")
        return {}
    }

    console.log(`OA: (NETWORK_BLOCK) ${blockMessage}`);

    if (isMV3()) {
        return { cancel: true, type: 'ad' };
    }

    return { cancel: true };
}

function onMalware({ tabId, tabURL, url, type, subtype, rule, blockMessage, isMainFrame }) {
    let tabHistory = malwarebytes.TABS[tabId] ? malwarebytes.TABS[tabId].meta.history.map((item) => {
        return item.url;
    }) : [tabURL];
    const host = urlHost(tabURL);
    const malwareHost = urlHost(url);
    const switchedOff = malwarebytes.isExcludedForDomain(EXCLUSION_MALWARE, host);
    if (switchedOff) {
        return {};
    }

    if (malwarebytes.EXCLUSION_INDIVIDUAL[host] &&
        malwarebytes.EXCLUSION_INDIVIDUAL[host][malwareHost]) {
        console.logOnce(url, `OM: Didn't block ${malwareHost} because user excluded on this page`);
        return {};
    }
    const db = databaseInfo(subtype);
    logEnvironmentInfo().then(() => {
        console.log(`OM: (PAGE_BLOCK) ${blockMessage}. ${db ? `Database: ${JSON.stringify(db)}` : ""}`);
    });
    malwarebytes.postNativeExclusion(url);
    malwarebytes.recordAll(RecordType.MALWARE, tabId, host, malwareHost, true);
    malwarebytes.updateBadgeCount(tabId);
    const historyMalwareEntry = {
        site: url,
        type,
        subtype,
        date: Date.now()
    };
    detectionHistory.addItem(historyMalwareEntry);
    onDetectionTelemetry(type, subtype, url, tabURL, tabHistory, rule);
    // awful hack workaround for ridiculous Edge redirectUrl bug:  remove when Edge bug is fixed
    let redirectURL = createBlockUrl(null, url, type, subtype, tabId, null, rule);

    // redirect requests are ignored for websocket connections, so we cancel the request
    if ((['ws://', 'wss://'].some((w) => url && url.startsWith(w))) || !isMainFrame) {
        console.log(`OM: (NETWORK_BLOCK) Malware (${type}) detection on ${url}. Cancelling Request.`);
        if (malwarebytes.TAB_MALWARE_NOTIFIED[tabId] && malwarebytes.TAB_MALWARE_NOTIFIED[tabId].has(host)) {
            console.log(`OM: (NETWORK_BLOCK) Malware (${malwareHost}) detection on ${url}. Already notified.`);
            return { cancel: true };
        }
        sendTabMessage(tabId, { type: MSG_INFO_BLOCKED_CONTENT, src: host, malware: malwareHost, tabId: tabId });
        return { cancel: true };
    }

    // TODO: detect when the malware is being loaded by the parent page
    //       these requests should be cancelled rather than redirected
    //       because it throws an error
    console.log(`OM: Malware (${type}) detection on ${url}. Redirecting to block page.`);
    if (tabId && browserName() === "Edge") {
        chrome.tabs.update(tabId, { url: redirectURL });
    }

    return { redirectUrl: redirectURL };
}

function formatDatabaseInfo(databases) {
    if (!databases || databases.length === 0) {
        return "";
    }
    let dbs = databases.filter(db => db && db.name && db.version);
    if (dbs.length === 0) {
        return "";
    }
    return dbs.map(db => `${db.name}: v${db.version}`).join(' or ');
}

export function onScam(
    {
        tabId, tabURL, referrer, url, type, subtype, filename, rule, blockMessage, isSilent, selectedCheckedDBs
    }) {
    let tabHistory = malwarebytes.TABS[tabId] ? malwarebytes.TABS[tabId].meta.history.map((item) => {
        return item.url;
    }) : [tabURL];

    let host;
    try {
        host = urlHost(tabURL || url);
    } catch (e) {
        host = tabURL;
    }

    console.debug("SCA: About to flag url '" + url + "' on tab with url " + tabURL);
    let urlOfHost;
    try {
        urlOfHost = urlHost(url);
    } catch (e) {
        //shhh
        urlOfHost = url;
    }
    const switchedOff = malwarebytes.isExcludedForDomain(EXCLUSION_SCAMS, host);
    if (switchedOff) {
        return {};
    }


    if (malwarebytes.EXCLUSION_INDIVIDUAL[host] &&
        malwarebytes.EXCLUSION_INDIVIDUAL[host][urlOfHost]) {
        console.logOnce(url, `OS: Didn't block ${urlOfHost} because user excluded on this page`);
        return {}; // return early if excluded
    }

    if (blockMessage) {
        //Some callers don't return the result, they handle the redirect (or not)
        console.log(`OS: (PAGE_BLOCK) ${blockMessage}`);
        logEnvironmentInfo().then(() => {
            console.log(`SCA: browser version logged`);
        });
    }

    malwarebytes.postNativeExclusion(url);
    malwarebytes.recordAll(RecordType.SCAM, tabId, tabURL ? host : urlOfHost, urlOfHost, true, isSilent);
    malwarebytes.updateBadgeCount(tabId);
    const historyScamEntry = {
        site: url,
        type,
        subtype,
        date: Date.now()
    };
    detectionHistory.addItem(historyScamEntry);
    onDetectionTelemetry(type, subtype, url, tabURL, tabHistory, rule);
    const DbInfo = formatDatabaseInfo(selectedCheckedDBs);
    // awful hack workaround for ridiculous Edge redirectUrl bug:  remove when Edge bug is fixed
    console.log('SCA: (PAGE_BLOCK) Redirecting ' + url +
        ' to block page for ' + type +
        ' detection. ' + url +
        ' not present in ' + DbInfo + '.');

    if (isSilent) {
        console.log("OSD: Silent block for scam");
        return {};
    } else {
        return doRedirect(tabId, referrer, url, type, subtype, filename, rule);
    }
}

function doRedirect(tabId, optionalReferrer, url, type, subtype, optionalFilename, rule) {
    let redirectUrl = createBlockUrl(
        optionalReferrer, url, type, subtype, tabId, optionalFilename ? optionalFilename : null, null, rule
    );
    if (tabId && browserName() === "Edge") {
        chrome.tabs.update(tabId, { url: redirectUrl });
    }
    return { redirectUrl };
}

function matchesAny(domains, database) {
    return database && domains.some((domain) => {
        if (domain.indexOf(".") === -1) {
            //No dot, we are looking at TLD
            if (!isNaN(domain)) {
                //It's just a number, so we probably have an IP - NOT a TLD - let's skip this check (too many FP's)
                return false;
            }
        }
        let result = database.data.contains(domain);
        if (result === true) {
            const dbName = database && database.cleanName;
            console.logOnce(domain, `ANY: Just matched "${domain}" in database: ${dbName}`);
        }
        return result;
    });
}

function isDetectableMalware(tabUrl, urlOfAd, domainsOfAd, database, tabId) {
    if (!database) {
        console.log("IDM: Not checking is detectable malware for missing database!");
        return false;
    }

    if (ALWAYS_ALLOW[domainsOfAd[0]]) {
        console.debug('IDM: Skipped due to domain is in ALWAYS_ALLOW list');
        return false;
    }

    if (tabUrl && isBrowserPage(tabUrl)) {
        console.debug("IDM: Skipped a malware scan for an internal browser page");
        return false;
    }
    // NOTE: Premium features need to be explicitly true, where Free features only need to not be explicitly false - no longer true
    if (malwarebytes.isProtectionActive(EXCLUSION_MALWARE, tabUrl, tabId)) {
        if (matchesAny(domainsOfAd, database)) {
            //NOTE: isExcluded checks the 'current website' tab switches
            let isMwWl = malwarebytes.isWhitelistedMalware(urlOfAd);
            return !isMwWl;
        }
    } else {
        console.log(`IDM: Determined no need to malware scan ${tabUrl} for ${urlOfAd} (layer is inactive)`);
    }
    return false;
}

/**
 * @param {string} urlOfTab - the url of the actual website you're visiting
 * @param {string} urlOfAd - the request urlOfAd - the ad itself
 * @param {Array<string>} domains - the domains of the ad
 * @param {number} tabId - the tabId
 * @param {boolean} isThirdParty - is the ad third party
 * @return {boolean|*}
 */
export function isDetectableAd(urlOfTab, urlOfAd, domains, tabId, isThirdParty) {
    if (urlOfTab && isBrowserPage(urlOfTab)) {
        return false;
    }

    const adsDb = DatabaseManager.getInstance().getCachedDatabaseByName('ads');

    if (
        malwarebytes.isProtectionActive(
            EXCLUSION_ADS,
            urlOfTab,
            tabId
        )
    ) {
        if (
            malwarebytes.isTrackerWhitelistedForDomain(tabId, urlOfAd) ||
            malwarebytes.isWhitelistedAds(urlOfAd)
        ) {
            return false;
        }
        return (
            (isThirdParty && matchesAny(domains, adsDb)) ||
            youTubePatterns.some((pattern) => urlOfAd.includes(pattern))
        );
    }
    return false;
}

async function getTabId() {
    return new Promise((resolve) => {
        chrome.windows.getCurrent({populate: true}, (window) => {
            const currentTab = window.tabs.find((tab) => tab.active);
            resolve(currentTab.id);
        });
    });
}


