import {
    suspiciousOfficeExts,
    suspiciousOfficeMimes,
    mimesPEs,
    mimesScripts,
    tldRiskLevel,
    susExes,
    susScripts,
    susArchives,
    susDiskMedia,
    allSus,
    susOnFF,
    urlPatternsDownloads,
} from './downloadConsts';

import {
    extensionsScripts,
    suspiciousDlExts,
    forwardSlashes,
    malDigiWordRegex,
} from '../patterns';

import {urlTLD, isValidIP, urlHost} from '../utils';

import {chrome} from '../polyfill.ts';

export const isDangerousExtension = (path = "") => extensionsScripts.test(path);
export const isSuspiciousExtension = (path = "") => suspiciousDlExts.test(path);
export const isDangerousMimeType = (mime) =>
    mime && (mimesPEs.includes(mime) || mimesScripts.includes(mime));

const isSusOfficeMime = (mimetype) =>
    suspiciousOfficeMimes.some((mime) => mime === mimetype);

// BG-851
export const hasSusDlOnRoot = (url, mimetype = "") => {
    let pathName;
    mimetype = mimetype.toLowerCase();

    try {
        pathName = new URL(url).pathname;
    } catch (err) {
        console.error("HSDOR: Could not determine if sus office DL: ", err);
        return false;
    }
    // the pathname property returns at least "/", more than one indicates we're not in the root
    if (
        pathName.length > 1 &&
        (pathName.match(forwardSlashes) || []).length === 1
    ) {
        return isSusOfficeMime(mimetype);
    }
    return false;
};

export const isUnsafeProtocol = (url) => url && url.startsWith("http://");
export const isUnsafeExtOverHttp = (urlExt, filename = "") =>
    urlExt === "txt" ?
        susOnFF.some((ext) => filename.endsWith(ext)) :
        allSus.some((ext) => ext === urlExt);

export const isUnsafeOverIp = (url, mimetype, urlExt, filename) => {
    // Check if is IP hostname
    if (isValidIP(urlHost(url))) {
        return (
            (urlExt === "txt" ?
                susOnFF.some((ext) => filename.endsWith(ext)) :
                allSus.some((ext) => urlExt === ext)) ||
            isSusOfficeMime(mimetype)
        );
    }
    return false;
};

export const isMalRandoDigiWordPattern = (url, filename) => {
    let path = "";
    try {
        path = new URL(url).pathname;
    } catch (err) {
        // hey we tried
    }
    return [path, filename].some((u) => malDigiWordRegex.test(u));
};

export const isSusUrlPattern = (urls, filename) => {
    for (let [ext, urlPattern] of urlPatternsDownloads) {
        if (
            ext.some((e) => filename.endsWith(e)) &&
            urls.some((u) => u && u.match(urlPattern))
        ) {
            return true;
        }
    }
    return false;
};

export const isSusTldDownload = (url, filename) => {
    if (!filename) {
        return;
    }

    let extLists;
    switch (tldRiskLevel[urlTLD(url)]) {
        case 1:
            extLists = [
                susExes,
                susScripts,
                susOnFF,
                suspiciousOfficeExts,
                susArchives,
                susDiskMedia,
            ];
            break;
        case 2:
            extLists = [
                susExes,
                susScripts,
                susOnFF,
                suspiciousOfficeExts,
                susArchives,
            ];
            break;
        case 3:
            extLists = [susExes, susScripts, susOnFF];
            break;
        default:
            console.debug("ISTD: Not a risky tld");
            return;
    }
    return extLists.some((exts) => exts.some((e) => filename.endsWith(e)));
};

export const cancelDownload = (id) => {
    chrome.downloads &&
        chrome.downloads.cancel(id, () => {
            if (
                chrome.runtime.lastError &&
                chrome.runtime.lastError.message ===
                    `Download ${id} is already complete`
            ) {
                chrome.downloads.removeFile(id);
            } else {
                // If the download doesn't exist when you try and erase it, chrome crashes.
                chrome.downloads.search(
                    {id},
                    (results) => results && chrome.downloads.erase({id}, null)
                );
            }
        });
};

export const determineIsPe = (download) => {
    return new Promise((res) => {
        try {
            const controller = new AbortController();
            const {signal} = controller;
            // Fetch the content & use the signal for aborting
            fetch(download.finalUrl || download.url, { signal })
                .then(async response => {

                    const reader = response.body.getReader();
                    // https://javascript.info/fetch-progress
                    let receivedLength = 0; // received that many bytes at the moment
                    let chunks = []; // array of received binary chunks (comprises the body)
                    // eslint-disable-next-line no-constant-condition
                    while (true) {
                        const {done, value} = await reader.read();

                        // we only want enough to determine if the file is a PE, so bail as soon as we have at least 100 bytes
                        if (done || receivedLength >= 100) {
                            controller.abort();
                            break;
                        }

                        chunks.push(value);
                        receivedLength += value.length;
                    }
                    let chunksAll = new Uint8Array(receivedLength);
                    let position = 0;
                    for (let chunk of chunks) {
                        chunksAll.set(chunk, position);
                        position += chunk.length;
                    }
                    const decoder = new TextDecoder();
                    const mz = decoder.decode(chunksAll.slice(0, 2));

                    console.log("DIP: Header: ", mz);
                    res({...download, isPe: mz === "MZ"});
                })
        } catch (err) {
            console.log("IP: error, ", err);
            res({...download, isPe: false});
        }
    });
};

