import {getFileStorage} from './idb-file-storage';
import {fetchBinary} from './utils';

let idbStorage;
getFileStorage({name: "db", persistent: true}).then((idb) => {
    idbStorage = idb;
});
//REFERENCE: https://doc.esdoc.org/github.com/rpl/idb-file-storage/ (for storage vars)
export const ioQueues = {

    //QUEUE: We should handle at most 4 requests in parallel as this supports the most browsers,
    //   I picked 3 because some other work from our app will already be taking place (e.g. unpacking)
    nextQueue: 0,
    queue1: Promise.resolve(),
    queue2: Promise.resolve(),
    queue3: Promise.resolve(),

    next: (thn) => {
        let queue;
        //get the next queue to use
        //NOTE: We dont multithread until we've loaded a storage
        if (ioQueues.nextQueue === 0) {
            queue = ioQueues.queue1;
        } else if (ioQueues.nextQueue === 1) {
            queue = ioQueues.queue2;
        } else /*if (fileIo.nextQueue == 2)*/ {
            queue = ioQueues.queue3;
        }
        //add the new func as the final then in the queue
        let intermediary = queue.then(thn);
        //prepare for the next queue request
        if (ioQueues.nextQueue === 0) {
            ioQueues.queue1 = intermediary;
            ioQueues.nextQueue ++;
        } else if (ioQueues.nextQueue === 1) {
            ioQueues.queue2 = intermediary;
            ioQueues.nextQueue ++;
        } else /*if (fileIo.nextQueue == 2)*/ {
            ioQueues.queue3 = intermediary;
            ioQueues.nextQueue = 0;
        }
        //return the queued 'then promise' to the caller
        return intermediary;
    },

};

//TODO: Can we potentially end up with all queues merging?
// Add A, B, C, D (each independent)
// Q1 [ A, D ],  Q2 [ B ],  Q3 [ C ]
// Add E dependent on D
// Q1 [ A, D ],  Q2 [ B, E ],  Q3 [ C ]
// is Q2(e) now pointing at Q1(d) ?

export const queuedFetchBinary = function(url, opts) {
    return ioQueues.next(() => fetchBinary(url, opts));
};

export const queuedFileRead = (fileName) => ioQueues.next(() => fileRead(fileName));

// Read file from IndexedDB storage
export const fileRead = function(fileName) {
    return new Promise(((resolve, _reject) => {
        if (idbStorage === null || idbStorage === undefined) {
            console.warn("FIL: " + fileName + " database is loading the IDB");
            getFileStorage({name: "db", persistent: true}).then((idb) => {
                idbStorage = idb;
                return resolve();
            });
        } else {
            return resolve();
        }
    })).then(() => {
        return idbStorage.get(fileName).then((file) => {
            if (!file) {
                throw new Error("storage.get returned nothing for " + fileName);
            }
            return Promise.resolve(file);
        }).then((file) => {
            return new Promise((resolve, reject) => {
                try {
                    let fileReader = new FileReader();
                    fileReader.addEventListener(
                        "loadend",
                        () => fileReader.error ? reject(fileReader.error) : resolve(fileReader.result)
                    );
                    fileReader.readAsArrayBuffer(file);
                } catch (error) {
                    return reject(error);
                }
            });
        }).then((fileContents) => {
            if (!fileContents) {
                throw new Error("FileReader");
            }
            return Promise.resolve(fileContents);
        }).catch((err) => console.log(`FIL: ${fileName} err reading: `, err));
    });
};

// Write file to IndexedDB storage
export const fileWrite = async function(filePath, fileName, fileContents) {
    let storage = await getFileStorage({name: filePath, persistent: true});
    if (!storage) {
        throw new Error("getFileStorage");
    }

    let blob = new Blob([fileContents]);
    if (!blob) {
        throw new Error("new Blob");
    }

    let result = await storage.put(fileName, blob);
    if (!result) {
        throw new Error("storage.put");
    }
};

export const fileExists = async function(filePath, fileName) {
    let storage = await getFileStorage({name: filePath, persistent: true});
    if (!storage) {
        throw new Error("getFileStorage");
    }

    let result = await storage.get(fileName);
    if (!result) {
        return false;
    }
    return true;
};

export async function fileDelete(filePath, fileName) {
    let storage = await getFileStorage({name: filePath, persistent: true});
    if (!storage) {
        throw new Error("getFileStorage");
    }

    try {
        await storage.remove(fileName);
    } catch (error) {
        console.log("Error deleting file: " + error);
    }
}

export const clearFiles = async function() {
    const storage = await getFileStorage({name: 'db', persistent: true});
    await storage.clear();
};
