import {IndexedDatabaseFile} from './indexed-database-file';
import {BloomFilter} from './bloom-filter';
import {cleanDbName} from '../utils';

export class IndexedDatabase {
    constructor(name, bloomFilter) {
        this.name = name;
        if (!bloomFilter) {
            this.file = new IndexedDatabaseFile(name, cleanDbName(name));
        } else {
            this.bloomFilter = BloomFilter.deserialize(bloomFilter, true);
        }
    }

    parse() {
        return this.file.read().then((fileContents) => {
            this.bloomFilter = BloomFilter.deserialize(fileContents);
            return this;
        });
    }

    parseBundled() {
        console.debug(`IDB: Reading the bundled database for ${this.name}`);
        return this.file.readBundled().then((fileContents) => {
            this.bloomFilter = BloomFilter.deserialize(fileContents);
            return this;
        });
    }

    ready() {
        return !! this.bloomFilter;
    }

    contains(str) {
        return this.ready() && this.bloomFilter.contains( str );
    }

    add(str) {
        if (!this.ready()) {
            throw new Error(`Database ${this.name} is not yet ready`);
        }
        this.bloomFilter.add(str);
    }

    getBloomFilterPlainObject() {
        if (!this.ready()) {
            throw new Error(`Database ${this.name} is not yet ready`);
        }
        return this.bloomFilter.getPlainObject();
    }

    replaceBloomFilter(obj) {
        this.bloomFilter.replace(obj);
    }
}
