import * as fzstd from 'fzstd';
import {chrome} from '../polyfill.ts';
import {binaryToString, isZstd} from '../utils';
import {clearFiles, queuedFetchBinary, queuedFileRead} from '../file';
import {DB_DIR} from "./db-consts";

export class IndexedDatabaseFile {
    constructor(name, cleanName) {
        this.name = name;
        this.cleanName = cleanName;
    }

    //Let the caller handle failures
    async read() {
        const data = await queuedFileRead(this.name);
        return this.unpack(data);
    }

    async readBundled() {
        console.debug('READ BUNDLED FOR: ', JSON.stringify({name: this.name, cleanName: this.cleanName}));
        let binary = await queuedFetchBinary(chrome.runtime.getURL(DB_DIR + "/" + this.name));
        return this.unpack(binary);
    }

    unpack(fileContents) {
        //Note: File contents are mostly precompiled bloom filters
        console.debug('UNPACKING: ', JSON.stringify({name: this.name, cleanName: this.cleanName}));
        let inflatedStr = '{}' //Default empty object;
        try {
            inflatedStr = this.unpackInflate(fileContents)
        } catch (error) {
            console.error('Error unpacking file: ', error);            
        }
        return JSON.parse(inflatedStr);
    }

    unpackInflate(fileContents) {
        //File can be compressed or uncompressed, it's uncompressed when it's genereated from incrementals
        const binaryArray = new Uint8Array(fileContents);
        if (isZstd(binaryArray)) {
            return binaryToString(fzstd.decompress(binaryArray));
        }
        return binaryToString(binaryArray);
    }

    static async clear() {
        return clearFiles();
    }
}
